import React from "react";
import defImg from "../../../../assets/img/fox.png";
import {useDispatch} from "react-redux";
import {openModal} from "../../../../Actions/ModalAction";
import {ParserData} from "../../../../Help/ParserData";


function AnimationGame({game}) {
    let {participants} = game;
    if (participants[0].color !== "1"){
        participants = participants.reverse();
    }
    return (
        <div className="coin-game" data-game-id="{{ $game->id }}">
            <CoinGameLeft participant={participants[0]}/>
            <CoinGameEye game={game}/>
            <CoinGameRight participant={participants[1]}/>
        </div>
    )
}

const CoinGameLeft = ({participant = {}}) => {
    const {account, cash} = participant;
    const {image, name = "nameless"} = account;
    return (
        <div className="coin-game__left">
            <div className="coin-game__wrap2">
                <div className="coin-game__box">
                    <img src={image} alt="avatar"
                         className="coin-game__avatar"/>
                    <i className="ic-coin-orange"></i>
                </div>
                <p className="coin-game__name">{name}</p>
            </div>
            <div className="coin-game__wrap">
                <p className="coin-game__text">сделал ставку </p>
                <p className="coin-game__value">{ParserData(cash)} <span>COINS</span></p>
            </div>
        </div>
    )
};
const CoinGameRight = ({participant = {}}) => {
    const {account, cash = 3999} = participant;
    const {image, name = "nameless"} = account;
    return (
        <div className="coin-game__right">
            <div className="coin-game__wrap">
                <p className="coin-game__value">{ParserData(cash)} <span>COINS</span></p>
                <p className="coin-game__text">сделал ставку </p>
            </div>
            <div className="coin-game__wrap2">
                <p className="coin-game__name">{name}</p>
                <div className="coin-game__box">
                    <img src={image} alt="avatar"
                         className="coin-game__avatar"/>
                    <i className="ic-coin-blue"></i>
                </div>
            </div>
        </div>
    )
};
const CoinGameEye = ({game}) => {
    const dispatch = useDispatch();
    let {id,participants} = game;
    if (participants[0].color !== "1"){
        participants = participants.reverse();
    }
    return (
        <div className="coin-game__eye">
            <a href="#waitPlayer" data-izimodal-open="#waitPlayer"
               onClick={() => dispatch(openModal("CoinFlipGameEye", {id,participants}))}
               data-izimodal-transitionin="fadeInDown"
                // onClick="MainFunction.showGame(this);"
               style={{display: "block", width: "100%", height: "100%"}}><i className="ic-eye"></i></a>
        </div>
    )
};

export {
    AnimationGame
}