import React from "react";
import {Crash} from "../Help/CanvasHelp";
import "./CrashCanvasStyle.css"
import ResizeSensor from "resize-sensor"
import {echo} from "../../../socket/Socket";

class CrashCanvas extends React.Component {
    // state = {
    //     currentPos:0,
    //     profit: 100,
    //     timer: 3,
    //     isGoing: false,
    //
    // }
    constructor(props) {
        super(props);
        // this.updateTimer = this.updateTimer.bind(this);
    }

    componentDidMount() {
        const {currentPos, profit, timer, isGoing, number, endGameAt, isOnline, prepareStart} = this.props;
        this.game = Crash({id: "crashGraphicCanvas", coordinates: currentPos});
        this.sensor = new ResizeSensor(document.getElementById("crashGraphic"), () => {
            this.game = Crash({id: "crashGraphicCanvas", coordinates: currentPos});
            if (prepareStart) {
                this.game.startTimer(currentPos, timer);
            } else {
                if (currentPos < number) {
                    this.game.paintGraphic(currentPos.toFixed(4));
                } else {
                    this.game.endGame(profit.toFixed(2));
                }
            }
        });
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        const {currentPos, profit = 0, timer, isGoing, number, endGameAt, isOnline, prepareStart} = this.props;
        this.game = Crash({id: "crashGraphicCanvas", coordinates: currentPos});
        clearTimeout(this.timer);

        if (prepareStart) {
            this.game.startTimer(currentPos, timer);
            this.timer = setTimeout(() => {
                this.props.ticTimer(timer - 1)
            }, 1000);
        } else {
            if (currentPos < number) {
                if (isOnline) {
                    this.game.paintGraphic(currentPos.toFixed(4));
                } else {
                    this.game.paintGraphic(currentPos.toFixed(4));
                    this.timer = setTimeout(() => {
                        // console.log(number);
                        // console.log(endGameAt);
                        // console.log(endGameAt);
                        this.props.setNewCoef({
                            coef: (number * ((endGameAt + 0.0225)) / (number + 17)),
                            endGameAt: endGameAt + 0.0225
                        }, false)
                    }, 20)
                }
                // this.timer = setTimeout(() => {

            } else {
                clearTimeout(this.timer);
                this.game.endGame(profit.toFixed(2));
            }
        }
    }

    componentWillUnmount() {
        this.game.clearTimer();
        clearInterval(this.timer);
    }

    render() {
        return (
            <div id="crashGraphic">
                <canvas className="canvas" id="crashGraphicCanvas">

                </canvas>
            </div>
        );
    }
}


export {
    CrashCanvas
}