import React from "react";
import {connect} from "react-redux";
import {startTimerCoinflip} from "../help/SpinnerGame";
import {AnimationGame} from "./AnimationGame";
import {getGameDataCoinFlip, startGame} from "../../../Actions/Game/CoinFlipAction";
import {getGameCoinFlipData} from "../../../Actions/ModalAction";
import {Loading} from "../../../Components/Loading";

const mapStateToProps = state => {
    return {
        id: state.Modal.props.id,
        fetched: state.Modal.fetched,
        fetching: state.Modal.fetching
    }
};

const mapDispatchToProps = dispatch => {
    return {
        getGameData: id => dispatch(getGameDataCoinFlip(id)),
        startGame: () => dispatch(startGame),
        getGameInfo: id => dispatch(getGameCoinFlipData(id))
    }
};

class AnimationGameWrapper extends React.Component {
    componentDidMount() {
        const fetched = this.props.fetched;
        if (!fetched) {
            this.props.getGameInfo(this.props.id);
        }

        // const id = this.props.id;
        // const startGame = this.props.startGame;
        // startTimerCoinflip(id, 1, startGame);
    }

    render() {
        const fetching = this.props.fetching;
        if (fetching) {
            return (
                <div style={{height: "300px", paddingTop: "1px"}} className="modal--loading">
                    <Loading/>
                </div>
            )
        }
        return <AnimationGame/>
    }
}


export default connect(mapStateToProps, mapDispatchToProps)(AnimationGameWrapper)