import {ADMIN_GET_MAIN_DATA} from "../../Actions/AdminActions/AdminMainAction";

const AdminMainReducer = (state = {

}, action) => {
    switch (action.type) {
        case ADMIN_GET_MAIN_DATA: return  {
            ...state,
            ...action.data
        };
        default: return state;
    }
};


export {
    AdminMainReducer
}