import axios from "axios";
import {AXIOS_PARAMS, headers} from "../Help/AxiosHTTP";
import {CLOSE_MODAL} from "./ModalAction";
import {UPDATE_USER_GAMES} from "./Game/CoinFlipAction";
import {errorNotification, notification} from "../Notifications/Notification";

export const CHANGE_SESSION_THEME = "CHANGE_SESSION_THEME";
export const GET_LOGIN = "GET_LOGIN";
export const LOGOUT = "GET_LOGIN";
//change action type to change role and auth
export const LIKE_GUEST = "LIKE_GUEST";
export const LIKE_USER = "LIKE_USER";
export const LIKE_ADMIN = "LIKE_ADMIN";
export const SET_MAIN_STYLE = "SET_MAIN_STYLE";
//end

//fetch action
export const START_FETCHING = "START_FETCHING_USER_DATA";
export const FETCHED = "FETCHED_USER_DATA";

//init new balance
export const UPDATE_BALANCE = "UPDATE_BALANCE";


const changeSessionTheme = dispatch => {
    let themeData = JSON.parse(localStorage.getItem("theme"));
    if(!themeData){
        localStorage.setItem("theme", JSON.stringify({theme: 1}));
        themeData = JSON.parse(localStorage.getItem("theme"));
    }
    const {theme = 1} = themeData;
    dispatch({
        type: CHANGE_SESSION_THEME,
        theme: theme?0:1
    })
    localStorage.setItem("theme", JSON.stringify({theme: theme?0:1}));
};

const setDefMean = dispatch => {
    let themeData = JSON.parse(localStorage.getItem("theme"));
    if(!themeData){
        localStorage.setItem("theme", JSON.stringify({theme: 1}));
        themeData = JSON.parse(localStorage.getItem("theme"));
    }
    const {theme = 1} = themeData;
    dispatch({
        type: CHANGE_SESSION_THEME,
        theme: theme?1:0
    })
};
//test action to change role and auth
// const changeToRole = (role) => dispatch => {
//     dispatch({
//         type: role
//     })
// };


const setMainStyle = (href) => dispatch => {
    dispatch({
        type: SET_MAIN_STYLE,
        href
    })
};
//end


// action to login in to service
const loginPost = (userData) => dispatch => {
    if (!checkAuth(dispatch)) {
        dispatch({
            type: START_FETCHING
        });
        const {email, password} = userData;
        axios.post(`${AXIOS_PARAMS.path}api/login`, {email, password}).then(res => {
           if(res.status !== 200){
               dispatch({
                   type: FETCHED,
                   status: res.status
               });
               errorNotification("Неверный логин или пароль", " ")
           } else {
               const {token, token_type} = res.data;
               localStorage.setItem("tokenData", JSON.stringify({token, token_type}));
               checkAuth(dispatch);
               dispatch({
                   type: GET_LOGIN,
                   data: {
                       isAuth: true,
                       token,
                       token_type
                   }
               });
               dispatch({
                   type: FETCHED,
                   status: res.status
               });
               dispatch({
                   type: CLOSE_MODAL
               });
               window.location.reload()
           }
        }).catch(e => {
            dispatch({
                type: FETCHED,
                status: "Введен неверный email или пароль"
            });
            errorNotification("Введен неверный email или пароль", " ")
        })
    }
};

const checkAuth = dispatch => {
    const loginData = JSON.parse(localStorage.getItem("tokenData"));
    dispatch({
        type: START_FETCHING,
    });
    if (loginData) {
        const {token, token_type} = loginData;
        axios.get(`${AXIOS_PARAMS.path}api/check-auth`, {...headers()}).then(res => {
            const data = res.data;
            dispatch({
                type: GET_LOGIN,
                data: {
                    isAuth: true,
                    token,
                    token_type,
                    ...data.user
                }
            });
            notification("Авторизация прошла успешно", `Добро пожаловать ${data.user.name}`);
            dispatch({
                type: FETCHED,
                status: res.status

            });
        }).catch(res => {
            errorNotification("Произошла ошибка авторизации", " ")
            dispatch({
                type: FETCHED,
                status: res.status
            });
            localStorage.removeItem("tokenData");
            return false;
        });
        return true;
    } else {
        dispatch({
            type: FETCHED,
            status: null,
        });
        localStorage.removeItem("tokenData");
        return false;
    }
};


const registerPost = (registerData) => dispatch => {
    const {name, surname, email, password} = registerData;
    dispatch({
        type: START_FETCHING
    });
    axios.post(`${AXIOS_PARAMS.path}api/register`, {
        name,
        last_name: surname,
        email,
        password,
        c_password: password
    }).then(res => {
        if(res.status !== 200){
            dispatch({
                type: FETCHED,
                status: res.status,
            })
        } else {
            notification("Аккаунт успешно создан", `Добро пожаловать ${name}`);
            dispatch({
                type: FETCHED,
                status: res.status
            });
            window.location.reload();
        }
    }).catch(e => {
        dispatch({
            type: FETCHED,
            status: "Пользователь с такими данными уже существует."
        });
        errorNotification("Пользователь с такими данными уже существует.", " ")
    })
};

const logout = dispatch => {
    axios.get(`${AXIOS_PARAMS.path}api/logout`,{...headers()}).then(res => {
        dispatch({
            type: LOGOUT
        });
        localStorage.removeItem("tokenData");
        window.location.reload();
        window.location.href = window.location.href;
    });
};

const updateBalanceHardly = dispatch => {
    axios.post(`${AXIOS_PARAMS.path}api/account/get-balance`,{},{...headers()}).then(res => {
        dispatch({
            type: UPDATE_BALANCE,
            balance: res.data,
        })
    }).catch(res => {
        errorNotification("Неудалось обновить баланс", " ")
    })
}

export {
    changeSessionTheme,
    setMainStyle,
    loginPost,
    registerPost,
    checkAuth,
    logout,
    setDefMean,
    updateBalanceHardly
}


