import {connect, useSelector} from "react-redux";
import KingOfTheHillPage from "../KingOfTheHillPage";
import {
    addParticipantClassic,
    addParticipantSenoyr, endClassicGame, endSenoyrGame,
    getKingOfTheHillData,
    updateClassicGameTimer,
    updateSenoyrGameTimer
} from "../../../Actions/Game/KingOfTheHillAction";
import {UPDATE_BALANCE} from "../../../Actions/UserAction";



const mapStateToProps = state => {
    const {id} = state.User;
    return {
        id,
        user: {
            fetching: state.User.fetching,
            fetched: state.User.fetched,
        },
        kingOTH: {
            fetching: state.KingOfTheHill.fetching,
            fetched: state.KingOfTheHill.fetched,
        }
    }
};
const mapDispatchToProps = dispatch => {
    return {
        getKingOfTheHillData: () => dispatch(getKingOfTheHillData),
        addParticipantClassic: data => dispatch(addParticipantClassic(data)),
        updateClassicGameTimer: newTime => dispatch(updateClassicGameTimer(newTime)),
        endClassicGame: (data) => dispatch(endClassicGame(data)),
        endSenoyrGame: (data) => dispatch(endSenoyrGame(data)),
        addParticipantSenoyr: data => dispatch(addParticipantSenoyr(data)),
        updateSenoyrGameTimer: data => dispatch(updateSenoyrGameTimer(data)),
        updateBalance: newBalance => dispatch({
            type: UPDATE_BALANCE,
            balance: newBalance
        })
    }
};


export default connect(mapStateToProps,mapDispatchToProps)(KingOfTheHillPage)