import React, {useState} from "react";
import "../style/Auth.css"
import {useDispatch, useSelector} from "react-redux";
import {loginPost} from "../../../Actions/UserAction";
import {closeModal, openModal} from "../../../Actions/ModalAction";


const AuthEmail = () => {
    const [email, setEmail] = useState("email123@email.ru");
    const [password, setPassword] = useState("123123dddEEE");
    const dispatch = useDispatch();
    const handlerForm = () => {
        dispatch(loginPost({email,password}))
    };
    const {status, fetched} = useSelector(state => state.User);

    return(
        <div className="modal modal--bet" id="authLogin">
            <span>Авторизация</span>
            <form onSubmit={e=>{
                e.preventDefault();
                handlerForm();
            }}>
                {/*<input type="hidden" name="_token" value="VoJZdzUqOFV18U2zYgOShE0XDKQeuSIN59l0HPTs"/>*/}
                    <ul>
                        <li>
                            <input type="email" name="email" value={email} onChange={e=>setEmail(e.target.value)} placeholder="Email"/>
                        </li>
                        <li>
                            <input type="password" name="password" value={password} onChange={e=>setPassword(e.target.value)} placeholder="Пароль"/><br/>
                        </li>
                    </ul>
                <div className={status && fetched?"":"hide"} style={{color: "red"}}>{status}</div>


                <button
                        // onClick="MainFunction.authUser(this, event);"
                        type="submit" name="Зарегистрироваться">Отправить</button><br/>
                        <a data-izimodal-open="#authRegistration"
                           onClick={()=>{
                               dispatch(closeModal);
                               setTimeout(() =>  dispatch(openModal('AuthRegistration')), 150)
                           }}
                           data-izimodal-transitionin="fadeInDown">Нет
                            аккаунта?</a>
            </form>
        </div>
    )
};

export {
    AuthEmail
}
