import {GET_PROFILE_DATA} from "../Actions/ProfileActions";

const ProfileReducer = (state = {
    getHistoryBalance: [],
    listGames: [],
    listReferrals: []
}, action) => {
    switch (action.type) {
        case GET_PROFILE_DATA: return {
            ...state,
            ...action.data
        };
        default: return state;
    }
};




export {
    ProfileReducer
}