import $ from "jquery";
import React, {createElement} from "react";
import blueCoin from "../../../assets/img/icons/coin-blue.png";
import orangeCoin from "../../../assets/img/icons/coin-orange.png";

function startTimerCoinflip(gameId, duration, startGameFunction) {
    $(function () {
        let timer = duration,
            minutes, seconds;
        let popup = $(`.iziModal-wrap[data-game-id=${gameId}]`);
        console.log(popup);
        if (popup) {
            let coinflipIntervalId = setInterval(function () {

                minutes = parseInt(timer / 60, 10);
                seconds = parseInt(timer % 60, 10);

                minutes = minutes < 10 ? "0" + minutes : minutes;
                seconds = seconds < 10 ? "0" + seconds : seconds;


                $('.wait-palyer__timer-value').text(seconds);
                if (--timer < 0) {
                    timer = duration;
                }
                if (timer == 0) {

                    setTimeout(function () {
                        $('.wait-palyer__timer-value').text('00');
                        startGameFunction()
                    }, 1000);
                    clearInterval(coinflipIntervalId);

                }

            }, 1000);
        }
    })
}

function startSpin(innerSelector, winner, endGameFunction) {
    let innerBlock = document.querySelector(`.${innerSelector}`);
    let moveUp = -61.5;
    let allItems = document.querySelectorAll(".coin");
    let nowActive = 3;
    allItems[nowActive].classList.add("coinActive");
    let startPosition = window.innerWidth <= 1023 ? 37 : 42;
    let timer;
    rollUp(100);
    function rollUp(duration) {
        if (nowActive === allItems.length - 4) {
            setTimeout(()=> {
                endGameFunction();
                endGameCoins();
            }, duration)
        } else {
            timer = setTimeout(() => {
                step(duration);
                if(nowActive > 50) {
                    allItems[nowActive].style.transition = `${duration/2000}s linear`;
                    duration *= 1.15;
                } else if(nowActive > 40) {
                    allItems[nowActive].style.transition = `${duration/2000}s linear`;
                    duration += 17;
                } else if (nowActive > 30) {
                    allItems[nowActive].style.transition = `${duration/2000}s linear`;
                    duration += 15;
                }
                rollUp(duration);
            }, duration)
        }
    }

    //next step when roll up
    function step(duration) {
        duration += 0.001;
        allItems[nowActive].classList.remove('coinActive');
        nowActive++;
        allItems[nowActive].classList.add('coinActive');
        // allItems[nowActive].style.transition = `${duration/1000}s linear`;
        innerBlock.style.transition = `${duration/1000}s linear`;
        innerBlock.style.marginTop = `${(nowActive - 3) * moveUp - startPosition}px`;
    }

    function endGameCoins() {
        let winnerBlock = document.querySelector(".wait-palyer__rate--first");
        let looserBlock = document.querySelector(".wait-palyer__rate--second");
        if (winner === 0) {
            let temp = winnerBlock;
            winnerBlock = looserBlock;
            looserBlock = temp;
        }
        let cashWinner = Number(winnerBlock.textContent.split("C")[0]);
        let cashLooser = Number(looserBlock.textContent.split("C")[0]);
        coinFlow(10);
        function coinFlow(duration) {
            if (cashLooser <= 0) {
                return ""
            } else {
                if(cashLooser >=30){
                    cashWinner+=Math.floor(cashLooser/2);
                    cashLooser-=Math.floor(cashLooser/2);
                } else {
                    cashLooser--;
                    cashWinner++;
                }
                timer = setTimeout(()=>{
                    winnerBlock.innerHTML = `${cashWinner}<br/><span>COINS</span>`;
                    looserBlock.innerHTML = `${cashLooser}<br/><span>COINS</span>`;
                    duration *= 1.09;
                    coinFlow(duration);
                },duration)
            }
        }
    }

    function clearTimer(){
        clearTimeout(timer);
    }
    return{
        clearTimer
    }
}

export {
    startTimerCoinflip,
    startSpin
}