import React from "react";
import {useDispatch, useSelector} from "react-redux";
import {openModal} from "../../../Actions/ModalAction";
import {ParserData} from "../../../Help/ParserData";

const ClassicGameBetButton = () => {
    const dispatch = useDispatch();
    const {isAuth, role_id} = useSelector(state => state.User);
    const length = useSelector(state => state.KingOfTheHill.gameClassic.participants.length);
    if (isAuth && role_id !== 4) {
        return (
            <a className="game__time-link button button--gradient" id="bedBet"
               onClick={() => dispatch(openModal("BetCoinKingOfTheHill", {
                   lastBet: (length + 1) * 10,
                   typeGame: "classic",
                   readOnly: true
               }))}
                // onClick="MainFunction.createGameKing('classic');"
            >ВНЕСТИ
                <span> {ParserData(length ? length * 10 + 10 : 10)} </span>COINS
            </a>
        )
    } else {
        return (
            <a className="game__time-link button button--gradient" id="bedBet" data-izimodal-open="#auth"
               onClick={() => dispatch(openModal("Auth"))}
               data-izimodal-transitionin="fadeInDown">ВНЕСТИ
                <span> {ParserData(length ? length * 10 + 10 : 10)} </span>COINS
            </a>
        )
    }
};
const SenyorGameBetButton = () => {
    const dispatch = useDispatch();
    const {isAuth, role_id} = useSelector(state => state.User);
    const {participants} = useSelector(state => state.KingOfTheHill.gameSenyor);
    let lastBet;
    if(participants.length){
        lastBet = Number(participants[0].cash) + 1;
    } else {
        lastBet = 100;
    }
    if (isAuth && role_id !== 4) {
        return (
            <a className="game__time-link button button--gradient" id="bedBet"
               onClick={() => dispatch(openModal("BetCoinKingOfTheHill", {lastBet, typeGame: "senyor"}))}
                // onClick="MainFunction.createGameKing('classic');"
            >ВНЕСТИ
                <span> {ParserData(lastBet)} </span>COINS
            </a>
        )
    } else {
        return (
            <a className="game__time-link button button--gradient" id="bedBet" data-izimodal-open="#auth"
               onClick={() => dispatch(openModal("Auth"))}
               data-izimodal-transitionin="fadeInDown">ВНЕСТИ
                <span> { ParserData(lastBet)} </span>COINS
            </a>
        )
    }
};

export {
    ClassicGameBetButton,
    SenyorGameBetButton
}