import React, {useEffect} from "react";
import {Switch, Route, Link} from 'react-router-dom';
import GamesPage from "./AdminPage/Games/GamesPage";
import CrashPage from "./AdminPage/Crash/CrashPage";
import {Error404} from "./Pages/404/404";
import {useSelector} from "react-redux";
import HomePageContainer from "./AdminPage/Home/Container/HomePageContainer";
import UsersPageContainer from "./AdminPage/Users/Container/UsersPageContainer";
import WalletPageContainer from "./AdminPage/Wallet/Container/WalletPageContainer";
import OutputPageContainer from "./AdminPage/Output/Container/OutputPageContainer";
import PromocodesPageContainer from "./AdminPage/Promocodes/Container/PromocodesPageContainer";
import GamesPageContainer from "./AdminPage/Games/Container/GamesPageContainer";
import CrashPageContainer from "./AdminPage/Crash/Container/CrashPageContainer";

function AdminApp() {
    const {role_id = 0} = useSelector(state => state.User);
    // useEffect(() => {
    //     checkTheme(role_id);
    // }, []);

    if (role_id !== 2) {
        return (
            <Switch>
                <Route path="/admin">
                    <Error404/>
                </Route>
            </Switch>
        )
    }
    return (
        <Switch>
            <Route exact path='/admin/'>
                <HomePageContainer/>
            </Route>
            <Route exact path='/admin/crash'>
                <CrashPageContainer/>
            </Route>
            <Route exact path='/admin/games'>
                <GamesPageContainer/>
            </Route>
            <Route exact path='/admin/output'>
                <OutputPageContainer/>
            </Route>
            <Route exact path='/admin/Promocodes'>
                <PromocodesPageContainer/>
            </Route>
            <Route exact path='/admin/users'>
                <UsersPageContainer/>
            </Route>
            <Route exact path='/admin/wallet'>
                <WalletPageContainer/>
            </Route>
            <Route path="/admin/" component={Error404}/>
        </Switch>
    )
}

export {
    AdminApp
}