import defImg from "../../../../assets/img/fox.png";
import React from "react";
import {useSelector} from "react-redux";
import {ParserData} from "../../../../Help/ParserData";

const LastWinnersList = () => {
    const games = useSelector(state => state.Jackpot.games);
    return (
        <div className="jackpot-history__wrapper nano" style={{marginTop: "20px"}}>
            <div className="nano-content">
                <ul className="jackpot-history__list last-winners">
                    <div className="jackpot-timer__wrap stats">
                        <p className="history__link history__link--active">ПОСЛЕДНИЕ {games.length} ПОБЕДИТЕЛЕЙ</p>
                    </div>
                    {
                        games.map((game, index) => {
                            return <LastWinnersListItem key={index} game={game}/>
                        })
                    }
                </ul>
            </div>
        </div>
    )
};
const LastWinnersListItem = ({game}) => {
    let {
        bank,
        winner_ticket,
        winner
    } = game;
    winner = winner? winner: {
        color: "red",
        name: "",
        image:defImg,
        range: "0 - 0"
    };
    const {
        color,
        image,
        name,
        range
    } = winner;
    return (
        <li className="jackpot-history__item history-item history-item--"
            style={{borderLeft: `2px solid ${color}`}}>
            <div className="history-item__left">
                <img src={image} alt="avatar" className="history-item__avatar"/>
                <div className="history-item__wrap">
                    <p className="history-item__name">{name}</p>
                </div>
            </div>
            <div className="history-item__right">
                <p className="history-item__value">{ParserData(bank)} <span>COINS</span></p>
                <p className="history-item__range">
                    <i className="ic-game-history">
                    </i> {range}
                </p>
            </div>
        </li>
    )
};


export {
    LastWinnersList
}