import React from "react";
import {Header} from "../block/Header";
import {SideMenuLeft} from "../../Components/Admin/SideMenuLeft";
import {CrashMain} from "./CrashMain";
import {echo} from "../../socket/Socket";


class CrashPage extends React.Component{
    componentDidMount() {
        this.props.getData();
        this.props.getGameDataCrash();
        echo.channel("crash-coef").listen("CrashCoef", res => {
            console.log(res);
        });
        echo.channel("crash").listen("CreateGameCrash", res => {
            this.props.updateBalanceHardly();
            this.props.setNewGame(res)
            console.log(res);
        });
        echo.channel("bet").listen("JoinCrash", res => {
            console.log(res);
            this.props.setNewBet(res);
            console.log("BEEEEEEEEEEEEEEEEEEEEEEEEEEETS")
        });
        echo.channel("crash-stop").listen("StopCrash", res => {
            console.log(res);
        });
        echo.channel("flash-out").listen("FlashOutCrash", res => {
            this.props.updateUbets(res.bet);
            console.log(res);
        });
        echo.channel("bet-adm").listen("AdmCrash", res => {
            console.log(res);
        });
        echo.channel("crash-timer").listen("CrashTimer", res => {
            console.log(res);
            console.log("DEFAULT TIMER")
            this.props.setNewCoef(res, true);
        });
        echo.channel("end-crash-timer").listen("EndGameTimerCrash", res => {
            console.log(res);
            console.log("PREPARE TIMER")
            this.props.ticTimer(10 - res.timer);
        })
    }
    componentWillUnmount() {
        echo.leaveChannel("crash-coef");
        echo.leaveChannel("crash");
        echo.leaveChannel("bet");
        echo.leaveChannel("bet-adm");
        echo.leaveChannel("crash-timer");
        echo.leaveChannel("end-crash-timer")
    }

    render() {
        return (
            <>
                <Header pageName={'Crash'}/>
                <div className="main">
                    <SideMenuLeft nowIs={'crash'}/>
                    <CrashMain/>
                </div>
            </>
        );
    }
}

export default CrashPage