import {GET_DAILY_GAME, START_DAILY_GAME} from "../../Actions/Game/BonusDailyGameAction";

const BonusDailyGameReducer = (state = {
    isAvailable: true,
    arr: [],
    isGoing: false,

},action) => {
    switch (action.type) {
        case GET_DAILY_GAME: return  {
            ...state,
            ...action.data
        };
        case START_DAILY_GAME: return {
            ...state,
            isGoing: true,
            ...action.data
        };
        default: return state;
    }
};



export {
    BonusDailyGameReducer
}