import {connect} from "react-redux";
import SenoyrGameWrapper from "../SenoyrGameWrapper";
import {endSenoyrGame} from "../../../../../Actions/Game/KingOfTheHillAction";

const mapStateToProps = state => {
    const {senoyr, gameSenyor} = state.KingOfTheHill;
    const {gameStarted, gameFinished, timer} = senoyr;
    const status_id = gameSenyor.status_id;
    return {
        gameStarted,
        gameFinished,
        timer,
        status_id
    }
};


const mapDispatchToProps = dispatch => {
    return {
        gameEnd: () => dispatch(endSenoyrGame)
    }
};


export default connect(mapStateToProps, mapDispatchToProps)(SenoyrGameWrapper);