import React, {useState} from "react";
import {Link} from "react-router-dom";
import {UsersSearch} from "./SearchCompionents/UsersSearch";
import {WalletSearch} from "./SearchCompionents/WalletSearch";
import {OutPutSearch} from "./SearchCompionents/OutputSearch";
import {PromoCodesSearchGen} from "./SearchCompionents/SearchPormo";
const SEARCH_BLOCKS = {
    "Пользователи": <UsersSearch/>,
    "Пополнение": <WalletSearch/>,
    "Вывод": <OutPutSearch/>,
    "Промокоды": <PromoCodesSearchGen/>
};

function Header({pageName}) {
    return (
        <header className="header">
            <div className="header__left">
                <Link to="/admin" className="header__logo">Logo</Link>
                <ul className="header__breadcrumbs breadcrumbs">
                    <li className="breadcrumbs__item">
                        <Link to="/admin" className="breadcrumbs__link">Главная /</Link>
                    </li>
                    <li className="breadcrumbs__item">
                        <a className="breadcrumbs__link breadcrumbs__link--active">{pageName}</a>
                    </li>
                </ul>
                <h3 className="header__page">{pageName}</h3>
            </div>
            {
                SEARCH_BLOCKS[pageName]
            }
        </header>
    )
}



export {
    Header
}