import React from "react";
import imgVector from "../../../assets/img/Vector.png"
import {useDispatch, useSelector} from "react-redux";
import {openModal} from "../../../Actions/ModalAction";
import {parseTime} from "../../../Help/ParserData";
const sha224 = require('js-sha256').sha224;

const CrashTopBar = () => {
    let dispatch = useDispatch();
    return (
        <div className="crash-top-bar">
            <a data-izimodal-open="#historyCrash"
               onClick={() => {
                   dispatch(openModal("CrashHistory"))
               }}
               data-izimodal-transitionin="fadeInDown"
               className="bank__button" style={{width: "auto", cursor: "pointer"}}
            >
                <img src={imgVector}/>
            </a>
            <HistoryGameList/>
        </div>
    )
};

const HistoryGameList = () => {
    const {games = []} = useSelector(state => state.Crash);
    return (
        <ul>
            {games.map((item, index) => {
                const {game = {},bets = []} = item;
                return <HistoryGameListItem key={index} game={game} bets={bets}/>
            })}
        </ul>
    )
};

const HistoryGameListItem = ({game = {}, bets = []}) => {
    const dispatch = useDispatch();
    const {profit,created_at: date, id} = game;
    const color = profit < 3 ? 1 : profit < 4 ? 2 : profit < 50 ? 3 : profit < 100 ? 4 : 5;
    const round = profit;
    let hash = sha224.create();
    hash = hash.update(round.toString()).hex();
    return (
        <li className={`color-${color}`}>
            <a  onClick={() => {
                dispatch(openModal("CrashHistoryGame", {
                    game: {
                        hash,
                        profit,
                        date: parseTime(date),
                        id,
                    },
                    bets: [...bets]
                }))
            }}>
                {profit.toFixed(2)}
                <span className="tooltiptext">
                    ROUND: {round}<br/>
                    HASH: {hash}
                </span>
            </a>
        </li>
    )
};
export {
    CrashTopBar
}