import React, {useEffect, useState} from "react";
import {WalletTable} from "./UI/WalletTable";
import {PaginationComponent} from "../block/Pagination";
import {adminGetUsersData} from "../../Actions/AdminActions/AdminUsersAction";
import {useDispatch, useSelector} from "react-redux";
import {adminGetWalletData} from "../../Actions/AdminActions/AdminWalletAction";


function WalletMain() {
    const dispatch = useDispatch();
    const {current_page, from, to, total, last_page} = useSelector(state => state.AdminWallet.payments);
    const [nowIsPage, setNowIsPage] = useState(current_page);
    const search = useSelector(state => state.AdminWallet.search);
    useEffect(() => {
        if (!search) {
            dispatch(adminGetWalletData(nowIsPage))
        }
    }, [nowIsPage]);
    return (
        <div className="base">
            <div className="base__wrapper base__wrapper--promo">
                <div className="table-base__checker checker">
                    <p className="checker__text">Выбрано <span className="check-counter">10</span> записей</p>
                    <a className="checker__all">Выбрать все записи<span></span></a>
                    <a className="checker__cancel">Отменить выделение<span></span></a>
                </div>
                <form action="{{ route(Route::currentRouteName())  }}" style={{display: "none"}}
                      className="sorted-form">
                    <input type="text" name="sort"/>
                </form>
                <WalletTable/>
                <div className="base__bottom">
                    {/*Start paginations */}
                    <div className="paginations">
                        <PaginationComponent setState={setNowIsPage} state={nowIsPage} maxPageCount={last_page}/>
                      <p className="paginations__info">Показано {`от ${from} до ${to} из ${total} записей`}
                      </p>
                    </div>
                    {/*End paginations*/}
                    <button className="base__save" onClick="MainFunction.saveUser();">Сохранить</button>
                </div>
            </div>
        </div>
    )
};

export {
    WalletMain
}