import React from "react";
import defImg from "../../../assets/img/fox.png";

const KingParticipant = ({participant}) => {
    const { account = {} } = participant;
    const {image} = account;
    return(
        <li className="game__participants-item">
            <img src={image} alt="avatar"/>
        </li>
    )
};

export {
    KingParticipant
}