import {ADMIN_GET_OUTPUT_DATA, CHANGE_SEARCH_OUTPUT} from "../../Actions/AdminActions/AdminOutputAction";

const AdminOutputReducer = (state = {
    search: null,
    applications: {
        current_page: 1,
        data: [],
        from: 0,
        last_page: 1000,
        per_page: 12,
        to: 12,
        total: 1000,
    },
}, action) => {
    switch (action.type) {
        case CHANGE_SEARCH_OUTPUT:
            return  {
                ...state,
                search: action.value
            }
        case ADMIN_GET_OUTPUT_DATA:
            return  {
                ...state,
                ...action.data
            };
        default: return state;
    }
};


export {
    AdminOutputReducer
}