import React from "react";
import {useSelector} from "react-redux";
import "./SpinGameStyle.css";
import {spinBonusGame} from "../Help/SpinGame";
// import {}

class SpinGame extends React.Component {
    componentDidMount() {
        let block = document.querySelector(".choose-winner__slider");
        this.game = spinBonusGame("bonus-daily");
        block.style.maxWidth = this.game.innerWidth + "px";
    }
    componentWillUnmount() {
        this.game.clearTimer();
    }
    render() {
        return (
            <div>
                <BonusSlider/>
            </div>
        );
    }
}

const BonusSlider = () => {
    const {arr = [], winner = 20} = useSelector(state => state.Bonus);
    return (
        <div className="bonus choose-winner__slider">
            <div className="bonus__slider ">
                <div className="bonus__slider--active" id = "bonus-daily">

                    {[...arr,...arr].map((item, index) => {
                        return <BonusSliderItem key={index}  item={index === 34 ? winner : item}/>
                    })}
                </div>

            </div>
            <div className="bonus__point">
                <i className="ic-roulette-arrow"></i>
            </div>
        </div>
    )
};
const BonusSliderItem = ({item = 0}) => {
    switch (true) {
        case item === 0:
            return (
                <div className="bonus__slider-item bonus-item" data-count="{{ $a }}">
                    <span className="bonus-item__value">{item}</span>
                    <span className="bonus-item__coins">COINS</span>
                </div>
            );
        case item === 100:
            return (
                <div className="bonus__slider-item bonus-item bonus-item--orange" data-count="{{ $a }}">
                    <span className="bonus-item__value">{item}</span>
                    <span className="bonus-item__coins">COINS</span>
                </div>
            );
        case item === 20 || item === 50:
            return (
                <div className="bonus__slider-item bonus-item bonus-item--lose" data-count="{{ $a }}">
                    <span className="bonus-item__value">{item}</span>
                    <span className="bonus-item__coins">COINS</span>
                </div>
            );
        case item === 200 || item === 500:
            return (
                <div className="bonus__slider-item bonus-item bonus-item--green" data-count="{{ $a }}">
                    <span className="bonus-item__value">{item}</span>
                    <span className="bonus-item__coins">COINS</span>
                </div>
            );
        case item === 1000 || item === 5000:
            return (
                <div className="bonus__slider-item bonus-item bonus-item--yellow" data-count="{{ $a }}">
                    <span className="bonus-item__value">{item}</span>
                    <span className="bonus-item__coins">COINS</span>
                </div>
            );
        default:
            return (
                <div className="bonus__slider-item bonus-item bonus-item--blue" data-count="{{ $a }}">
                    <span className="bonus-item__value">{item}</span>
                    <span className="bonus-item__coins">COINS</span>
                </div>
            )

    }
};
export {
    SpinGame
}