import axios from "axios";
import {AXIOS_PARAMS, headers} from "../Help/AxiosHTTP";
import {errorNotification} from "../Notifications/Notification";

export const GET_PROFILE_DATA = "GET_PROFILE_DATA";



const getProfileData = dispatch => {
    axios.get(`${AXIOS_PARAMS.path}api/account/profile`, {...headers()}).then( res => {
        dispatch({
            type: GET_PROFILE_DATA,
            data: res.data
        })
    }).catch(e => {
        errorNotification("Service not response", " ");
    })
};



export {
    getProfileData
}