import React, {useEffect} from "react";
import CrashPage from "./Pages/CrashPage/CrashPage";
import HelpPage from "./Pages/HelpPage/HelpPage";
import {Error404} from "./Pages/404/404";
import {Switch, Route, Link} from 'react-router-dom';
import JackpotPageContainer from "./Pages/JackpotPage/Container/JackpotPageContainer";
import CoinFlipPageContainer from "./Pages/CoinflipPage/Container/CoinFlipPageContainer";
import KingOfTheHillPageContainer from "./Pages/KingOfTheHillPage/Container/KingOfTheHillPageContainer";
import ProfilePageContainer from "./Pages/ProfilePage/Container/ProfilePageContainer";
import DailyBonusPageContainer from "./Pages/DailyBonusPage/Container/DailyBonusPageContainer";
import {useDispatch, useSelector} from "react-redux";
import {Banner} from "./Components/Banner";
import {AccessDenied} from "./Pages/404/AccessDenied";
import {checkTheme} from "./Help/CheckTheme";
import {setDefMean} from "./Actions/UserAction";

function UserApp() {
    // if (role_id !== 2) {
    //     return (
    //         <Switch>
    //
    //         </Switch>
    //     )
    // }
    // const dispatch = useDispatch();
    const {role_id,sessionTheme = 1,isAuth} = useSelector(state => state.User);
    useEffect(()=>{
        checkTheme(role_id)
    },[sessionTheme]);

    return (
        <Switch>
            <Route exact path='/'>
                <JackpotPageContainer/>
            </Route>
            <Route exact path='/coinflip'>
                <CoinFlipPageContainer/>
            </Route>
            <Route exact path='/king-of-the-hill'>
                <KingOfTheHillPageContainer/>
            </Route>
            <Route exact path='/crash'>
                <CrashPage/>
            </Route>
            <Route exact path='/help'>
                <HelpPage/>
            </Route>
            {
                isAuth?
                    <Route exact path='/profile'>
                        <ProfilePageContainer/>
                    </Route>
                    :
                    ""
            }
            {
                isAuth?
                    <Route exact path='/daily-bonus'>
                        <DailyBonusPageContainer/>
                    </Route>
                    :
                    ""
            }
            <Route path='/admin'/>
            <Route component={Error404}/>
        </Switch>
    )
}

export {
    UserApp
}