import React from "react";
import {useSelector} from "react-redux";
import {CreateGameMobile} from "./UI/Game/CreateGame";
import {HistoryStatistics} from "./UI/HistoryStatistics";
import {HelpBlockMobile} from "../../Components/HelpBlock";
import {HistoryListGame} from "./UI/HistoryListGame";
import {BalanceMobile} from "../../Components/Balance";
import {CoinFlipWrapGame} from "./UI/Game/GameWrapper";
import {Loading} from "../../Components/Loading";
import {Banner} from "../../Components/Banner";

function CoinFlipCenter({games = {}}) {
    const isOpen = useSelector(state => state.Chat.isOpen);
    const {
        fetching,
        fetched,
        status
    } = useSelector(state => state.CoinFlip);
    if (fetching) {
        return <Loading width="60%"/>
    }
    if (fetched && status !== 200) {
        return <Banner width="60%"
                       title="Coinflip is dead, try later"
                       subtitle="Try reload page after 1 minute"
                />
    }
    return (
        <div className={`main__center main__center--king ${isOpen ? "" : "main__center--close"}`}>
            <CreateGameMobile/>
            <div className="main__center--king-wrap nano">
                <div className="nano-content">
                    <HistoryStatistics/>
                    <CoinHint/>
                    <CoinFlipWrapGame/>
                    <HistoryListGame/>
                </div>
            </div>
            <BalanceMobile/>
            <HelpBlockMobile/>
        </div>
    )
}


const CoinHint = () => {
    return (
        <ul className="coin-hint">
            <li className="coin-hint__text">Игрок</li>
            <li className="coin-hint__text">Ставка</li>
            <li className="coin-hint__text">Наблюдение</li>
            <li className="coin-hint__text">Ставка</li>
            <li className="coin-hint__text">Игрок</li>
        </ul>
    )
};


export {
    CoinFlipCenter
}