import React from 'react';
import './App.css';
import "./assets/css/admin/admin.fontFace.css";
import "./assets/css/main.min.css";
// import "./assets/css/main-dark.min.css";
// import "./assets/css/admin.min.css";
import {useDispatch, useSelector} from "react-redux";
import "../node_modules/izimodal/css/iziModal.min.css";
import Modal from "react-awesome-modal";
import {closeModal} from "./Actions/ModalAction";
import "./assets/css/PopupStyle.css";
import {MODALS} from "./Help/Modals";
import AppWrapper from "./Wrapper/AppWrapper";
import ReactNotification from 'react-notifications-component'
import 'react-notifications-component/dist/theme.css'
import {Link} from "react-router-dom";

function App() {
    const dispatch = useDispatch();
    const isVisible = useSelector(state => state.Modal.isVisible);
    const nowIs = useSelector(state => state.Modal.nowIs);
    const width = window.innerWidth;

    //check auth
    return(
        <div className="App">
            <ReactNotification/>
            {/*<Notifications position="top-right"/>*/}
            <AppWrapper/>
            <Modal visible={isVisible} className="lol" width={nowIs === "UserInfoModal"?0 :(width < 500 ? "100%" : "65%")}
                   onClickAway={() => dispatch(closeModal)}>
                {MODALS[nowIs]}
            </Modal>
            <div>
                <Link to="/admin">Перейти в админ панель</Link>
            </div>
        </div>
    );
}


export default App;
