import React, {useState} from "react";
import {useDispatch, useSelector} from "react-redux";
import {openModal} from "../../../Actions/ModalAction";
import $ from "jquery";


const Referral = () => {
    return (
        <div className="referral">
            <h4 className="referral__title">РЕФЕРАЛЬНАЯ СИСТЕМА</h4>
            <div className="referral__wrap">
                <ReferralLeft/>
                <ReferralRight/>
            </div>
        </div>
    )
};

const ReferralLeft = () => {
    const dispatch = useDispatch();
    const {
        experience,
        level
    } = useSelector(state => state.User);
    return (
        <div className="referral__left">
            <div className="ref-level">
                <div className="ref-level__top">
                    <p className="ref-level__level">{level} LEVEL.</p>
                    <p className="ref-level__exp">{experience%1000} / <span>1000 EXP.</span></p>
                </div>
                <div className="ref-level__timeline" style={{width: "100%"}}>
                    <span style={{width: `${100 - ((experience%1000)/1000 )*100}%`}}></span>
                </div>
                <p className="ref-level__text">1 уровень = 200 руб.</p>
                <div className="set-profile-form">
                    <div className="set-profile"><span>Профиль:</span></div>
                    <div className="set-profile"><label>E-mail:</label><input type="text" name="email"
                                                                              value=""
                                                                              placeholder=""/>
                    </div>
                    <div className="set-profile"><label>Пароль:</label> <input id="password" type="password"
                                                                               name="password"
                                                                               placeholder="Введите новый пароль"/>
                    </div>
                    <div className="set-profile"><label>Подтвердите:</label> <input id="password-check"
                                                                                    type="password"
                                                                                    name="re-type password"
                                                                                    placeholder="Подтвердите пароль"/>
                    </div>
                    <div className="set-profile"><label>Telegram:</label> <input type="text"
                                                                                 value=""
                                                                                 name="telegram"
                                                                                 placeholder=""/>
                    </div>
                    <div className="set-profile"><label>Вконтакте:</label> <input type="text" name="vk"
                                                                                  value=""
                                                                                  placeholder=""
                                                                                  disabled/>
                    </div>
                    <div className="set-profile"><label>Facebook:</label> <input type="text" name="facebook"
                                                                                 value=""
                                                                                 placeholder=""
                                                                                 disabled/>
                    </div>
                    <div className="set-profile">
                        <button id="saveProfile" className="button ref-link__promo-button">
                            Сохранить
                        </button>
                    </div>
                </div>
            </div>
            <div className="ref-value">
                <p className="ref-value__text">Кол-во ваших <br/>
                    рефералов:</p>
                <a className="ref-value__link"
                   onClick={() => dispatch(openModal("ReferralListModal"))}
                   data-izimodal-open="#referralTable"
                   data-izimodal-transitionin="fadeInDown">0</a>
            </div>
        </div>
    )
};

const ReferralRight = () => {
    const [promo, setPromo] = useState("");
    const {level,id} = useSelector(state => state.User);
    const [isVisible, setIsVisible] = useState(false);
    return (
        <div className="referral__right ref-link">
            <form className="ref-link__promo">
                <input type="text"
                       value={promo}
                       onChange={e => setPromo(e.target.value)}
                       name="promo"
                       className="ref-link__promo-field"
                       placeholder="Введите промокод"
                />
                <a id="promoActive" onClick="MainFunction.activatePromocode(this);"
                   className="button ref-link__promo-button">Активировать</a>
            </form>
            <p className="ref-link__title">Получайте бонусы с рефералов!</p>
            <div className="ref-link__wrap">
                <p className="ref-link__percent">{level ? level * 0.1 : 0.1}%</p>
                <p className="ref-link__text">
                    С каждого приведенного игрока на наш сервис
                </p>
            </div>
            <div style={{position:"relative"}}>

                <a id="showNotify" className="ref-link__link" onClick={(e) => {
                    const el = document.createElement('textarea');
                    el.value = e.target.textContent;
                    document.body.appendChild(el);
                    el.select();
                    document.execCommand('copy');
                    document.body.removeChild(el);
                    setIsVisible(true);
                    setTimeout(()=>{
                        setIsVisible(false)
                    },1500)
                }}>http://fox-x.ru/referral-link/{id}
                </a>
                <div className={`isCopied ${isVisible?"isCopied--active" : ""}`}>
                    Скопированно
                </div>
            </div>

            <input type="text" id="showNotifyInput" className="ref-link__link--hidden"
                   value={`http://fox-x.ru/referral-link/${id}`}/>
            <p className="ref-link__stock">Делитесь ссылкой с друзьями и получайте реферальные начисления!</p>
        </div>
    )
};


export {
    Referral
}