import React from "react";
import {ParticipantsWrapper} from "./UI/Game/Participants";
import {ChooseWinner} from "./UI/Game/ChooseWinner";
import defImg from "../../assets/img/fox.png";
import {JackpotHistory} from "./JackpotHistory";
import {MainCenterWrapper} from "../../Wrapper/MainCenterWrapper";
import {JackpotTimer} from "./UI/Game/JackpotTimer";
import {BankMobile} from "./UI/Game/Bank";
import {RoomsTypeBlockMobile} from "./UI/Game/RoomTypeBlock";
import {HelpBlockMobile} from "../../Components/HelpBlock";
import {BalanceMobile} from "../../Components/Balance";
import {useDispatch, useSelector} from "react-redux";
import {gameEnd, gameStart} from "../../Actions/Game/JackpotAction";
import {Loading} from "../../Components/Loading";
import {Banner} from "../../Components/Banner";
import {ParserData} from "../../Help/ParserData";

function JackpotCenter() {
    // const winnerIsChosen = useSelector(state => state.Jackpot.winnerIsChosen);
    const dispatch = useDispatch();
    const {
        isGoing,
        winnerIsChosen,
        fetching,
        fetched,
        status,
        game
    } = useSelector(state => state.Jackpot);

    if (fetching) {
        return <Loading width="60%"/>
    }
    if (fetched && status !== 200) {
        return <Banner width="60%" title="Jackpot is dead, try later" subtitle="Try reload page after 1 minute"/>
    }
    return (
        <MainCenterWrapper>
            <RoomsTypeBlockMobile/>
            <ParticipantsWrapper/>

            {
                isGoing ?
                    <ChooseWinner game={game} gameEndFunction={() => {
                        return {
                            gameEnd: () => dispatch(gameEnd),
                            gameStart: () => dispatch(gameStart)
                        }
                    }}/>
                    :
                    ""
            }
            {
                winnerIsChosen ?
                    <GameThirdStep/>
                    :
                    ""
            }
            <JackpotTimer/>
            <JackpotHistory/>
            <BankMobile/>
            <BalanceMobile/>
            <HelpBlockMobile/>
        </MainCenterWrapper>
    )
}

const GameThirdStep = () => {
    const {percent = 50, image = defImg, name = "nameless", bank = 3000, hash = "0iasjfg90asujdfas", link_hash, winner_ticket} = useSelector(state => state.Jackpot.result);
    return (
        <div className="winner winner-step-3 ">
            {/*
            @if($game && strtotime($game->end_game_at) < strtotime($time) && strtotime($game->animation_at) - 5 <= strtotime($time)
			&& strtotime($game->animation_at) >= strtotime($time)
			&& $game->winner_ticket)  @else hide @endif
                */}
            <h5 className="winner__title">ПОБЕДИТЕЛЬ</h5>
            <div className="winner__wrap">
                <div className="winner__left winner-circle">
                    <p className="winner-circle__text">ШАНС ВЫИГРЫША</p>
                    <span
                        className="winner-circle__numbers winner-percent">
                            {/*{{$result ? $result['percent'] : ''}}%*/} {percent}%
                        </span>
                </div>
                <div className="winner__center">
                    <div className="winner__center-top">
                        <div className="winner__avatar">
                            <img src={image} alt="avatar"/>
                        </div>
                        <div className="winner__box">
                            <p className="winner__name">
                                {/*{{$result ? $result['name'] : ''}}*/}{name}
                            </p>
                            <p className="winner__value">
                                {/*{{$result ? $result['bank'] : ''}}*/} {ParserData(bank)}
                                <span>COINS</span></p>
                        </div>
                    </div>
                    <p className="winner__hash">
                        Round number: <a target="_blank" href={link_hash}>
                        {hash}
                        {/*{{$result ? $result['hash'] : ''}}*/}
                    </a>
                    </p>
                </div>
                <div className="winner__right winner-circle">
                    <p className="winner-circle__text">СЧАСЛИВЫЙ БИЛЕТ</p>
                    <span className="winner-circle__numbers winner-number">
                        <i className="ic-dollar"></i>
                        {winner_ticket}
                        {/*{{$result ? $result['winnerTicket'] : ''}}*/}
                        </span>
                </div>
            </div>
        </div>
    )
};


export {
    JackpotCenter
}