import React, {useEffect, useState} from "react";
import {OutputTable} from "./UI/OutputTable";
import {useDispatch, useSelector} from "react-redux";
import {adminGetUsersData} from "../../Actions/AdminActions/AdminUsersAction";
import {adminGetOutputData} from "../../Actions/AdminActions/AdminOutputAction";
import {PaginationComponent} from "../block/Pagination";


function OutputMain() {
    const dispatch = useDispatch();
    const {current_page,from,to,total,last_page} = useSelector(state => state.AdminOutput.applications);
    const [nowIsPage, setNowIsPage] = useState(current_page);
    const {isVisible} = useSelector(state => state.Modal);
    const {search} = useSelector(state => state.AdminUsers);
    useEffect(() =>{
        if(!search){
            dispatch(adminGetOutputData(nowIsPage))
        }
    },[nowIsPage,isVisible]);


    return(
        <div className="base">
            <div className="base__wrapper base__wrapper--promo">

                <form style={{display:"none"}} className="sorted-form">
                    <input type="text" name="sort"/>
                </form>
                <OutputTable/>
                <div className="base__bottom">
                    {/*Start paginations */}
                    <div className="paginations">
                        <PaginationComponent setState={setNowIsPage} state={nowIsPage} maxPageCount={last_page}/>
                        <p className="paginations__info">Показано {`от ${from} до ${to} из ${total} записей`}
                        </p>
                    </div>
                    {/*End paginations*/}
                    <button className="base__save" onClick="MainFunction.saveUser();">Сохранить</button>
                </div>
            </div>
        </div>
    )
}

export {
    OutputMain
}