import {
    ADD_BET_JACKPOT,
    CHANGE_GAME_TYPE, FETCHED,
    GAME_END,
    GAME_ROLLING,
    GAME_START,
    GET_JACKPOT_DATA, NEW_GAME, SET_WINNER,
    START_FETCHING, UPDATE_BANK, UPDATE_TIMER, UPDATE_TIMER_NEW_GAME
} from "../../Actions/Game/JackpotAction";
import {spliceGame} from "../Help/help";

export const START_TIME = 35;
export const NEXT_GAME_TIME = 15;


const JackpotReducer = (state = {
    fetching: true,
    fetched: false,
    status: 200,
    bank: 0,
    game: {
        bank: 0,
        participants: [],
    },
    gameType: {},
    gameTypeId: 1,
    games: [],
    luckyOfTheDay: {},
    biggestBank: {},
    time: {},
    timer: {},
    startGame: true,
    isGoing: false,
    winnerIsChosen: false,
    result: {
        name: ""
    },
    endGame: START_TIME,
    nextGame: NEXT_GAME_TIME
}, action) => {
    switch (action.type) {
        case GET_JACKPOT_DATA: return {
            ...state,
            ...action.data,
            startGame: true,
            isGoing: false,
            winnerIsChosen: false,
            game: {
                bank: 0,
                participants: [],
                ...action.data.game
            }
        };
        case NEW_GAME: return  {
            ...state,
            game: {
                bank: 0,
                participants: []
            },
            games: [action.game, ...spliceGame(state.games)],
            startGame: true,
            isGoing: false,
            winnerIsChosen: false,
            endGame: START_TIME,
            nextGame: NEXT_GAME_TIME
        };
        case CHANGE_GAME_TYPE: return {
            ...state,
            gameTypeId: action.gameTypeId
        };
        case GAME_START: return {
            ...state,
            isGoing: false,
            startGame: true,
        };
        case GAME_ROLLING: return {
            ...state,
            startGame: false,
            winnerIsChosen: false,
            isGoing: true
        };
        case GAME_END: return {
            ...state,
            isGoing: true,
            winnerIsChosen: true
        };
        case ADD_BET_JACKPOT: return {
            ...state,
            game: {
                ...state.game,
                participants: [action.bet, ...state.game.participants]
            },
            winnerIsChosen: false,
            isGoing: false,
        };
        case UPDATE_BANK: return {
            ...state,
            game:{
                ...state.game,
                bank: action.bank
            }
        };
        case UPDATE_TIMER: return {
            ...state,
            endGame: action.newTime,
        };
        case UPDATE_TIMER_NEW_GAME: return {
            ...state,
            nextGame: action.newTime,
        };
        case SET_WINNER: return {
            ...state,
            game: {
                ...state.game,
                ...action.winner
            },
            result: {
                ...action.winnerData,
                winner_ticket: action.winnerData.winnerTicket
            }
        };
        //case for fetching
        case START_FETCHING: return  {
            ...state,
            status: "",
            fetching: true,
            fetched: false
        };
        case FETCHED: return  {
            ...state,
            status: action.status,
            fetching: false,
            fetched: true
        };

        default: return state
    }
};


export {
    JackpotReducer
}

