import axios from "axios";
import {AXIOS_PARAMS, headers} from "../../Help/AxiosHTTP";
import {errorNotification} from "../../Notifications/Notification";


export const ADMIN_GET_GAMES_DATA = "ADMIN_GET_GAMES_DATA";



const adminGetGamesData = dispatch => {
    axios.get(`${AXIOS_PARAMS.path}api/admin/games`, {...headers()}).then(res => {
        console.log(res);
        dispatch({
            type: ADMIN_GET_GAMES_DATA,
            data: res.data
        })
    }).catch(res => {
        errorNotification("Произошла непредвиденная ошибка", " ");
    })
};







export {
    adminGetGamesData
}