import axios from "axios";
import {AXIOS_PARAMS, headers} from "../../Help/AxiosHTTP";
import {errorNotification} from "../../Notifications/Notification";


export const ADMIN_GET_CRASH_DATA = "ADMIN_GET_CRASH_DATA";



const adminGetCrashData = dispatch => {
    axios.get(`${AXIOS_PARAMS.path}api/admin/crash`, {...headers()}).then(res => {
        dispatch({
            type: ADMIN_GET_CRASH_DATA,
            data: res.data
        })
        // console.log(res);
    }).catch(res => {
        errorNotification("Произошла непредвиденная ошибка", " ");
    })
};







export {
    adminGetCrashData
}