import React from "react";
import {useSelector} from "react-redux";

const HistoryGames = () => {
    return(
        <div id="historyBodyTwo" className="history__body history__body--active nano">
            <div className="nano-content">
                <HistoryGamesList/>
            </div>
        </div>
    )
};
const HistoryGamesList = ({offers = [{payment_id: 3},{payment_id: 2, winner: "nameless", account: "nameless"},{payment_id: 2, winner: "nameless", account: "nameless1"}]}) => {
    const historyGames = useSelector(state => state.Profile.listGames);
    return(
        <ul className="history__list">
            {historyGames.map((offer, index)=> {
                return <HistoryGamesListItem key={index} offer={offer}/>
            })}
        </ul>
    )
};

const HistoryGamesListItem = ({offer = {}}) => {
    let {payment_type_id = 0} = offer;
    if(payment_type_id === 7) {
        let {price = 999, name = "nameless", date="22/22/2222"} = offer;
        return(
            <li className="history__item hist-balance hist-balance--yellow">
                <div className="hist-balance__left">
                    <p className="hist-balance__sum"><i className="ic-money-yellow"></i>{price} Руб.</p>
                </div>
                <div className="hist-balance__right">

                    <p className="hist-balance__source--user"><i className="ic-user-black"></i> Игрок <span>{name}</span> пополнил баланс</p>
                    <p className="hist-balance__data">{date}</p>
                </div>
            </li>
        )
    } else {
        let {winner = "", account = ""} = offer;
        if(winner === account){
            const {price = 9999, nameGame = "Jackpot", date = "22/22/2222"} = offer;
            return (
                <li className="history__item hist-balance hist-balance--game hist-balance--green">
                    <div className="hist-balance__left">
                        <p className="hist-balance__sum">
                            <i className="ic-money-green">
                            </i>{price} coins</p>
                        <p className="hist-balance__game">{nameGame}</p>
                    </div>
                    <div className="hist-balance__right">
                        <p className="hist-balance__data">{date}</p>
                    </div>
                </li>
            )
        } else {
            const {price = 9999, nameGame = "Jackpot", date = "22/22/2222"} = offer;
            return (
                <li className="history__item hist-balance hist-balance--game hist-balance--red">
                    <div className="hist-balance__left">
                        <p className="hist-balance__sum">
                            <i className="ic-money-red"></i>{price} coins</p>
                        <p className="hist-balance__game">{nameGame}</p>
                    </div>
                    <div className="hist-balance__right">
                        <p className="hist-balance__data">{date}</p>
                    </div>
                </li>
            )
        }
    }
};


export {
    HistoryGames
}