import React from "react";
import {ClassicGame} from "./ClassicGame";
import {ClassicWinner} from "./ClassicWinner";
import {startTimerKingOfTheHill} from "../../Help/StartTimer";

// this is class for classic game

class ClassicGameWrapper extends React.Component {


    render() {
        const gameFinished = this.props.gameFinished;
        const status_id = this.props.status_id;

        return (
            <>
                {
                    status_id ?
                        <ClassicGame/>
                        :
                        ""
                }
                {
                    gameFinished ?
                        <ClassicWinner/>
                        :
                        ""
                }
            </>
        );
    }
}

export default ClassicGameWrapper