import React from "react";
import newGameImg from "../../assets/img/new-game-circle.png"
import timeOutGame from "../../assets/img/time-out-circle.png"
import defImg from "../../assets/img/fox.png";

import {useDispatch, useSelector} from "react-redux";
import {openModal} from "../../Actions/ModalAction";
import {BannerJackpot} from "./UI/JackpotBanner/JackpotBanner";
import {LastWinnersList} from "./UI/Game/LastWinners";
import {MainCenterWrapper} from "../../Wrapper/MainCenterWrapper";
import {ParserData} from "../../Help/ParserData";
function JackpotHistory(){
    const startGame = useSelector(state => state.Jackpot.startGame);
    return(
        <div className="jackpot-history">
            {
                !startGame?
                    <GameIsGoing/>
                    :
                    <StartGameMessage/>
            }
            <BetsList/>
            <LastWinnersList/>
            <BannerJackpot/>
        </div>
    )
}
const StartGameMessage = () => {
    let isAuth = useSelector(state => state.User.isAuth);
    let dispatch = useDispatch();
    return(
        <div className="jackpot-history-bottom message-start-game">
            <div className="jackpot-history-bottom__left">
                <img src={newGameImg} alt="icon" className="jackpot-history__img"/>
                    <div className="jackpot-history__wrap">
                        <p className="jackpot-history__title">Игра началась, делайте ваши депозиты</p>
                        <p className="jackpot-history__hash">Хэш раунда Sha224
                            <a>
                                {/*{{isset($game) ? hash('sha224', strval($game->winner_ticket_big)) : ''}}*/}
                            </a></p>
                    </div>
            </div>
            <div className="jackpot-history-bottom__right ">
                {
                    isAuth ?
                        <a data-izimodal-open="#betModal" data-izimodal-transitionin="fadeInDown"
                           className="jackpot-history__button" onClick={()=>dispatch(openModal("BetJackPot"))}>СДЕЛАТЬ СТАВКУ</a>
                        :
                        <a className="jackpot-history__button" data-izimodal-open="#auth" onClick={()=>dispatch(openModal("Auth"))}
                           data-izimodal-transitionin="fadeInDown">СДЕЛАТЬ СТАВКУ</a>
                }
            </div>
        </div>
    )
};
const GameIsGoing = () => {
    return(
        <div className="jackpot-history__wrapper nano bets" style={{height: "auto"}}>
            <div className="nano-content" style={{position: "unset",height: "auto"}}>
                <div className="jackpot-history-top ">
                    {/*@if($game && $game->end_game_at
			&& strtotime($game->end_game_at) < strtotime($time)
			&& strtotime($game->animation_at) - 14 <= strtotime($time)
			&& strtotime($game->animation_at) - 5 >= strtotime($time)) @else hide @endif*/}
                    <img src={timeOutGame} alt="icon" className="jackpot-history__img"/>
                    <div className="jackpot-history__wrap">
                        <p className="jackpot-history__title">ВРЕМЯ СТАВОК ИСТЕКЛО</p>
                        <p className="jackpot-history__hint">Игра уже начинается!</p>
                        <p className="jackpot-history__hash">Хэш раунда Sha224
                            <a>
                                {/*{{isset($game) ? hash('sha224', strval($game->winner_ticket_big)) : ''}}*/}
                            </a></p>
                    </div>
                </div>
            </div>
        </div>
    )
};

const BetsList = () => {
    const game = useSelector(state => state.Jackpot.game);
    if(game){
        const {participants} = game;
        return (
            <ul className="jackpot-history__list bets">
                {participants.reverse().map((bet, index) => {
                    return (<BetsListItem key={index} bet={bet}/>)
                })}
            </ul>
        )
    }
    return (
        <ul className="jackpot-history__list bets">
        </ul>
    )
};
const BetsListItem = ({bet = {}}) => {
    const {account, cash=3000, color = "#1ec",min_cash_number,max_cash_number} = bet;
    const {image = defImg, name = "nameless"} = account;
    return(
        <li className="jackpot-history__item history-item history-item--"
            style={{borderLeft: `2px solid ${color}`}}>
            <div className="history-item__left">
                <img src={image} alt="avatar" className="history-item__avatar"/>
                    <div className="history-item__wrap">
                        <p className="history-item__name">
                            {/*{{$bet->account->name}}*/}
                            {name}
                        </p>
                    </div>
            </div>
            <div className="history-item__right">
                <p className="history-item__value">
                    {/*{{$bet->cash}} */}
                    {ParserData(cash)}
                    <span> COINS</span></p>
                <p className="history-item__range">
                    <i className="ic-game-history"></i>
                    {ParserData(min_cash_number)} - {ParserData(max_cash_number)}{/*{{$bet->min_cash_number}} - {{$bet->max_cash_number}}*/}
                </p>
            </div>
        </li>
    )
};




export {
    JackpotHistory
}