import React from "react";
import {useSelector} from "react-redux";


const CrashResultTable = () => {
    return(
        <table className="table table-hover" style={{width: "100%", borderSpacing: 0, marginTop: 0}}>
            <CrashResultTableTitle/>
            <CrashResultTableInner/>
        </table>
    )
};
const CrashResultTableTitle = () => {
    return(
        <thead>
        <tr>
            <th scope="col">Пользователь</th>
            <th scope="col">Коэффициент</th>
            <th scope="col">Ставка</th>
            <th scope="col">Возможный выигрыш</th>
        </tr>
        </thead>
    )
};
const CrashResultTableInner = () => {
    const {ubets = []} = useSelector(state => state.Crash);
    return(
        <tbody id="result">
        {
            ubets.map(bet => {
                return <CrashResultTableItem bet={bet}/>
            })
        }
        </tbody>
    )
};
const CrashResultTableItem = ({ubet = {}}) => {
    const {coef = 0} = useSelector(state => state.Crash.game);
    const isWin = Math.pow(1.06,coef);
    const {
        bet = {},
        user = {}
    } = ubet;
    const {
        number,
        price
    } = bet;
    const {
        name,
        id
    } = user;
    return(
        <tr>
            <td scope="row" style={{textAlign: "center"}}>{name} ({id})</td>
            <td style={{textAlign: "center"}}>{number}X</td>
            <td style={{textAlign: "center"}}>{price}</td>
            <td className={`${isWin > number?"text-success":"text-danger"}`} style={{textAlign: "center"}}>{isWin > number?"+":"-"}{isWin > number ?price * number - price:""}</td>
        </tr>
    )
}
export {
    CrashResultTable
}