import React, {useEffect} from "react";
import {useDispatch, useSelector} from "react-redux";
import { updateJackpotTimer as chooseTimer, updateJackpotTimerNewGame as nextGame} from "../../../../Actions/Game/JackpotAction";
import {NEXT_GAME_TIME, START_TIME} from "../../../../Reducers/Game/JackpotReducer";

export const JackpotTimer = () => {
    const dispatch = useDispatch();
    const winnerIsChosen = useSelector(state => state.Jackpot.isGoing);
    const timer = useSelector(state => winnerIsChosen ? state.Jackpot.nextGame : state.Jackpot.endGame);
    const DEFAULT_TIMER = winnerIsChosen ? NEXT_GAME_TIME : START_TIME ;
    const stepRay = 200/ DEFAULT_TIMER;
    const updateJackpotTimer = winnerIsChosen ? chooseTimer : nextGame;
    const participants = useSelector(state => state.Jackpot.endGame);
    const isActive = participants.length ?
        participants.filter(item => item.account_id !== participants[0].account_id).length ?
            1
            :
            0
        :
        0;
    useEffect(() => {
        if(!timer || !isActive) return;

        const timerSync = setTimeout(()=>{
            dispatch(updateJackpotTimer(timer - 1))
        },1000);
        return () => clearTimeout(timerSync);
    },[winnerIsChosen, timer]);
    return(
        <div className="jackpot-timer first">
            <div className="jackpot-timer__wrap first">
                <div className="jackpot-timer__left first">
                    <span style={{width: ( timer) * stepRay >= 100 ? 100  + "%" : (timer) * stepRay  + "%" , transition: "0.9s linear"}}></span>
                </div>
                <div className="jackpot-timer__center first">
                    <p className="jackpot-timer__title first">До начала</p>

                    <p id="jackpotTimer" className="jackpot-timer__time first">
                        {/*{{($game && strtotime($game->end_game_at) > strtotime($time)) ? strtotime($game->end_game_at) - strtotime($time) : '35'}}*/}
                        {timer}
                    </p>
                </div>
                <div className="jackpot-timer__right first">
                    <span style={{width: (timer ) * stepRay - 100 <= 0 ? 0 + "%" :  (timer ) * stepRay - 100 + "%", transition: "0.9s linear"}}></span>
                </div>
            </div>
        </div>
    )
};