function spliceGame(games){
    let temp = games;
    temp.splice(9,1);
    return temp;
}



function findUserBet (ubets = [], userID){
    const myBet =  ubets.filter(item => item.user.id === userID);
    if(myBet.length > 0 ){
        if(Number(myBet[0].bet.number) === 0){
            return {
                isIBet: true,
                isICanCashOut: true,
            }
        } else {
            return {
                isIBet: true,
                isICanCashOut: false,
            }
        }
    } else {
        return {
            isIBet: false,
            isICanCashOut: false,
        }
    }
}

function updateUbets(ubets = [], bet = {}){
    console.log(ubets);
    console.log(bet);

    return [...ubets.map(item => {
        if(item.user.id === bet.user_id){
            return {
                user: {...item.user},
                bet: {...bet}
            }
        } else {
            return item
        }
    })]
}
export {
    spliceGame,
    findUserBet,
    updateUbets
}