import {ADMIN_GET_OUTPUT_DATA} from "../../Actions/AdminActions/AdminOutputAction";
import {ADMIN_GET_PROMO_DATA, CHANGE_SEARCH_PROMO} from "../../Actions/AdminActions/AdminPromocodesAction";

const AdminPromoCodesReducer = (state = {
    search: null,
    promocodes: {
        current_page: 1,
        data: [],
        from: 0,
        last_page: 1000,
        per_page: 12,
        to: 12,
        total: 1000,
    },
}, action) => {
    switch (action.type) {
        case ADMIN_GET_PROMO_DATA:
            return {
                ...state,
                ...action.data
            };
        case CHANGE_SEARCH_PROMO:
            return {
                ...state,
                search: action.value
            };
        default:
            return state;
    }
};


export {
    AdminPromoCodesReducer
}