import React, {useEffect, useState} from "react";


const BetForm = ({valueBet, setValueBet, readOnly = false}) => {
    const [isDown, setIsDown] = useState(false);
    const [isUp, setIsUp] = useState(false);
    useEffect(() => {
        if(readOnly){
            return;
        }
        let timer;
        clearTimeout(timer);
        if (isDown) {
            setIsUp(false);
            timer = setTimeout(() => {
                if(valueBet +1 <=0){
                    setValueBet(0)
                } else {
                    setValueBet(valueBet + 1)
                }
            }, 75);
        } else if (isUp) {
            setIsDown(false);
            timer = setTimeout(() => {
                if(valueBet - 1 <=0){
                    setValueBet(0)
                } else {
                    setValueBet(valueBet - 1)
                }
            }, 75);
        } else {
            clearTimeout(timer);
        }

    }, [valueBet, isDown, isUp]);
    return(
        <form className="bet__form">
            <input type="hidden" name="balance" value="{{ getBalance(auth()->user()) }}"/>
            <div className="bet__form-wrap">
                <label className="bet__form-label">COINS</label>
                <div id="coins2" className="bet__form-box">
                    <input type="number" name="balance" className="bet__form-input inputWithoutArrow" name="cash"
                        // onChange="MainFunction.validatePrice(this); MainFunction.calculate(this, event, 'coinflip');"
                           readOnly={readOnly}
                           onChange={(e)=>{
                               let nextValue = Number(e.target.value)
                               if(nextValue >= 0){
                                   setValueBet(nextValue)
                               } else {
                                   setValueBet(0)
                               }
                           }}
                           value={valueBet}/>
                    <span className="bet__form-up"
                          onMouseDown={() => {
                              setIsDown(true);
                          }}
                          onMouseUp={() => {
                              setIsDown(false)
                          }}
                          onMouseLeave={() => {
                              setIsDown(false)
                          }}
                    >

                    </span>
                    <span className="bet__form-down"
                          onMouseDown={() => {
                              setIsUp(true);
                          }}
                          onMouseUp={() => {
                              setIsUp(false)
                          }}
                          onMouseLeave={() => {
                              setIsUp(false)
                          }}
                    >

                    </span>
                </div>
            </div>
            <span className="bet__form-sum">=</span>
            <div className="bet__form-wrap bet__form-wrap--gray">
                <label className="bet__form-label">руб</label>
                <div id="coins-result2" className="bet__form-box count-money">
                    <input type="number" className="bet__form-input" readOnly value={valueBet/10}/>
                    <span className="bet__form-up"></span>
                    <span className="bet__form-down"></span>
                </div>
            </div>
        </form>
    )
};
export {
    BetForm
}