import {connect} from "react-redux";
import {CrashCanvas} from "../CrashCanvas";
import {checkOnline, reloadCanvas, setNewCoef, ticTimer} from "../../../../Actions/Game/CrashAction";


const mapStateToProps = state => {
    const Crash = state.Crash;
    const {game = {},isGoing = false, prepareStart = true,timer,isReload} = Crash;
    const {coef, endGameAt, number, profit,isOnline} = game;
    const currentPos = typeof coef === "undefined" ? 0: coef;
    return {
        currentPos,
        endGameAt,
        number,
        profit,
        isOnline,
        isGoing,
        prepareStart,
        timer,
        isReload
    }
};

const mapDispatchToProps = dispatch => {
    return {
        setNewCoef: newCoef => dispatch(setNewCoef(newCoef)),
        checkOnline: online => dispatch(checkOnline(online)),
        ticTimer: timer => dispatch(ticTimer(timer)),
        reloadCanvas: () => dispatch(reloadCanvas)
    }
};


export default connect(mapStateToProps,mapDispatchToProps)(CrashCanvas);