import ResizeSensor from "resize-sensor";
import blickBlue from "../../../assets/img/blick.png";
import blickOrange from "../../../assets/img/blickOrange.png";
import blickGreen from "../../../assets/img/blickGreen.png";

const data = [
    {
        profit: 1.2,
        time: 2
    }, {
        profit: 1.76,
        time: 3
    }, {
        profile: 2.3,
        time: 5
    }
];

const blickImgBlue = new Image();
blickImgBlue.src = blickBlue;

const blickImgGreen = new Image();
blickImgGreen.src = blickGreen;

const blickImgOrange = new Image();
blickImgOrange.src = blickOrange;


function Crash({id}) {
    let graphStep = 0.1;
    let startX = 0;
    let canvas = document.getElementById(id);
    let context = canvas.getContext('2d');
    let canvasWidth;
    let canvasHeight;
    let scaleX = 80;
    let scaleY = 300;
    let border = 5;
    let drawX = 0, drawY = 0, drawX2 = 0;
    let interval;
    let si = 0;
    let platr = 1;
    let cti = 15;
    let c_t = 10;
    let newStep;
    let stage = 9;
    let sec = 0;
    let timer;
    let drawYY = 0, drawXX = 0;
    let rotate = 1;
    let profit = 30;

    createCanvas();

    return {
        clearTimer: () => clearInterval(timer),
        startTimer: (currentX, i,setNewValue, isGoing) => startCrashTimer(currentX, i,setNewValue, isGoing),
        paintGraphic: (currentX) => paintCrashGraphic(currentX),
        endGame: (endGameNum) => crashedGame(endGameNum),
    };

    function startCrashTimer(currentX, i,setNewValue, isGoing) {
                clearCanvas();

                canvasWidth = canvas.width - 20;
                canvasHeight = canvas.height - 25;

                if ((border * 2 + currentX * scaleX) > canvasWidth) {
                    scaleX = (canvasWidth - border * 2) / currentX;
                }
                if ((border * 2 + getCrashGraphicY(currentX) * scaleY) > canvasHeight) {
                    scaleY = (canvasHeight - border * 2) / getCrashGraphicY(currentX);
                }

                context.clearRect(0, 0, canvasWidth, canvasHeight);
                context.clearRect(0, 0, canvasWidth + 20, canvasHeight);

                context.lineWidth = 0.5;

                context.moveTo(border + 20 + 0.5, canvasHeight - border);
                context.lineTo(border + 20 + 0.5, border);
                context.moveTo(border + 20 + 0.5, canvasHeight - border);
                context.lineTo(canvasWidth - border, canvasHeight - border);

                context.strokeStyle = '#aaa';
                context.stroke();

                context.stroke();
                context.font = "40px Tahoma";
                context.textAlign = "center";
                context.fillStyle = "green";
                context.fillText('Place your bets...' + i, canvas.width / 2, canvas.height / 2);
    }

    function paintCrashGraphic(curentX, randomNumber) {

        canvasWidth = canvas.width;
        canvasHeight = canvas.height - 20;
        context.lineWidth = 2;
        if ((border * 2 + curentX * scaleX) > canvasWidth) {
            scaleX = (canvasWidth - border * 2) / curentX;
        }
        if ((border * 2 + getCrashGraphicY(curentX) * scaleY) > canvasHeight) {
            scaleY = (canvasHeight - border * 2) / getCrashGraphicY(curentX);
        }

        context.lineWidth = 0.5;
        context.stroke();
        context.closePath();

        context.strokeStyle = '#aaa';
        context.clearRect(0, 0, canvasWidth, canvasHeight);
        context.beginPath();
        context.moveTo(border + 20 + 0.5, canvasHeight - border);
        context.lineTo(border + 20 + 0.5, border);
        context.moveTo(border + 20 + 0.5, canvasHeight - border);
        context.lineTo(canvasWidth - border, canvasHeight - border);


        context.closePath();
        drawX = startX;
        var isFirst = true;
        context.stroke();
        context.beginPath();
        context.lineWidth = 0.5;

        //Нижняя разметка цифр
        var m = 0;
        var x_max = 10;
        var x0 = 30, y0 = 30;
        var stepY = Math.round(canvasHeight / maxCount);
        var stepX = Math.round(canvasWidth / 10);
        var maxCount = 35 + 10;
        var maxCount = 35 + 10;
        var width = canvas.width - 80;
        var height = canvas.height + 10;
        //нижняя разметка и цифры
        context.font = "10px Tahoma";
        context.textAlign = "center";
        context.fillStyle = "#aaa";
        for (var i = x0; m < x_max; i += stepX) {
            m++;
            context.moveTo(i, height + y0);
            context.lineTo(i, height + y0 + 15);
            context.fillText(m + 's', i + 3, canvasHeight + 10);
        }
        context.lineWidth = 0.5;
        context.stroke();
        context.closePath();

        //Вертикальная разметка цифр
        var v = 0;
        var v_max = 7;
        var stepYv = 1;
        var _i = 8;
        var h = canvasHeight / v_max;
        var height = 10;
        while (v < v_max) {
            _i = _i - stepYv;
            v_max--;
            context.fillText(_i + 'x', 10, height + 10);

            height = height + h;
        }
        // console.log(curentX);
        // context.font = "50px Tahoma";
        // context.textAlign = "center";
        // context.fillStyle = "#ffcd05";
        // // if()
        // context.fillText(drawY.toFixed(2) + 'x', canvasWidth / 2, canvasHeight / 2);
        initGraph(curentX);
    }

    function initGraph(curentX, isFirst, randomNumber) {
        context.closePath();
        // $('#dp_bet').attr('disabled', true);
        clearCanvas();

        canvasWidth = canvas.width - 20;
        canvasHeight = canvas.height - 25;

        if ((border * 2 + curentX * scaleX) > canvasWidth) {
            scaleX = (canvasWidth - border * 2) / curentX;
        }
        if ((border * 2 + getCrashGraphicY(curentX) * scaleY) > canvasHeight) {
            scaleY = (canvasHeight - border * 2) / getCrashGraphicY(curentX);
        }

        context.clearRect(0, 0, canvasWidth + 20, canvasHeight);
        context.clearRect(0, 0, canvasWidth + 20, canvasHeight);
        context.clearRect(0, 0, canvasWidth + 20, canvasHeight);

        initScale(curentX);

        drawX = startX;
        var drawXX = startX;
        var isFirst = true;
        var isFirstX = true;

        context.beginPath();
        context.lineWidth = 9.5;
        context.stroke();

        context.strokeStyle = '#FFB838';
        while (drawX <= curentX) {
            drawY = getCrashGraphicY(drawX);
            drawX += graphStep;
            if (isFirst) {
                isFirst = false;
                context.moveTo(border + drawX * scaleX + 10, canvasHeight - 5 - border - (drawY - 1) * scaleY);
            } else {
                context.lineTo(border + drawX * scaleX + 10, canvasHeight - 5 - border - (drawY - 1) * scaleY);
            }
        }

        context.stroke();
        context.font = "50px Tahoma";
        context.textAlign = "center";
        if (parseInt(getCrashGraphicY(curentX)) < 10) {
            context.fillStyle = '#E95E3F';
        } else if (parseInt(getCrashGraphicY(curentX)) >= 10 && parseInt(getCrashGraphicY(curentX)) <= 50) {
            context.fillStyle = '#7DD358';
        } else if (parseInt(getCrashGraphicY(curentX)) >= 51 && parseInt(getCrashGraphicY(curentX)) <= 100) {
            context.fillStyle = '#3E8EBA';
        } else {
            context.fillStyle = '#FFB838';
        }
        var coeff = 0;
        if (drawY != undefined) {
            coeff = drawY.toFixed(2);
        }
        context.fillText(coeff + "x", canvasWidth / 2, canvasHeight / 2)
        init2Line(curentX);
    }

    function init2Line(curentX) {
        context.closePath();

        canvasWidth = canvas.width;
        canvasHeight = canvas.height - 25;

        if ((border * 2 + curentX * scaleX) > canvasWidth) {
            scaleX = (canvasWidth - border * 2) / curentX;
        }
        if ((border * 2 + getCrashGraphicY(curentX) * scaleY) > canvasHeight) {
            scaleY = (canvasHeight - border * 2) / getCrashGraphicY(curentX);
        }

        drawX = startX;
        drawXX = startX;
        var isFirst = true;
        var isFirstX = true;

        context.beginPath();
        context.lineWidth = 10;

        while (drawXX <= curentX) {
            drawYY = getCrashGraphicY(drawXX);
            drawXX += graphStep;

            if (parseInt(getCrashGraphicY(drawXX)) < 100) {
                if (isFirstX) {
                    isFirstX = false;
                    context.moveTo(border + drawXX * scaleX + 10, canvasHeight - border - (drawYY - 1) * scaleY);
                } else {
                    context.lineTo(border + drawXX * scaleX + 10, canvasHeight - border - (drawYY - 1) * scaleY);
                }
            }
        }

        context.strokeStyle = '#3E8EBA';
        context.stroke();

        init3Line(curentX);
    }

    function init3Line(curentX) {
        context.closePath();

        canvasWidth = canvas.width;
        canvasHeight = canvas.height - 25;

        if ((border * 2 + curentX * scaleX) > canvasWidth) {
            scaleX = (canvasWidth - border * 2) / curentX;
        }
        if ((border * 2 + getCrashGraphicY(curentX) * scaleY) > canvasHeight) {
            scaleY = (canvasHeight - border * 2) / getCrashGraphicY(curentX);
        }

        drawX = startX;
        drawXX = startX;
        var isFirst = true;
        var isFirstX = true;

        context.beginPath();
        context.lineWidth = 10;

        while (drawXX <= curentX) {
            drawYY = getCrashGraphicY(drawXX);
            drawXX += graphStep;

            if (parseInt(getCrashGraphicY(drawXX)) < 50) {
                if (isFirstX) {
                    isFirstX = false;
                    context.moveTo(border + drawXX * scaleX + 10, canvasHeight - border - (drawYY - 1) * scaleY);
                } else {
                    context.lineTo(border + drawXX * scaleX + 10, canvasHeight - border - (drawYY - 1) * scaleY);
                }
            }
        }

        context.strokeStyle = '#7DD358';
        context.stroke();

        init4Line(curentX);
    }

    function init4Line(curentX) {
        context.closePath();

        canvasWidth = canvas.width;
        canvasHeight = canvas.height - 25;

        if ((border * 2 + curentX * scaleX) > canvasWidth) {
            scaleX = (canvasWidth - border * 2) / curentX;
        }
        if ((border * 2 + getCrashGraphicY(curentX) * scaleY) > canvasHeight) {
            scaleY = (canvasHeight - border * 2) / getCrashGraphicY(curentX);
        }

        drawX = startX;
        drawXX = startX;
        var isFirst = true;
        var isFirstX = true;

        context.beginPath();
        context.lineWidth = 10;

        while (drawXX <= curentX) {
            drawYY = getCrashGraphicY(drawXX);
            drawXX += graphStep;

            if (parseInt(getCrashGraphicY(drawXX)) < 10) {
                if (isFirstX) {
                    isFirstX = false;
                    context.moveTo(border + drawXX * scaleX + 10, canvasHeight - border - (drawYY - 1) * scaleY);
                } else {
                    context.lineTo(border + drawXX * scaleX + 10, canvasHeight - border - (drawYY - 1) * scaleY);
                }
            }
        }

        context.strokeStyle = '#E95E3F';
        context.stroke();

        arcGraf(curentX);
    }

    function arcGraf(curentX) {
        context.closePath();

        canvasWidth = canvas.width;
        canvasHeight = canvas.height - 25;

        if ((border * 2 + curentX * scaleX) > canvasWidth) {
            scaleX = (canvasWidth - border * 2) / curentX;
        }
        if ((border * 2 + getCrashGraphicY(curentX) * scaleY) > canvasHeight) {
            scaleY = (canvasHeight - border * 2) / getCrashGraphicY(curentX);
        }

        drawX = startX;
        drawXX = startX;

        context.beginPath();
        context.lineWidth = 1;


        //Первый параметр - весь Х
        //Второй параметр - весь Y
        //Третий параметр - размер
        drawXX = curentX;
        drawYY = getCrashGraphicY(drawXX);

        context.arc(border + drawXX * scaleX + 10, canvasHeight - border - (drawYY - 1) * scaleY + 2, 8, 0, 2 * Math.PI, false);

        if (parseInt(getCrashGraphicY(drawXX)) < 10) {
            context.strokeStyle = '#E95E3F';
            context.fillStyle = '#E95E3F';
            context.fill();
            context.stroke();
        } else if (parseInt(getCrashGraphicY(drawXX)) >= 10 && parseInt(getCrashGraphicY(drawXX)) <= 50) {
            context.save();
            context.globalAlpha = 1;
            context.shadowColor = '#7DD358';
            context.shadowBlur = drawY.toFixed(2);
            context.strokeStyle = '#7DD358';
            context.fillStyle = '#7DD358';
            context.fill();
            context.stroke();
            context.restore();

            context.arc(border + drawXX * scaleX + 10, canvasHeight - border - (drawYY - 1) * scaleY + 2, 8, 0, 2 * Math.PI, false);
            context.save();
            context.globalAlpha = 1;
            context.shadowColor = '#7DD358';
            context.shadowBlur = 20;
            context.strokeStyle = '#7DD358';
            context.fillStyle = '#7DD358';
            context.fill();
            context.stroke();
            context.restore();
            context.arc(border + drawXX * scaleX + 10, canvasHeight - border - (drawYY - 1) * scaleY + 2, 8, 0, 2 * Math.PI, false);
            context.save();
            context.globalAlpha = 1;
            context.shadowColor = '#7DD358';
            context.shadowBlur = 40;
            context.strokeStyle = '#7DD358';
            context.fillStyle = '#7DD358';
            context.fill();
            context.stroke();
            context.restore();
            context.save();
            context.globalAlpha = 0.05;
            // rotate img
            context.translate((border + drawXX * scaleX + 10 - canvasHeight / 2) + canvasHeight / 2, (canvasHeight - border - (drawY - 1) * scaleY + 2 - canvasHeight / 2) + canvasHeight / 2);
            context.rotate(rotate * Math.PI / 180);
            context.translate(-(border + drawXX * scaleX + 10 - canvasHeight / 2) - canvasHeight / 2, -(canvasHeight - border - (drawY - 1) * scaleY + 2 - canvasHeight / 2) - canvasHeight / 2);
            context.drawImage(blickImgGreen, border + drawXX * scaleX + 10 - canvasHeight / 2, canvasHeight - border - (drawY - 1) * scaleY + 2 - canvasHeight / 2, canvasHeight, canvasHeight);
            context.globalAlpha = 1;
            context.restore();

        } else if (parseInt(getCrashGraphicY(drawXX)) >= 51 && parseInt(getCrashGraphicY(drawXX)) <= 100) {
            context.save();
            context.globalAlpha = 1;
            context.shadowColor = '#3E8EBA';
            context.shadowBlur = drawY.toFixed(2) / 3;
            context.strokeStyle = '#3E8EBA';
            context.fillStyle = '#3E8EBA';
            context.fill();
            context.stroke();
            context.restore();
            context.arc(border + drawXX * scaleX + 10, canvasHeight - border - (drawYY - 1) * scaleY + 2, 8, 0, 2 * Math.PI, false);
            context.save();
            context.globalAlpha = 1;
            context.shadowColor = '#3E8EBA';
            context.shadowBlur = 30;
            context.strokeStyle = '#3E8EBA';
            context.fillStyle = '#3E8EBA';
            context.fill();
            context.stroke();
            context.restore();
            context.arc(border + drawXX * scaleX + 10, canvasHeight - border - (drawYY - 1) * scaleY + 2, 8, 0, 2 * Math.PI, false);
            context.save();
            context.globalAlpha = 1;
            context.shadowColor = '#3E8EBA';
            context.shadowBlur = 60;
            context.strokeStyle = '#3E8EBA';
            context.fillStyle = '#3E8EBA';
            context.fill();
            context.stroke();
            context.restore();
            context.save();
            context.globalAlpha = 0.05;
            // rotate img
            context.translate((border + drawXX * scaleX + 10 - canvasHeight / 2) + canvasHeight / 2, (canvasHeight - border - (drawY - 1) * scaleY + 2 - canvasHeight / 2) + canvasHeight / 2);
            context.rotate(rotate * Math.PI / 180);
            context.translate(-(border + drawXX * scaleX + 10 - canvasHeight / 2) - canvasHeight / 2, -(canvasHeight - border - (drawY - 1) * scaleY + 2 - canvasHeight / 2) - canvasHeight / 2);
            context.drawImage(blickImgBlue, border + drawXX * scaleX + 10 - canvasHeight / 2, canvasHeight - border - (drawY - 1) * scaleY + 2 - canvasHeight / 2, canvasHeight, canvasHeight);
            context.globalAlpha = 1;
            context.restore();
        } else {
            context.save();
            context.globalAlpha = 1;
            context.shadowColor = '#FFB838';
            context.shadowBlur = drawY.toFixed(2) / 5;
            context.strokeStyle = '#FFB838';
            context.fillStyle = '#FFB838';
            context.fill();
            context.stroke();
            context.restore();
            context.arc(border + drawXX * scaleX + 10, canvasHeight - border - (drawYY - 1) * scaleY + 2, 8, 0, 2 * Math.PI, false);
            context.save();
            context.globalAlpha = 1;
            context.shadowColor = '#FFB838';
            context.shadowBlur = 30;
            context.strokeStyle = '#FFB838';
            context.fillStyle = '#FFB838';
            context.fill();
            context.stroke();
            context.restore();
            context.arc(border + drawXX * scaleX + 10, canvasHeight - border - (drawYY - 1) * scaleY + 2, 8, 0, 2 * Math.PI, false);
            context.save();
            context.globalAlpha = 1;
            context.shadowColor = '#FFB838';
            context.shadowBlur = 60;
            context.strokeStyle = '#FFB838';
            context.fillStyle = '#FFB838';
            context.fill();
            context.stroke();
            context.restore();
            context.save();
            context.globalAlpha = 0.05;
            // rotate img
            context.translate((border + drawXX * scaleX + 10 - canvasHeight / 2) + canvasHeight / 2, (canvasHeight - border - (drawY - 1) * scaleY + 2 - canvasHeight / 2) + canvasHeight / 2);
            context.rotate(rotate * Math.PI / 180);
            context.translate(-(border + drawXX * scaleX + 10 - canvasHeight / 2) - canvasHeight / 2, -(canvasHeight - border - (drawY - 1) * scaleY + 2 - canvasHeight / 2) - canvasHeight / 2);
            context.drawImage(blickImgOrange, border + drawXX * scaleX + 10 - canvasHeight / 2, canvasHeight - border - (drawY - 1) * scaleY + 2 - canvasHeight / 2, canvasHeight, canvasHeight);
            context.globalAlpha = 1;
            context.restore();
        }
        rotate += 1;
    }

    function initScale(curent) {
        // console.log('current', curent);
        clearCanvas();
        context.closePath();

        context.clearRect(0, 0, canvasWidth, canvasHeight);
        context.lineWidth = 0.5;
        canvasWidth = canvas.width;
        canvasHeight = canvas.height - 20;

        //context.beginPath();

        //Нижняя разметка цифр
        if (curent < 13) {
            var m = 13;
        } else {
            //var m = Math.trunc(curent);
            var m = Math.trunc(curent);
        }
        var curX = getCrashGraphicY(curent);
        var mm = 0;
        var x_max = 200;
        var x0 = 15, y0 = 15;
        var stepY = Math.round(canvasHeight / maxCount);
        var maxCount = 35 + 10;
        var maxCount = 35 + 10;
        var width = canvas.width - 80;
        var height = canvas.height + 10;
        var newStep;
        var simb;
        var x_stage;


        //Вертикальная разметка цифр
        var v = 0;
        var stepYv = 0;

        var h = canvasHeight;

        var height = 10;
        if (curX < 7) {
            var _i = 7;
            var v_max = 7;
        } else {
            var _i = curX;
            var v_max = curX;
        }
        var nwst;
        var simbX;

        if (curX < 10) {
            x_stage = 2;
        } else if (curX > 9 && curX < 50) {
            x_stage = 10;
        } else if (curX > 49 && curX < 200) {
            x_stage = 50;
        } else if (curX > 199 && curX < 400) {
            x_stage = 100;
        } else {
            x_stage = 200;
        }

        if (x_stage == 2) {
            nwst = curX;
            if (nwst < 3) {
                nwst = 3;
            } else {
                nwst = curX;
            }
        } else {
            nwst = curX;
        }

        if (curX < 3) {
            curX = 3;
        }

        var cwX = h / nwst;

        // console.log('cwx = ' + cwX + ' canvasHeight = ' + canvasHeight + ' NWST = ' + Math.trunc(nwst));
        let isFirstAssign = false;
        while (v < curX) {
            _i = _i - stepYv;
            v_max--;
            v++;
            context.font = "10px Tahoma";
            context.textAlign = "center";
            context.fillStyle = "#aaa";

            if (curX < 10) {
                if (!isFirstAssign && v === 2) {
                    // console.log(v);
                    isFirstAssign = true;
                    simbX = 1.5 + 'x';

                    context.moveTo(border + 20 + 0.5, h - 10);
                    context.lineTo(canvasWidth - border, h - 10);
                } else {
                    if (curX == 3) {
                        simbX = v + 'x';

                        context.moveTo(border + 20 + 0.5, h - 10);
                        context.lineTo(canvasWidth - border, h - 10);
                    } else {
                        if (v % x_stage) {
                            simbX = v + 'x';

                            context.moveTo(border + 20 + 0.5, h - 10);
                            context.lineTo(canvasWidth - border, h - 10);
                        } else {
                            simbX = '';
                        }
                    }
                }


                context.fillText(simbX, 10, h - 10);
            } else {
                if (v % x_stage) {
                    simbX = '';
                } else {
                    simbX = v + 'x';

                    context.moveTo(border + 20 + 0.5, h);
                    context.lineTo(canvasWidth - border, h);
                }
                context.fillText(simbX, 10, h);
            }
            h = h - cwX;
        }


        si = si + 0.025;

        if (curent < 15) {
            newStep = 1;
        } else if (curent > 14 && curent < 30) {
            newStep = 2;
        } else if (curent > 30 && curent < 70) {
            newStep = 5;
        } else if (curent > 70 && curent < 150) {
            newStep = 10;
        }

        if (newStep == 1) {
            stage = curent / newStep;
            if (stage < 13) {
                stage = 13;
            }
        } else {
            stage = curent;
        }

        var cw = canvasWidth / stage;
        //context.closePath();

        context.strokeStyle = '#aaa';
        //context.beginPath();

        //нижняя разметка и цифры
        for (var i = x0; mm < m; i += cw) {
            context.font = "10px";
            context.textAlign = "center";
            context.fillStyle = "#aaa";
            // context.moveTo(i, height + y0);
            // context.lineTo(i, height + y0 + 15);
            if (mm % newStep) {
                simb = '';
            } else {
                simb = mm + 's';
                if (mm != 0) {
                    context.moveTo(i + 3, canvasHeight - border);
                    context.lineTo(i + 3, border);
                }

                context.lineWidth = 0.1;
            }
            context.fillText(simb, i + 3, canvasHeight + 10);
            mm++;
        }

        context.strokeStyle = '#aaa';
        context.stroke();

        iniLineGr();
    }

    function crashedGame(endGameNum = 0) {
        clearCanvas();
        context.font = "50px Tahoma";
        context.textAlign = "center";
        context.fillStyle = "#E95E3F";
        if(endGameNum){
            context.fillText('Crashed @' + endGameNum + 'x', canvasWidth / 2, canvasHeight / 2);
        } else {
            context.fillText('Crashed @' + drawY.toFixed(2) + 'x', canvasWidth / 2, canvasHeight / 2);
        }
    }

    function iniLineGr() {
        context.closePath();
        context.beginPath();


        context.moveTo(border + 20 + 0.5, canvasHeight - border);
        context.lineTo(border + 20 + 0.5, border);
        context.moveTo(border + 20 + 0.5, canvasHeight - border);
        context.lineTo(canvasWidth - border, canvasHeight - border);

        context.lineWidth = 0.5;

        context.strokeStyle = '#aaa';
        context.stroke();

    }

    function clearCanvas() {
        canvasWidth = canvas.width;
        canvasHeight = canvas.height;
        context.clearRect(0, 0, canvasWidth, canvasHeight);
    }

    function getCrashGraphicY(x) {
        return Math.pow(1.06, x);
    }

    function createCanvas() {
        canvasWidth = canvas.clientWidth;
        canvasHeight = canvas.clientHeight;
        canvas.width = canvasWidth;
        canvas.height = canvasHeight;
        return canvas.getContext("2d");
    }

}

export {
    Crash
}
