import React from "react";
import {useDispatch, useSelector} from "react-redux";
import defImg from "../../../../assets/img/fox.png";
import {openModal} from "../../../../Actions/ModalAction";
import {gameIn} from "../../help/TakeBetRequest";
import {ParserData} from "../../../../Help/ParserData";


const CoinFlipWrapGameItem = ({game}) => {
    //looser is current player
    const {
        looser,
        participants,
        id
    } = game;

    if (Number(looser.color) === 0) {
        return (
            <div className="coin-game" data-game-id="{{ $game->id }}">
                <CoinGameLeft id={id}/>
                <CoinGameEye game={game}/>
                <CoinGameRight participant={looser}/>
            </div>
        )
    } else {
        return (
            <div className="coin-game" data-game-id="{{ $game->id }}">
                <CoinGameLeftRev participant={looser}/>
                <CoinGameEyeRev game={game}/>
                <CoinGameRightRev id={id}/>
            </div>
        )
    }

};

const CoinGameEye = ({game}) => {
    const dispatch = useDispatch();
    const {isAuth, role_id} = useSelector(state => state.User);
    return (
        <div className="coin-game__eye">
            {
                isAuth && role_id != 4 ?

                    <a onClick={() => dispatch(openModal('CoinFlipAwaitPlayer', {...game, count: 0}))}
                       data-izimodal-open="#waitPlayer"
                        // onClick="MainFunction.showGame(this);"
                       data-izimodal-transitionin="fadeInDown"
                       style={{display: "block", width: "100%", height: "100%"}}>
                        <i className="ic-eye"></i>
                    </a>
                    :
                    <a data-izimodal-open="#auth" data-izimodal-transitionin="fadeInDown"
                       onClick={() => dispatch(openModal("Auth"))}
                       style={{display: "block", width: "100%", height: "100%"}}>
                        <i className="ic-eye"></i>
                    </a>
            }
        </div>
    )
};
const CoinGameLeft = ({id}) => {
    const dispatch = useDispatch();
    const {isAuth, role_id} = useSelector(state => state.User);


    return (
        <div className="coin-game__action coin-game__action--orange">
            <a onClick={() => {
                   if (isAuth && role_id !== 4) {
                       gameIn(id,dispatch)
                   } else {
                       dispatch(openModal("Auth"))
                   }
               }}
                // onClick="MainFunction.checkAuthUser(this);"
               className="coin-game__button">ИГРАТЬ</a>
        </div>
    )

};

const CoinGameRight = ({participant}) => {
    const {cash, account} = participant;
    const {name, image} = account;

    return (
        <div className="coin-game__right">
            <div className="coin-game__wrap">
                <p className="coin-game__value">
                    {ParserData(cash)}
                    {/*{{$game->participants->first()->cash}} */}
                    <span> COINS</span></p>
                <p className="coin-game__text">сделал ставку </p>
            </div>
            <div className="coin-game__wrap2">
                <p className="coin-game__name">
                    {name}
                    {/*{{$game->participants->first()->account->name}}*/}
                </p>
                <div className="coin-game__box">
                    <img src={image} alt="avatar"
                         className="coin-game__avatar"/>
                    <i className="ic-coin-blue"></i>
                </div>
            </div>
        </div>
    )
};
const CoinGameLeftRev = ({participant = {}}) => {
    const {cash, account} = participant;
    const {name, image} = account;
    return (
        <div className="coin-game__left">
            <div className="coin-game__wrap2">
                <div className="coin-game__box">
                    <img src={image} alt="avatar"
                         className="coin-game__avatar"/>
                    <i className="ic-coin-orange"></i>
                </div>
                <p className="coin-game__name">
                    {name}
                    {/*{{$game->participants->first()->account->name}}*/}
                </p>
            </div>
            <div className="coin-game__wrap">
                <p className="coin-game__text">сделал ставку </p>
                <p className="coin-game__value">
                    {ParserData(cash)}
                    {/*{{$game->participants->first()->cash}}*/}
                    <span> COINS</span></p>
            </div>
        </div>
    )
}
const CoinGameRightRev = ({id}) => {
    const dispatch = useDispatch();
    const {isAuth, role_id} = useSelector(state => state.User);
    return (
        <div className="coin-game__action coin-game__action--blue">
            <a onClick={() => {
                   if (isAuth && role_id !== 4) {
                       gameIn(id,dispatch)
                   } else {
                       dispatch(openModal("Auth"))
                   }
               }}
                // onClick="MainFunction.checkAuthUser(this);"
               className="coin-game__button">ИГРАТЬ</a>
        </div>
    )
}
const CoinGameEyeRev = ({game}) => {
    const dispatch = useDispatch();
    const {isAuth, role_id} = useSelector(state => state.User);
    return (
        <div className="coin-game__eye">
            {
                isAuth && role_id !== 4 ?
                    <a href="#waitPlayer" data-izimodal-open="#waitPlayer" data-izimodal-transitionin="fadeInDown"
                        // onClick="MainFunction.showGame(this);"
                       onClick={() => dispatch(openModal('CoinFlipAwaitPlayer', {...game, count: 1}))}
                       style={{display: "block", width: "100%", height: "100%"}}>
                        <i className="ic-eye">

                        </i>
                    </a>
                    :
                    <a data-izimodal-open="#auth" data-izimodal-transitionin="fadeInDown"
                       onClick={() => dispatch(openModal("Auth"))}
                       style={{display: "block", width: "100%", height: "100%"}}>
                        <i className="ic-eye">

                        </i>
                    </a>
            }
        </div>
    )
}

export {
    CoinFlipWrapGameItem
}


