import axios from "axios";
import {AXIOS_PARAMS, headers} from "../Help/AxiosHTTP";
import {CLEAR_GAME, START_GAME} from "./Game/CoinFlipAction";

export const OPEN_MODAL = "OPEN_MODAL";
export const CLOSE_MODAL = "CLOSE_MODAL";

//fetching
export const START_FETCHING = "START_FETCHING_MODAL";
export const FETCHED = "FETCHED_MODAL";

export const UPDATE_PROPS = "UPDATE_PROPS";

const openModal = (modalName, props={}) => dispatch => {
    dispatch({
        type: OPEN_MODAL,
        modalName,
        props
    })
};


const closeModal = dispatch => {
    dispatch({
        type: CLOSE_MODAL,
    })
};

//coinflip actions
const getGameCoinFlipData = id => dispatch => {
    dispatch({
        type: START_FETCHING
    });
    axios.post(`${AXIOS_PARAMS.path}api/account/get-result-coinflip`, {gameId: id},{...headers()}).then(res => {
        dispatch({
            type: CLEAR_GAME
        });
        dispatch({
            type: UPDATE_PROPS,
            data: res.data
        });
        dispatch({
            type: START_GAME
        });
        dispatch({
            type: FETCHED,
            status: ""
        });

    })
};



export {
    openModal,
    closeModal,
    getGameCoinFlipData
}