import axios from "axios";
import {AXIOS_PARAMS, headers} from "../../Help/AxiosHTTP";
import {errorNotification} from "../../Notifications/Notification";


export const ADMIN_GET_WALLET_DATA = "ADMIN_GET_WALLET_DATA";
export const CHANGE_SEARCH_WALLET = "CHANGE_SEARCH_WALLET";



const adminGetWalletData = (page = 1,search = null, dataRange = []) => dispatch => {
    axios.get(`${AXIOS_PARAMS.path}api/admin/wallet?${search?`search=${search}&` : ""}page=${page}${dataRange.length?`&calendar_range=${dataRange[0]}—${dataRange[1]}`: ""}`, {...headers()}).then(res => {
        console.log(res);
        dispatch({
            type: ADMIN_GET_WALLET_DATA,
            data: res.data
        })
    }).catch(res => {
        errorNotification("Произошла непредвиденная ошибка", " ");
    })
}







export {
    adminGetWalletData
}