function spinBonusGame(id, winner = 30) {
    const innerBlock = document.getElementById(id);
    let moveStep = 84;
    let nowIs = 0;
    let timer;
    let width = window.innerWidth;
    let innerWidth = innerBlock.parentNode.clientWidth;

    // let startPosition = -80;
    let startPosition = width <= 540 ?
        ((((540 - width) * 124) / 260) + 211)
        :
        width <= 1280 ?
            211
            :
            width <= 1420 ?
                ((((1420 - width) * 68) / 139) + 82)
                :
                82;
    innerBlock.style.marginLeft = `-${startPosition}px`;

    // step();
    // setTimeout(()=> {
    //     step(2000)
    //     step(2000)
    // }, 500);
    // setTimeout(()=> {
    //     step(500)
    // }, 500);    setTimeout(()=> {
    //     step(5000)
    // }, 500);
    setLoop(35);

    function setLoop(duration) {
        if (nowIs < winner) {
            timer = setTimeout(() => {
                // duration+= Number(`1.10${playerLength - nowIs}`);
                if (winner - nowIs < 35) {
                    if (winner - nowIs > 24) {
                        duration *= 1.11;
                        step(duration);
                        setLoop(duration)

                    } else if (winner - nowIs > 9) {
                        duration *= 1.068;
                        step(duration);
                        setLoop(duration)
                    } else if (winner - nowIs > 7) {
                        duration *= 1.10;
                        step(duration * 1.2);
                        setLoop(duration)
                    } else {
                        console.log(1);
                        duration *= 1.25;
                        step(duration * 1.5);
                        setLoop(duration)
                    }
                } else {
                    step(duration);
                    setLoop(duration)
                }
            }, duration)
        } else {
            setTimeout(() => {
                // gameEndFunction().gameEnd();
            }, duration * 4);
            // setTimeout(() => {
            //     gameEndFunction().gameStart();
            //     clearTimeout(timer);
            // }, duration * 10)
        }
    }

    function step(duration) {
        innerBlock.style.marginLeft = `-${nowIs * moveStep + startPosition}px`;
        innerBlock.style.transition = `${duration / 1000}s linear`;
        nowIs++;
    }

    function clearTimer() {
        clearTimeout(timer);
    }

    return {
        clearTimer,
        innerWidth
    }
}

export {
    spinBonusGame
}