import React from "react";
import "./CoinFlipStyle.css";
import {startSpin} from "../help/SpinnerGame";
import orangeCoin from "../../../assets/img/icons/coin-orange.png"
import blueCoin from "../../../assets/img/icons/coin-blue.png"

class GameSpinnerBlock extends React.Component {

    componentDidMount() {
        this.clearGame = startSpin("waitPlayerInner", this.props.winner, this.props.finishGame);
    }

    componentWillUnmount() {
        this.clearGame.clearTimer()
    }

    componentDidUpdate(prevProps, prevState, snapshot) {

    }

    render() {
        const winner = this.props.winner;
        return (
            <>
                <div className="waitPlayerCircleWrapper">
                    <div className="waitPlayerCircle"></div>
                </div>
                <div className="wait-palyer__roulette roulette">
                    <div className="waitPlayerInner">
                        {
                            [...new Array(60).map(item => 0)].map((item, index) => {
                                return <Coin key={index} color={winner === 1 ? index % 2 === 0 : index % 2 !== 0}/>
                            })
                        }
                    </div>
                </div>
            </>
        );
    }
}

const Coin = ({color}) => {
    return (
        <div className="coin">
            <img src={color ? orangeCoin : blueCoin} alt="icon"/>
        </div>
    )
}

export default GameSpinnerBlock