import {ADMIN_GET_GAMES_DATA} from "../../Actions/AdminActions/AdminGamesAction";

const AdminGamesReducer = (state = {
    coinflipGames: [],
    jackpotGames: []
}, action) => {
    switch (action.type) {
        case ADMIN_GET_GAMES_DATA: return {
            ...state,
            ...action.data
        }
        default: return state;
    }
};


export {
    AdminGamesReducer
}