const filterCoinFlipGameWithoutCurrentID = (games, game_id) => {
    console.log(game_id);
    console.log(games.filter(game => game.id !== game_id));
    return games.filter(game => game.id !== game_id)
};
const filterCoinFlipActiveGameWithoutCurrent = (games, game_id) => {
    return games.filter(game => game.id === game_id).length
};

const filterCoinFlipHistoryGame = (games, newGame) => {
    const tempGames = games;
    tempGames.unshift(newGame);
    tempGames.splice(10,1);
    return tempGames;
};

export {
    filterCoinFlipGameWithoutCurrentID,
    filterCoinFlipActiveGameWithoutCurrent,
    filterCoinFlipHistoryGame
}