import axios from "axios";
import {AXIOS_PARAMS} from "../../Help/AxiosHTTP";
import {errorNotification} from "../../Notifications/Notification";

export const GET_DATA_ABOUT_GAME = "GET_DATA_ABOUT_GAME";
export const SET_NEW_COEF = "SET_NEW_COEF";
export const CHECK_ONLINE = "CHECK_ONLINE";
export const START_TIMER = "START_TIMER";
export const START_GAME = "START_GAME";
export const BETS_TIME = "BETS_TIME";
export const RELOAD = "RELOAD";
export const SET_NEW_GAME = "SET_NEW_GAME";
export const SET_NEW_BET = "SET_NEW_BET";
export const CHECK_AM_I_BET = "CHECK_AM_I_BET";
export const MERGE_GAME = "MERGE_GAME";
export const UPDATE_UBETS = "UPDATE_UBETS";
//fetch action
export const START_FETCHING = "START_FETCHING_CRASH";
export const FETCHED = "FETCHED_CRASH";


const getGameDataCrash = dispatch => {
    dispatch({
        type: START_FETCHING,
    })
    // let data = {
    //     isStart: false,
    //     isGoing: false,
    //     isEnd: false,
    //     timer: 10,
    //     randNumbers: [1,1.3, 1.6, 1.9]
    // };
    axios.get(`${AXIOS_PARAMS.path}api/crash`).then(res => {
        dispatch({
            type: GET_DATA_ABOUT_GAME,
            data: res.data,
        })
        dispatch({
            type: FETCHED,
        })
    }).catch(res => {
        dispatch({
            type: FETCHED,
        })
        errorNotification("Непредвиденная ошибка", " ")
    });
};

const setNewCoef = (newCoef, online) => dispatch => {
    dispatch({
        type: SET_NEW_COEF,
        newCoef
    });
    dispatch({
        type: START_GAME,
    });
    checkOnline(online);
};

const checkOnline = online => dispatch => {
    dispatch({
        type: CHECK_ONLINE,
        online
    })
};
const reloadCanvas = dispatch => {
    dispatch({
        type: RELOAD,
    })
};

const setNewGame = newGame => dispatch => {
    const {
        game = {},
        ubets = []
    } = newGame;
    dispatch({
        type: SET_NEW_GAME,
        game,
        ubets
    })
}

const ticTimer = time => dispatch => {
    console.log(time);
    dispatch({
        type: START_TIMER,
        time
    });
    if(time === 9){
        dispatch({
            type: MERGE_GAME
        })
    }
    if (time === 0) {
        dispatch({
            type: START_GAME,
        })
    } else if (time <= 9) {
        dispatch({
            type: BETS_TIME
        })
    }


};

const setNewBet = (res) => dispatch => {
    const {
        bet = {},
        cashout = {},
        info = {}
    } = res;
    dispatch({
        type: SET_NEW_BET,
        data: {
            bet: {
                price: bet,
                number: cashout
            },
            user: {
                ...info
            }
        }
    })
};

const checkAmIBet = (userID) => dispatch => {
    dispatch({
        type: CHECK_AM_I_BET,
        userID
    })
};


const updateUbets = (bet) => dispatch => {
    dispatch({
        type: UPDATE_UBETS,
        bet
    })
}
export {
    getGameDataCrash,
    setNewCoef,
    checkOnline,
    ticTimer,
    reloadCanvas,
    setNewGame,
    setNewBet,
    checkAmIBet,
    updateUbets
}