import React from "react";
import {useDispatch, useSelector} from "react-redux";
import {closeModal} from "../../../Actions/ModalAction";
import defImg from "../../../assets/img/fox.png";
import {startTimerCoinflip} from "../help/SpinnerGame";
import GameSpinnerBlock from "./GameSpinnerBlock";
import {finishGame} from "../../../Actions/Game/CoinFlipAction";

const AnimationGame = ({}) => {

    const {fetched} = useSelector(state => state.Modal);
    let {game = {}, winnerApplication = {}} = useSelector(state => state.Modal.props);
    const {gameReady, gameEnd} = useSelector(state => state.CoinFlip);
    const {id = "", hashWinner = "", winnerTicket = "",} = useSelector(state => state.Modal.props);
    const dispatch = useDispatch();

    if(!fetched) {
        return  <></>
    }

    const {bank = "200"} = game;
    const bet = Number(bank) / 2;
    const {color} = winnerApplication;
    const winner = Number(color);
    const winnerUser = {
        ...winnerApplication,
        ...winnerApplication.account,
        account: "",
    };
    const looserUser = {
        ...game.looser,
        ...game.looser.account,
        account: "",
    };
    const participants = winner? [winnerUser, looserUser] : [looserUser, winnerUser];
    return (
        <div className="iziModal-wrap" style={{height: "auto"}} data-game-id={`${id}`}>
            <div className="iziModal-content" style={{padding: 0}}>
                <div className="modal__top">
                    <h6 className="modal__title">Coinflip #{id}</h6>
                    <a className="modal__close" data-izimodal-close onClick={() => dispatch(closeModal)}>
                        <i className="ic-close">
                        </i>
                    </a>
                </div>
                <div className="modal__body">
                    <div className="conclusion__balance conclusion__balance--hashSecret">
                        <p className="conclusion__balance-text">Round hash: {hashWinner}</p>
                        <p className={`modal-hash__text ${gameEnd ? "" : "hide"}`}>Round secret
                            <a href={`http://sha224.net/?val=${winnerTicket}`} target="blank"
                               className="conclusion__balance-hash">
                                {winnerTicket}
                            </a>
                        </p>
                    </div>
                </div>
                <div className="wait-palyer">
                    <LeftPlayer participant={participants[0]} winner={gameEnd ? winner === 1 : false} bet={bet}/>
                    {
                        gameReady ?
                            <GameSpinnerBlock winner={winner} finishGame={() => {
                                dispatch(finishGame);
                            }}/>
                            :
                            <Bank totalCash={participants[0].bet + participants[1].bet}/>
                    }
                    <RightPlayer participant={participants[1]} winner={gameEnd ? winner === 0 : false} bet={bet}/>
                </div>
                {
                    gameEnd ?
                        <div className="wait-palyer__action"><p className="wait-palyer__action-text">Победил:
                            {participants[winner? 0 : 1].name}</p>
                        </div>
                        :
                        <div className="wait-palyer__spinner">
                            <p className="wait-palyer__spinner-text">Игра началась</p>
                            <i className="ic-load-green wait-palyer__spinner-icon"></i>
                        </div>
                }
            </div>
        </div>
    )
};
const LeftPlayer = ({participant = {}, winner = false, bet}) => {
    const {image = defImg, name = "nameless"} = participant;
    return (
        <div className="wait-palyer__left">
            <div className="wait-palyer__box">
                <i className="ic-coin-orange"></i>
                <div className="wait-palyer__avatar">
                    <img src={image} alt="avatar"/>
                </div>
            </div>
            <p className="wait-palyer__nick">{name}</p>
            <p className="wait-palyer__rate wait-palyer__rate--first"
               data-cash="{{ $game->participants->first()->cash }}">{bet}<br/><span>COINS</span></p>
            <p className="wait-palyer__tickets">0-50</p>
            <p className={`wait-palyer__text ${winner ? "" : "hide"}`}>Победил</p>
        </div>
    )
};

const RightPlayer = ({participant = {}, winner = false, bet}) => {
    const {image = defImg, name = "nameless"} = participant;
    return (
        <div className="wait-palyer__right">
            <div className="wait-palyer__box">
                <i className="ic-coin-blue">
                </i>
                <div className="wait-palyer__avatar">
                    <img src={image} alt="avatar"/>
                </div>
            </div>
            <p className="wait-palyer__nick">{name}</p>
            <p className="wait-palyer__rate wait-palyer__rate--second"
               data-cash="{{ $game->participants->first()->cash }}">{bet}<br/><span>COINS</span></p>
            <p className="wait-palyer__tickets">50-100</p>
            <p className={`wait-palyer__text ${winner ? "" : "hide"}`}>Победил</p>
        </div>
    )
};

const Bank = ({totalCash, timer = "10"}) => {
    return (
        <div className="coin-create__center coin-create__center--one">
            <span className="coin-create__vs">vs</span>
            <p className="wait-palyer__bet">Bank</p>
            <p className="wait-palyer__bet-done">{totalCash}</p>
            <div className="wait-palyer__timer">
                <i className="ic-timer"></i>
                <p className="wait-palyer__timer-text">0:<span
                    id="coinModalTimer2"
                    className="wait-palyer__timer-value">{timer}</span>
                </p>
            </div>
        </div>
    )
};
export {
    AnimationGame
}