import React, {useState} from "react";
import {useDispatch, useSelector} from "react-redux";
import {changeGameType} from "../../../../Actions/Game/JackpotAction";

const RoomsTypeBlockMobile = () => {
    const gameTypeID = useSelector(state => state.Jackpot.gameTypeId);
    const dispatch = useDispatch();
    return(
        <div className="rooms mobile-show">
            <p className="rooms__title">Выбор комнаты:</p>
            <ul className="rooms__wrap">
                <li className={`rooms__link ${gameTypeID === 1? "rooms__link--active":""}`} data-game-id="1"
                    onClick={()=>dispatch(changeGameType(1))}>CLASSIC
                </li>
                <li className={`rooms__link ${gameTypeID === 2? "rooms__link--active":""}`} data-game-id="2"
                    onClick={()=>dispatch(changeGameType(2))}>SMALL
                </li>
            </ul>
            <div id="roomClassic" className={`rooms__box ${gameTypeID === 1 ? "rooms__box--active" : ""}`}>
                <p className="rooms__text">Минимальная ставка от 300 coins</p>
            </div>
            <div id="roomSmall" className={`rooms__box ${gameTypeID === 2 ? "rooms__box--active" : ""}`}>
                <p className="rooms__text">Максимальная ставка до 300 coins</p>
            </div>
        </div>
    )
};

const RoomsTypeBlock = () => {
    const gameTypeID = useSelector(state => state.Jackpot.gameTypeId);
    const dispatch = useDispatch();
    return (
        <div className="rooms">
            <p className="rooms__title">Выбор комнаты:</p>
            <ul className="rooms__wrap">
                <li className={`rooms__link ${gameTypeID === 1 ? 'rooms__link--active' : ""}`} data-game-id="1"
                    onClick={() => dispatch(changeGameType(1))}>
                    CLASSIC
                </li>
                <li className={`rooms__link ${gameTypeID === 2 ? 'rooms__link--active' : ""}`} data-game-id="2"
                    onClick={() => dispatch(changeGameType(2))}>
                    SMALL
                </li>
            </ul>
            <div id="roomClassic" className={`rooms__box ${gameTypeID === 1 ? "rooms__box--active" : ""}`}>
                <p className="rooms__text">Минимальная ставка от 300 coins</p>
            </div>
            <div id="roomSmall" className={`rooms__box ${gameTypeID  === 2? "rooms__box--active" : ""}`}>
                <p className="rooms__text">Максимальная ставка до 300 coins</p>
            </div>
        </div>
    )
};

export {
    RoomsTypeBlock,
    RoomsTypeBlockMobile
}