export const AXIOS_PARAMS = {
    path: "https://localhost/"
    // path: "/"
};



export const headers = () => ({
    headers: {
        ...getToken()
    }
});


function getToken() {
    const tokenData = JSON.parse(localStorage.getItem("tokenData"));
    if(tokenData) {
        const {token, token_type} = tokenData;
        return {
            "Authorization": `${token_type} ${token}`
        }
    } else {
        return {}
    }
}