import React from "react";
import {CoinFlipWrapGameItem} from "./GameBeforeStart";
import {AnimationGame} from "./AnimationGame";
import {useSelector} from "react-redux";



const CoinFlipWrapGame = () => {
    const {animationGames,activeGames} = useSelector(state => state.CoinFlip);
    return(
        <div className="wrap-coinflip">
            {
                animationGames.map((game,index)=> {
                    return <AnimationGame game={game} key={index}/>
                })
            }
            {activeGames.map((game,index) => {
               return <CoinFlipWrapGameItem key={index} game={game}/>;
            })}
        </div>
    )
};


export {
    CoinFlipWrapGame
}


