import {connect} from "react-redux";
import CrashPage from "../CrashPage";
import {adminGetCrashData} from "../../../Actions/AdminActions/AdminCrashAction";
import {
    getGameDataCrash,
    setNewBet,
    setNewCoef,
    setNewGame,
    ticTimer,
    updateUbets
} from "../../../Actions/Game/CrashAction";
import {updateBalanceHardly} from "../../../Actions/UserAction";


const mapStateToProps = state => {
    return {

    }
};


const mapDispatchToProps = dispatch => {
    return {
        getData: () => dispatch(adminGetCrashData),
        getGameDataCrash: () => dispatch(getGameDataCrash),
        setNewCoef: newCoef => dispatch(setNewCoef(newCoef)),
        ticTimer: timer => dispatch(ticTimer(timer)),
        setNewGame: newGame => dispatch(setNewGame(newGame)),
        setNewBet: res => dispatch(setNewBet(res)),
        updateUbets: (bet) => dispatch(updateUbets(bet)),
        updateBalanceHardly: () => dispatch(updateBalanceHardly)
    }
};



export default connect(mapStateToProps,mapDispatchToProps)(CrashPage)