import React, {useEffect, useState} from "react";
import {PromocodesTable} from "./UI/PromocodesTable";
import {useDispatch, useSelector} from "react-redux";
import {adminGetOutputData} from "../../Actions/AdminActions/AdminOutputAction";
import {PaginationComponent} from "../block/Pagination";
import {adminGetPromoData} from "../../Actions/AdminActions/AdminPromocodesAction";


function PromocodesMain() {
    const dispatch = useDispatch();
    const {current_page,from,to,total,last_page} = useSelector(state => state.AdminPromo.promocodes);
    const [nowIsPage, setNowIsPage] = useState(current_page);
    const {search} = useSelector(state => state.AdminUsers);
    useEffect(() =>{
        if(!search){
            dispatch(adminGetPromoData(nowIsPage))
        }
    },[nowIsPage]);
    return (
        <div className="base">
            <div className="base__wrapper base__wrapper--promo">
                <div className="table-base__checker checker">
                    <p className="checker__text">Выбрано <span className="check-counter">10</span> записей</p>
                    <a className="checker__all">Выбрать все записи<span></span></a>
                    <a className="checker__cancel">Отменить выделение<span></span></a>
                </div>
                <form action="{{ route(Route::currentRouteName())  }}" style={{display:"none"}} className="sorted-form">
                    <input type="text" name="sort"/>
                </form>
                <PromocodesTable/>
                <div className="base__bottom">
                    {/*Start paginations */}
                    <div className="paginations">
                        <div className="paginations">
                            <PaginationComponent setState={setNowIsPage} state={nowIsPage} maxPageCount={last_page}/>
                            <p className="paginations__info">Показано {`от ${from} до ${to} из ${total} записей`}</p>
                        </div>
                    </div>
                    {/*End paginations*/}
                    <button className="base__save" onClick="MainFunction.saveUser();">Сохранить</button>
                </div>
            </div>
        </div>
    )
}

export {
    PromocodesMain
}