import React from "react";
import {useDispatch, useSelector} from "react-redux";
import {openModal} from "../../../../Actions/ModalAction";
import {ParserData} from "../../../../Help/ParserData";

const BankMobile = () => {
        const {participants,bank} = useSelector(state => state.Jackpot.game);
    const isAuth = useSelector(state => state.User.isAuth);
    const dispatch = useDispatch();


    return(
        <div className="bank mobile-show">
            <div className="bank__info">
                <p className="bank__title">Банк:</p>
                <p className="bank__players"> {participants.length} игроков</p>
            </div>
            <div className="bank__body">
                <div className="bank__circle">
                    <div className="bank__circle-inside">
                        <p className="bank__value">{ParserData(bank)}
                            <span>COINS</span>
                        </p>
                    </div>
                </div>
                {
                    isAuth ?
                        <a onClick={()=>dispatch(openModal("BetJackPot"))}
                            // onClick="$('#betModal').iziModal('open');"
                           className="button button--gradient bank__button"
                        >
                            СДЕЛАТЬ СТАВКУ
                        </a>
                        :
                        <a href="#"
                           className="button button--gradient bank__button"
                           data-izimodal-open="#auth"
                           onClick={()=>dispatch(openModal("Auth"))}
                           data-izimodal-transitionin="fadeInDown"
                        >
                            СДЕЛАТЬ СТАВКУ
                        </a>
                }
            </div>
        </div>
    )
};
const BankBlock = () => {
    const {participants,bank} = useSelector(state => state.Jackpot.game);
    const isAuth = useSelector(state => state.User.isAuth);
    const dispatch = useDispatch();
    return (
        <div className="bank">
            <div className="bank__info">
                <p className="bank__title">Банк:</p>
                <p className="bank__players">
                    {participants.length} игроков
                </p>
            </div>
            <div className="bank__body">
                <div className="bank__circle">
                    <div className="bank__circle-inside">

                        <p className="bank__value">
                            {ParserData(bank)}
                            <span>COINS</span>
                        </p>
                    </div>
                </div>
                {
                    isAuth ?
                        <a onClick={()=>dispatch(openModal("BetJackPot"))}
                            // onClick="$('#betModal').iziModal('open');"
                           className="button button--gradient bank__button"
                        >
                            СДЕЛАТЬ СТАВКУ
                        </a>
                        :
                        <a href="#"
                           className="button button--gradient bank__button"
                           data-izimodal-open="#auth"
                           onClick={()=>dispatch(openModal("Auth"))}
                           data-izimodal-transitionin="fadeInDown"
                        >
                            СДЕЛАТЬ СТАВКУ
                        </a>
                }
            </div>
        </div>
    )
};


export {
    BankMobile,
    BankBlock
}