const ParserData = coins => {
    return String(Math.floor(coins)).split("").reverse().join("").match(/.{1,3}/g).reverse().map(item => item.split("").reverse().join("")).join(",")
};
const parseTime = time =>  {
    return time.split(" ")[0].split("-").reverse().join("/");
};

const parseName = name => {
    let temp =  name;
    temp = temp.split(" ");
    let result = "";
    for(let i = 0; i < temp.length; i ++) {
        if(i === 0){
            if(temp[0].split("").length >= 8 && temp.length > 1){
                result += temp[0].substr(0, 8) + ".";
            } else {
                result +=temp[0].substr(0, 8) + " ";
            }
        } else if (i === temp.length - 1){
            result += temp[i].substr(0,1) + "."
        } else {
            result += temp[i].substr(0,1) + "."
        }
    }
    // result += temp[0].substr(0, 10) + "...";

    return result;
};


export {
    ParserData,
    parseTime,
    parseName
}