import React from "react";
import {useSelector} from "react-redux";
import {parseName, ParserData} from "../../../Help/ParserData";
import defImg from "../../../assets/img/fox.png";


const GameBankMobile = () => {
    const bank = useSelector(state => state.KingOfTheHill.gameClassic.bank);
    const {
        participants = [{}],
    } = useSelector(state => state.KingOfTheHill.gameClassic);
    let name;
    if(participants.length){
        name = participants[0].account.name;
    } else {
        name = "Coins";
    }
    return(
        <div className="game__bank game__bank--mobile">
            <p className="game__bank-title">В БАНКЕ:</p>
            <span className="game__bank-value">{ParserData(bank)}</span>
            <span className="game__bank-text">{parseName(name)}</span>
        </div>
    )
};
