import React from "react";
import defImg from "../../../../assets/img/fox.png";
import {useSelector} from "react-redux";
import {ParserData} from "../../../../Help/ParserData";


const ClassicWinner = () => {
    return(
        <div className="winner classic" >
            <h5 className="winner__title">ПОБЕДИТЕЛЬ</h5>
            <div className="winner__wrap">
                <WinnerLeft/>
                <WinnerCenter/>
                <WinnerRight/>
            </div>
        </div>
    )
};

const WinnerLeft = () => {
    const {
        bank = "300",
        winner = {cash: 300}
    } = useSelector(state => state.KingOfTheHill.classicWinner);
    return(
        <div className="winner__left winner-circle">
            <p className="winner-circle__text">СТАВКА ПОБЕДИТЕЛЯ</p>
            <span className="winner-circle__numbers">{Math.floor((Number(winner.cash)/Number(bank)) * 100)}%</span>
        </div>
    )
};
const WinnerCenter = () => {
    const {
        winner = {},
        game = {}
    } = useSelector(state => state.KingOfTheHill.classicWinner);
    // const {img = defImg, name = "nameless", cashValue = 9999} = winner;
    const {
        cash
    } = winner;
    const {
        name,
        image = defImg
    } = winner.account;
    let {hash = "3810da02b449a2e0f440578ff92deb0" } = game;
    return(
        <div className="winner__center">
            <div className="winner__center-top">
                <div className="winner__avatar">
                    <img src={image} alt="avatar"/>
                </div>
                <div className="winner__box ">
                    <p className="winner__name">{name}</p>
                    <p className="winner__value ">{ParserData(cash)}<span>COINS</span></p>
                    {/*hide style for winner__value*/}
                </div>
            </div>
            <p className="winner__hash">Round number:
                {/*hide style for winner_hash*/}
                <a href="http://sha224.net/?val=0.53604995623978">{hash}</a></p>
        </div>
    )
};
const WinnerRight = () => {
    const {
        bank = "300"
    } = useSelector(state => state.KingOfTheHill.classicWinner);
    return(
        <div className="winner__right winner-circle">
            <p className="winner-circle__text">СУММА ВЫИГРЫША</p>
            <span className="winner-circle__numbers">
                <i className="ic-dollar">
                </i>
                {ParserData(bank)}
            </span>
        </div>
    )
};


export {
    ClassicWinner
}