import React from "react";
import {useSelector} from "react-redux";
import {GameHistory} from "./UI/GameHistory";
import ClassicGameContainer from "./UI/Classic/Container/ClassicGameContainer";
import SenoyrGameContainer from "./UI/Senoyr/Container/SenoyrGameContainer";
import {Loading} from "../../Components/Loading";
import {Banner} from "../../Components/Banner";



function KingOfTheHillCenter() {
    const isOpen = useSelector(state => state.Chat.isOpen);
    const {
        fetching,
        fetched,
        status
    } = useSelector(state => state.KingOfTheHill);

    if (fetching) {
        return <Loading/>
    }
    if (fetched && status !== 200) {
        return <Banner title="King of the hill is dead, try later" subtitle="Try reload page after 1 minute"/>
    }
    return(
        <div className={`main__center main__center--king ${isOpen?"" : "main__center--close"}`}>
            <div className="main__center--king-wrap nano">
                <div className="nano-content">
                    <ClassicGameContainer/>
                    <SenoyrGameContainer/>
                    <GameHistory/>
                </div>
            </div>
        </div>
    )
}

export {
    KingOfTheHillCenter
}