import React, {useState} from "react";
import {useDispatch, useSelector} from "react-redux";
import {registerPost} from "../../../Actions/UserAction";
import {closeModal, openModal} from "../../../Actions/ModalAction";
import ReCAPTCHA from "react-google-recaptcha";

const FullRegister = () => {
    const dispatch = useDispatch();

    const [login, setLogin] = useState("login");
    const [email, setEmail] = useState("email@email.ru");
    const [name, setName] = useState("name");
    const [surname, setSurname] = useState("surname");
    const [password, setPassword] = useState("123123dddEEE");
    const [repeatPassword, setRepeatPassword] = useState("123123dddEEE");
    const [captcha, setCaptcha] = useState(true);
    const {status, fetched} = useSelector(state => state.User);
    return (
        <div className="modal modal--bet" id="authRegistration"><span>Регистрация</span>
            <form onSubmit={e => {
                e.preventDefault();
                dispatch(registerPost({login,email,name,surname,password}))
            }}>
                <ul className="register-block">
                    <div className="register-block_path">
                        <li>
                            <input type="text"
                                   name="login"
                                   value={login}
                                   onChange={e => setLogin(e.target.value)}
                                   placeholder="Логин"
                            />
                        </li>
                        <li>
                            <input type="text"
                                   name="name"
                                   value={name}
                                   onChange={e => setName(e.target.value)}
                                   placeholder="Имя"
                            />
                        </li>
                        <li>
                            <input type="password"
                                   name="password"
                                   value={password}
                                   onChange={e => setPassword(e.target.value)}
                                   placeholder="Пароль"
                                   required
                            />
                            <div className={password?"hide":""} style={{color: "red"}}>Поле Пароль обязательно для заполнения.</div>
                        </li>
                    </div>
                    <div className="register-block_path">
                        <li>
                            <input type="email"
                                   name="email"
                                   value={email}
                                   onChange={e => setEmail(e.target.value)}
                                   placeholder="Email"
                                   required
                            />
                            <div className={email?"hide":""} style={{color: "red"}}>Поле E-Mail адрес обязательно для заполнения.</div>
                        </li>
                        <li>
                            <input type="text"
                                   name="last_name"
                                   value={surname}
                                   onChange={e => setSurname(e.target.value)}
                                   placeholder="Фамилия"
                            />
                        </li>
                        <li>
                            <input type="password"
                                   name="repeatPassword"
                                   value={repeatPassword}
                                   onChange={e => setRepeatPassword(e.target.value)}
                                   placeholder="Повторите пароль"
                            />
                            <div className={password === repeatPassword ?"hide":""} style={{color: "red"}}>Введённые пароли не совпадают.</div>
                            <br/>
                        </li>
                    </div>
                </ul>
                <div className={status && fetched ? "" : "hide"} style={{color: "red"}}>Пользователь с такими данными уже существует.</div>
                <div className="g-recaptcha" data-sitekey="6LfBW58UAAAAANNC0-8TUsM_TeR7mRzbzgWdLRpr"></div>
                <div style={{display:"flex",justifyContent:"center"}}>
                    <ReCAPTCHA onChange={()=>setCaptcha(true)} sitekey="6LfBW58UAAAAANNC0-8TUsM_TeR7mRzbzgWdLRpr"/>
                </div>
                <div className={captcha?"hide":""} style={{color: "red"}}>Введите капчу.</div>
                <button
                    // onClick="MainFunction.registration(this, event);"
                    type={captcha?"submit":"button"}
                    name="Зарегистрироваться">
                    Отправить
                </button><br/>
                <a data-izimodal-open="#authLogin"
                   onClick={() => {
                       dispatch(closeModal);
                       setTimeout(()=>dispatch(openModal("Auth")),150)
                   }}
                   data-izimodal-transitionin="fadeInDown">Уже
                    зарегистрированы?</a>

            </form>
        </div>
    )
};

export {
    FullRegister
}