import {connect} from "react-redux";
import JackpotPage from "../JackpotPage";
import {
    addParticipants,
    gameRolling,
    getJackpotData,
    getWinner, prepareNewGame,
    updateJackpotTimer, updateJackpotTimerNewGame
} from "../../../Actions/Game/JackpotAction";


const mapStateToProps = state => {
    const Jackpot = state.Jackpot;
    const {id} = state.User;
    const {timer, startGame, isGoing, gameTypeId} = Jackpot;
    return {
        Jackpot: state.Jackpot,
        timer,
        startGame,
        isGoing,
        gameTypeId,
        id,
        user: {
            fetching: state.User.fetching,
            fetched: state.User.fetched,
        },
        jackpot: {
            fetching: state.Jackpot.fetching,
            fetched: state.Jackpot.fetched,
        }
    }
};

const mapDispatchToProps = dispatch => {
    return {
        gameRolling: () => dispatch(gameRolling),
        getJackpotData: (gameTypeId) => dispatch(getJackpotData(gameTypeId)),
        addParticipants: (newParticipant) => dispatch(addParticipants(newParticipant)),
        updateJackpotTimer: newTime => dispatch(updateJackpotTimer(newTime)),
        updateJackpotTimerNewGame: newTime => dispatch(updateJackpotTimerNewGame(newTime)),
        getWinner: winnerData => dispatch(getWinner(winnerData)),
        prepareNewGame: (game,id) => dispatch(prepareNewGame(game,id))
    }
};




export default connect(mapStateToProps,mapDispatchToProps)(JackpotPage)