import React, {useState} from "react";
import {useDispatch, useSelector} from "react-redux";
import defImg from "../../../assets/img/fox.png";
import {closeModal} from "../../../Actions/ModalAction";
import {AXIOS_PARAMS, headers} from "../../../Help/AxiosHTTP";
import {errorNotification, notificationSuccess} from "../../../Notifications/Notification";
import axios from "axios"

const setNewInfo = (setter, paramName, newParamValue) => {
    setter(prevParam => {
        return {
            ...prevParam,
            [paramName]: newParamValue
        }

    })
}


const UserInfoModal = () => {
    const dispatch = useDispatch();
    const {
        total_price_input = 320779122.3,
        total_price_output = 0,
        total_price_referal = 0,
        user = {},
        payments = [],
    } = useSelector(state => state.Modal.props);
    const {
        id,
        balance,
        image = defImg,
        vkontakte_id,
        name,
        last_name,
        role_id,
        email,
        is_blocked,
        is_referral_power,
        facebook_id,
    } = user;
    const [formInfo, setFormInfo] = useState({
        id,
        balance,
        image,
        vkontakte_id: `https://vk.com/id${vkontakte_id}`,
        name,
        last_name,
        role_id,
        email,
        is_blocked,
        is_referral_power,
        facebook_id: `https://facebook.com/id${facebook_id}`
    });
    console.log(formInfo);
    return (
        <div className="modal" style={{
            visibility: "visible",
            opacity: 1,
            transition: "all 0.5s ease 0s",
            margin: 0,
            minWidth: "900px",
            width: "100%"
        }}>
            <div className="modal__top">
				<span className="modal__icon">
					<img src={image} alt="" style={{width: "100%"}}/>
				</span>
                <p className="modal__name">{name} </p>
                <div className="modal__close">
                    <a className="modal__close-link" onClick={() => dispatch(closeModal)}></a>
                </div>
            </div>
            <div className="user-info">
                <div className="user-info__top">
                    <div className="user-info__box">
                        <p className="user-info__title">Имя</p>
                        <input type="text" className="user-info__field" name="name" onChange={(e) => {
                            setNewInfo(setFormInfo, "name", e.target.value)
                        }} value={formInfo.name}/>
                    </div>
                    <div className="user-info__box">
                        <p className="user-info__title">Фамилия</p>
                        <input type="text" className="user-info__field" name="last_name" onChange={(e) => {
                            setNewInfo(setFormInfo, "last_name", e.target.value)
                        }} value={formInfo.last_name}/>
                    </div>
                    <div className="user-info__box">
                        <p className="user-info__title">Ссылка</p>
                        {
                            facebook_id ?
                                <input type="text" className="user-info__field user-info__field--wide"
                                       onChange={(e) => {
                                           setNewInfo(setFormInfo, "facebook_id", e.target.value)
                                       }} value={formInfo.facebook_id}/>
                                :
                                vkontakte_id ?
                                    <input type="text" className="user-info__field user-info__field--wide"
                                           onChange={(e) => {
                                               setNewInfo(setFormInfo, "vkontakte_id", e.target.value)
                                           }} value={formInfo.vkontakte_id}/>
                                    :
                                    email ?
                                        <input type="text" className="user-info__field user-info__field--wide"
                                               onChange={(e) => {
                                                   setNewInfo(setFormInfo, "email", e.target.value)
                                               }} value={formInfo.email}/>
                                        :
                                        <input type="text" className="user-info__field user-info__field--wide"
                                               onChange={(e) => {
                                                   setNewInfo(setFormInfo, "email", e.target.value)
                                               }} value={formInfo.email ? formInfo.email : "Нет ссылки"}/>
                        }

                    </div>
                </div>
                <div className="user-info__bottom">
                    <div className="user-info__box">
                        <p className="user-info__title">Баланс</p>
                        <input type="number" className="user-info__field" name="balance" onChange={(e) => {
                            setNewInfo(setFormInfo, "balance", e.target.value)
                        }} value={formInfo.balance}/>
                    </div>
                    <div className="user-info__box">
                        <p className="user-info__title">Тип пользователя</p>
                        <div className="selectric-wrapper selectric-below">
                            <RoleCell role_id={formInfo.role_id} setter={setFormInfo}/>
                        </div>
                    </div>
                    <div className="user-info__box">
                        <p className="user-info__title">Реф. отчисления</p>
                        <ReferralPowerCell isReferralPower={formInfo.is_referral_power} setter={setFormInfo}/>
                    </div>
                    <div className="user-info__box">
                        <p className="user-info__title">Заблокировать</p>
                        <BlockedCell isBlocked={formInfo.is_blocked} setter={setFormInfo}/>
                    </div>
                </div>
            </div>


            <div className="modal__statistics">
                <p className="modal__statistics-title">Сводная статистика</p>
                <table className="modal-table">
                    <thead>
                    <tr>
                        <th>Общая сумма вывода</th>
                        <th>Общая сумма пополнений</th>
                        <th>Общая сумма реф. начислений</th>
                    </tr>
                    </thead>
                    <tbody>
                    <tr>
                        <td>{total_price_output}</td>
                        <td>{total_price_input}</td>
                        <td>{total_price_referal}</td>
                    </tr>
                    </tbody>
                </table>
            </div>
            <div className="wrap-paginate-user">
                <div className="modal__history">
                    <p className="modal__history-title">История пополнений/вывода</p>
                    <table className="modal-table">
                        <thead>
                        <tr>
                            <th>Сумма</th>
                            <th>Назначение</th>
                            <th>Дата</th>
                        </tr>
                        </thead>
                    </table>
                    <div className="nano modal-table__wrap">
                        <div className="nano-content">
                            <table className="modal-table modal-table--spacing">
                                <tbody>
                                {payments.map(item => {
                                    return (
                                        <tr className={item.payment_type_id===1?"modal-table--green":"modal-table--red"}>
                                            <td>{item.price}</td>
                                            <td>{
                                                item.payment_type_id === 1 ?
                                                    "Пополнение"
                                                    :
                                                    "Вывод"
                                            }</td>
                                            <td>{item.created_at.split(" ")[0]}</td>
                                        </tr>
                                    )
                                })}
                                </tbody>
                            </table>
                        </div>
                    </div>

                </div>
                <div className="modal__bottom">
                    <div></div>
                    <a className="modal__save" onClick={() => {
                        axios.post(`${AXIOS_PARAMS.path}api/admin/save-info-user`, {...formInfo}, {...headers()}).then(res => {
                            dispatch(closeModal);
                            notificationSuccess("Пользовательские данные успешно изменены", " ")
                        }).catch(res => {
                            dispatch(closeModal);
                            errorNotification("Непредвиденная ошибка", " ")
                        })
                    }}>Сохранить</a>
                </div>
                {/*<input type="hidden" name="id" value="55"/>*/}
            </div>
        </div>
    )
}

const RoleCell = ({role_id, setter}) => {
    return (
        <div className="selectric">
            <select className="label" style={{width: '100%', borderRadius: "50px"}} onChange={(e) => {
                setNewInfo(setter, "role_id", Number(e.target.value))
            }}>
                <option value="2" selected={role_id === 2}>Admin</option>
                <option value="4" selected={role_id === 4}>Youtuber</option>
                <option value="1" selected={role_id === 1}>User</option>
            </select>
        </div>
    )
};

const ReferralPowerCell = ({isReferralPower, setter}) => {
    return (
        <div className="selectric">
            <select name="is_referral_power" className="label" style={{width: '100%', borderRadius: "50px"}}
                    onChange={(e) => {
                        setNewInfo(setter, "is_referral_power", Number(e.target.value))
                    }}>
                <option selected={isReferralPower} value="1" className="bgn-text bgn-text--green">Да</option>
                <option selected={!isReferralPower} value="0" className="bgn-text bgn-text--red">Нет</option>
            </select>
        </div>

    )
};

const BlockedCell = ({isBlocked, setter}) => {
    return (
        <div className="selectric">
            <select name="is_blocked" className="label" style={{width: '100%', borderRadius: "50px"}} onChange={(e) => {
                setNewInfo(setter, "is_blocked", Number(e.target.value))
            }}>
                <option selected={isBlocked} value="1" className="bgn-text bgn-text--green">Да</option>
                <option selected={!isBlocked} value="0" className="bgn-text bgn-text--red">Нет</option>
            </select>
        </div>
    )
}

export {
    UserInfoModal
}