import React from "react";
import {useDispatch, useSelector} from "react-redux";
import {closeModal} from "../../../Actions/ModalAction";
import {gameIn} from "../help/TakeBetRequest";


const AwaitPlayer = () => {
    const dispatch = useDispatch();
    const {id, hash = "12dasfasfa", link_hash, looser, count, bank: cash} = useSelector(state => state.Modal.props);
    const {account} = looser;
    const {
        name,
        image
    } = account;
    return (
        <div className="modal modal--bet" id="waitPlayer2">
            <div className="modal__top">
                <h6 className="modal__title">Coinflip #{id}</h6>
                <a className="modal__close" data-izimodal-close onClick={() => dispatch(closeModal)}><i
                    className="ic-close"></i></a>
            </div>
            <div className="modal__body">
                <div className="conclusion__balance">
                    <a href={link_hash} target="blank" className="conclusion__balance-hash">
                        <p className="conclusion__balance-text">Round hash: {hash}</p>
                    </a>
                </div>
            </div>

            <div className="wait-palyer">
                {
                    count === 1 ?
                        <div className="wait-palyer__left">
                            <div className="wait-palyer__box">
                                <i className="ic-coin-orange"></i>
                                <div className="wait-palyer__avatar">
                                    <img src={image} alt="avatar"/>
                                </div>
                            </div>
                            <p className="wait-palyer__nick">{name}</p>
                            <p className="wait-palyer__rate">{cash} <br/><span>COINS</span></p>
                            <p className="wait-palyer__tickets">0-50</p>
                        </div>
                        :
                        <div className="wait-palyer__left">
                            <div className="wait-palyer__loader">
                                <i className="ic-load-gray"></i>
                            </div>
                            <p className="wait-palyer__loader-hint">Ожидание игрока</p>
                            <p className="wait-palyer__tickets">0-50</p>
                        </div>
                }
                <div className="coin-create__center coin-create__center--one">
                    <span className="coin-create__vs">vs</span>
                    <p className="wait-palyer__bet">Bank</p>
                    <p className="wait-palyer__bet-done">{cash}</p>
                    <div className="wait-palyer__timer">
                        <i className="ic-timer"></i>
                        <p className="wait-palyer__timer-text">0:<span id="coinModalTimer3"
                                                                       className="wait-palyer__timer-value">10</span>
                        </p>
                    </div>
                </div>
                {
                    count === 0 ?
                        <div className="wait-palyer__right">
                            <div className="wait-palyer__box">
                                <i className="ic-coin-blue"></i>
                                <div className="wait-palyer__avatar">
                                    <img src={image} alt="avatar"/>
                                </div>
                            </div>
                            <p className="wait-palyer__nick">{name}</p>
                            <p className="wait-palyer__rate">{cash} <br/><span>COINS</span></p>
                            <p className="wait-palyer__tickets">50-100</p>
                        </div>
                        :
                        <div className="wait-palyer__right">
                            <div className="wait-palyer__loader">
                                <i className="ic-load-gray"></i>
                            </div>
                            <p className="wait-palyer__loader-hint">Ожидание игрока</p>
                            <p className="wait-palyer__tickets">50-100</p>
                        </div>

                }
            </div>
            <div className="modal-hint">
                <p className="modal-hint__text">Сделайте ставку</p>
            </div>

            <div className="modal__buttons">
                <a className="button button--gradient" data-izimodal-open="#betModal"
                   onClick={() => {
                       gameIn(id);
                       dispatch(closeModal)
                   }}
                   data-izimodal-transitionin="fadeInDown">СДЕЛАТЬ СТАВКУ</a>
                <a href="#" className="button button--border" data-izimodal-close
                   onClick={() => dispatch(closeModal)}>Отмена</a>
            </div>
        </div>
    )
};
const AwaitStart = () => {
    return (
        <div className="modal modal--bet" id="waitPlayer">

        </div>
    )
};

export {
    AwaitPlayer,
    AwaitStart,
}