import React, {useEffect, useState} from "react";
import defImg from "../../../../assets/img/fox.png";
import "./jackpotWinnerStyle.css";
import {startGame} from "../../help/JackpotGame";
import {GameTypeId} from "../../help/GameTypeId";

class ChooseWinner extends React.Component {
    componentDidMount() {
        const gameEndFunction = this.props.gameEndFunction;
        const length = this.props.game.participants.length;
        const winner = length <= 100 ? 84: 484;
        this.game = startGame(winner, gameEndFunction);
        document.querySelector(".choose-winner").style.maxWidth = this.game.innerWidth;

    }

    componentWillUnmount() {
        this.game.clearTimer();
    }

    render() {
        const game = this.props.game;
        return (
            <div className="choose-winner">
                <a href="#" className="choose-winner__game">
                    <GameTypeId/>
                </a>
                <h5 className="choose-winner__title">ВЫБИРАЕМ ПОБЕДИТЕЛЯ</h5>
                <div className="wrap-choose-winner-slider">
                    <WinnerSlider players={game.participants} winner={game.participants.filter(item => item.account_id === game.winner_account_id)[0]}/>
                </div>
                <div className="choose-winner__point">
                    <i className="ic-roulette-arrow"></i>
                </div>
            </div>
        )
    }
}

const WinnerSlider = ({players = [], winner = {color: "#112"}}) => {
    const [playersList, setPlayersList] = useState([]);
    const [firstRender, setFirstRender] = useState(false);
    useEffect(()=>{
        if(!firstRender){
            setPlayersList([...new Array(players.length <= 100 ? 100 : 500).map(item => 0)].map((player, index) => {
                return <WinnerSliderItem key={index} player={
                    index === 88 || index === 488 ? winner : players[rand(0, players.length - 1)]
                }/>
            }));
            setFirstRender(true);
        }
    },[]);
    return (
        <div className="choose-winner__slider">
            <div className="jackpotWinnerInner">
                {playersList}
            </div>
        </div>
    )
};
const WinnerSliderItem = ({player = {}, bank = 9000}) => {
    let {img = defImg, color = "#cde", cash = 3000, id = 0} = player;
    return (
        <div className="choose-winner__slider-item choose-winner__slider-item--"
             data-count={id}>
            <img src={img} alt="avatar"
                 data-percent={Math.round(cash * 100 / bank)}
                 className="choose-winner__slider-avatar--"
                 style={{borderBottom: `3px solid ${color}`}}
            />
        </div>
    )
};

function rand(min, max) {
    let temp = Math.floor(Math.random() * (max - min + 1) + min);
    return temp;
}

export {
    ChooseWinner
}