import {ADMIN_GET_CRASH_DATA} from "../../Actions/AdminActions/AdminCrashAction";

const AdminCrashReducer = (state = {
    bets: [],
    game: {},
    profit: 0,
}, action) => {
    switch (action.type) {
        case ADMIN_GET_CRASH_DATA: return  {
            ...state,
            ...action.data,
        };
        default: return state;
    }
};


export {
    AdminCrashReducer
}