import React from "react";
import {useSelector} from "react-redux";


const WalletTable = () => {
    return(
        <>
            <WalletListHeader/>
            <WalletListInner/>
        </>
    )
};


const WalletListHeader = () => {
    return(
        <table id="table1-top" className="table-base table-base--five">
            <thead>
            <th className="table-base-col1">
                <label className="table-checkbox">
                    <input type="checkbox" className="table-checkbox__inp table-check--all"/>
                    <span className="table-checkbox__check--all"></span>
                </label>
            </th>
            <th className="table-base-col2 table-sort"
                data-order="{{ isset($sort) && $sort && isset($sort[0]) && $sort[0]->order == 'desc' ? 'desc' : 'asc' }}"
                data-column="id">ID<span></span></th>
            <th className="table-base-col3">Пользователь</th>
            <th className="table-base-col4 table-sort"
                data-order="{{ isset($sort) && $sort && isset($sort[0]) && $sort[1]->order == 'desc' ? 'desc' : 'asc' }}"
                data-column="payment_system_id">Система<span></span></th>
            <th className="table-base-col5 table-sort"
                data-order="{{ isset($sort) && $sort && isset($sort[0]) && $sort[2]->order == 'desc' ? 'desc' : 'asc' }}"
                data-column="price">Сумма<span></span></th>
            </thead>
        </table>
    )
};

const WalletListInner = () => {
    const {data: payments} = useSelector(state => state.AdminWallet.payments)
    return(
        <div className="nano table__scroll">
            <div className="nano-content">
                <table id="table1-body" className="table-base table-base--five">
                    <tbody>
                    {
                        payments.map(user =>{
                            return <WalletListItem user={user}/>
                        })
                    }
                    </tbody>
                </table>
            </div>
        </div>
    )
};

const WalletListItem = ({user}) => {
    const { id = 123123,
       account = {name: "name", last_name: "last"},
        vkontakte_id = "",
        payment_system = {name: "Сбербанк"},
        price = 3000
    } = user;
    return(
        <tr>
            <td className="table-base-col1">
                <label className="table-checkbox">
                    <input type="checkbox" className="table-checkbox__inp table-check--simple"/>
                    <span className="table-checkbox__check"></span>
                </label>
            </td>
            <td className="table-base-col2">{id}</td>
            {
                vkontakte_id ?
                    <td className="table-base-col3">
                        <a target="_blank" href={`https://vk.com/id${vkontakte_id}`}>{account.name} {account.last_name}</a>
                    </td>
                    :
                    <td className="table-base-col3">{account.name} {account.last_name}</td>
            }
            <td className="table-base-col4">{payment_system.name}</td>
            <td className="table-base-col5">{price}</td>
        </tr>
    )
};

export {
    WalletTable
}