import React, {useEffect, useState} from "react";
import {UsersTable} from "./UI/UsersTable";
import {PaginationComponent} from "../block/Pagination";
import {useDispatch, useSelector} from "react-redux";
import {adminGetUsersData} from "../../Actions/AdminActions/AdminUsersAction";


function UsersMain() {
    const dispatch = useDispatch();
    const {current_page,from,to,total,last_page} = useSelector(state => state.AdminUsers.users);
    const [nowIsPage, setNowIsPage] = useState(current_page);
    const {isVisible} = useSelector(state => state.Modal);
    const {search} = useSelector(state => state.AdminUsers);
    useEffect(() =>{
        if(!search){
            dispatch(adminGetUsersData(nowIsPage))
        }
    },[nowIsPage,isVisible]);
    return (
        <div className="base">
            <div className="base__wrapper base__wrapper--promo">
                <div className="table-base__checker checker">
                    <p className="checker__text">Выбрано <span className="check-counter">10</span> записей</p>
                    <a className="checker__all">Выбрать все записи<span></span></a>
                    <a className="checker__cancel">Отменить выделение<span></span></a>
                </div>

                <UsersTable/>
                <div className="base__bottom">
                    {/*Start paginations */}
                    <div className="paginations">

                        <PaginationComponent state={nowIsPage} setState={setNowIsPage} maxPageCount={last_page}/>
                        <p className="paginations__info">Показано {`от ${from} до ${to} из ${total} записей`}
                        </p>
                    </div>
                    {/*End paginations*/}
                    <button className="base__save" onClick="MainFunction.saveUser();">Сохранить</button>
                </div>
            </div>
        </div>
    )
}



export {
    UsersMain
}