import {connect} from "react-redux";
import ClassicGameWrapper from "../ClassicGameWrapper";
import {endClassicGame, startClassicGame} from "../../../../../Actions/Game/KingOfTheHillAction";

const mapStateToProps = state => {
    const {classic, gameClassic} = state.KingOfTheHill;
    const { gameStarted, gameFinished, timer} = classic;
    const status_id = gameClassic.status_id;
    return {
        gameStarted,
        gameFinished,
        timer,
        status_id
    }
};


const mapDispatchToProps = dispatch => {
    return {
        gameEnd: () => dispatch(endClassicGame)
    }
};


export default connect(mapStateToProps, mapDispatchToProps)(ClassicGameWrapper)