import React from "react";
import defImg from "../../../assets/img/fox.png"
import {useSelector} from "react-redux";
import {parseTime} from "../Help/ParseTime";
import {ParserData} from "../../../Help/ParserData";

function GameHistory () {
    const games = useSelector(state => state.KingOfTheHill.games);
    return(
        <ul className="game-history">
            {games.map((game,index) => {
                return <GameHistoryItem key={index} game={game}/>
            })}
        </ul>
    )
}

const GameHistoryItem = ({game}) => {
    const {
        bank,
        winner,
        id,
        end_game_at
    } = game;

    const {
        image,
        name
    } = winner;

    // const {winnerImg = defImg,winnerName = "nameless", totalCash = 9999, game_id = 999999, gameEnd = "5 day ago"} = game;
    return(
        <li className="game-history__item">
            <div className="game-history__left">
                <div className="game-history__avatar">
                    <img src={image} alt="avatar"/>
                </div>
                <p className="game-history__name">
                    {/*{{$game->winner->name}} */}
                    {name}
                </p>
                <span className="game-history__text">выиграл</span>
                <p className="game-history__result">
                    {/*{{$game->participants->sum('cash')}} */}
                    {ParserData(bank)}
                    <span> COINS</span></p>
            </div>
            <div className="game-history__right">
                <p className="game-history__title">
                    {id}
                    {/*{{$game->type->name}} #{{$game->id}}*/}
                </p>
                <p className="game-history__time">
                    <i className="ic-clock"></i>
                    { parseTime(end_game_at)}
                    {/*{{ \Carbon\Carbon::parse($game->end_game_at)->diffForHumans()}}*/}
                </p>
            </div>
        </li>
    )
};

export {
    GameHistory
}