import React from "react";
import Header from "../../Components/Header/Header";
import {MainWrapper} from "../../Wrapper/MainWrapper";
import {SideBarLeft} from "./SideBarLeft";
import {CoinFlipCenter} from "./CoinflipCenter";
import OnlineChat from "../../Components/Chat/Chat";
import {echo} from "../../socket/Socket";
import {Loading} from "../../Components/Loading";

class CoinFlipPage extends React.Component {
    componentDidMount() {
        this.props.getCoinFlipPageData();
        echo.channel("coin-flip").listen("CreateGameCoinFlip", e => {
            this.props.addGameCoinFlip(e);
        }).listen("StartGameCoinflip", e => {
            this.props.gameStartedSocketHandler(e, this.props.userId);
        }).listen("EndGameCoinflip", e => {
            const userId = this.props.userId;
            this.props.gameEndSocketData(e, userId);
            console.log(e);
        })
    }

    componentWillUnmount() {
        echo.leaveChannel("coin-flip");
    }

    render() {
        const {user, coinflip} = this.props;
        if (user.fetching && coinflip.fetching) {
            return <Loading width="100%"/>
        }
        return (
            <div>
                <Header nowIs={"/coinflip"}/>
                {
                    user.fetching && coinflip.fetching ? <Loading width="100%"/>
                        :
                        <MainWrapper>
                            <SideBarLeft/>
                            <CoinFlipCenter/>
                            <OnlineChat/>
                        </MainWrapper>
                }
            </div>
        );
    }
}

export default CoinFlipPage
