import React from "react";
import {KingParticipant} from "../KingParticipants";
import defImg from "../../../../assets/img/fox.png";
import {GameBank, GameBankMobile} from "../GameBank";
import {GameTimer} from "../GameTime";
import {useSelector} from "react-redux";
import "./ParticipantsList.css"

function ClassicGame() {
    const {
        status,
        participants,
    } = useSelector(state => state.KingOfTheHill.gameClassic);
    return (
        <div className="game game-classic">
            <ClassicGameLeft/>
            <ClassicGameCenter status={status} participants={participants}/>
            <GameBankMobile/>
            <ClassicGameRight/>
        </div>
    )
}

const ClassicGameLeft = () => {
    const {
        id,
        participants,
        status_id,
    } = useSelector(state => state.KingOfTheHill.gameClassic);
    return (
        <div className="game__left">
            <p className="game__title">Classic #{id}</p>
            <ul className="game__participants game__participants_classic game__participants-wrapper">
                {
                    status_id ?
                        participants.reverse().map((participant, index) => {
                            return <KingParticipant key={index} participant={participant}/>
                        })
                        :
                        ""
                }
            </ul>
        </div>
    )
};

const ClassicGameCenter = () => {
    const {
        participants = [{}],
        status_id,
    } = useSelector(state => state.KingOfTheHill.gameClassic);
    let image, name;
    if(participants.length){
        image = participants[0].account.image;
        name = participants[0].account.name;
    } else {
        image = defImg;
    }
    return (
        <>
            <div className="game__center">
                <div className="game__participants--adaptive">
                    {
                        status_id ?
                            <>
                                {participants.reverse().map((participant, index) => {
                                    return <KingParticipant key={index} participant={participant}/>
                                })}
                            </>
                            :
                            ""
                    }
                </div>
                <div className="game__winner">
                    {
                        status_id && participants.length > 0 ?
                            <img src={image} alt="winner"
                                 className="game__winner-img"/>
                            :
                            <img src={defImg} alt="winner" className="game__winner-img"/>
                    }
                </div>
            </div>
        </>
    )
};

const ClassicGameRight = () => {
    return (
        <div className="game__right">
            <GameBank/>
            <GameTimer/>
        </div>
    )
};
export {
    ClassicGame
}