import React from "react";
import style from "./style/HeroStyle.module.css";

const Hero = ({title, subtitle}) => {
    return (
        <div className={style.wrapper}>
            <h1 className={style.title}>{title}</h1>
            <h3 className={style.subtitle}>{subtitle}</h3>
        </div>
    )
};

export {
    Hero
}