import React from "react";
import Header from "../../Components/Header/Header";
import {MainWrapper} from "../../Wrapper/MainWrapper";
import {SideBarLeft} from "./SideBarLeft";
import {KingOfTheHillCenter} from "./KingOfTheHillCenter";
import OnlineChat from "../../Components/Chat/Chat";
import {HelpBlockMobile} from "../../Components/HelpBlock";
import {BalanceMobile} from "../../Components/Balance";
import {echo} from "../../socket/Socket";
import {Loading} from "../../Components/Loading";


class KingOfTheHillPage extends React.Component {
    componentDidMount() {
        this.props.getKingOfTheHillData();
        echo.channel("king").listen("StartGameKing",e => {
            if(e.endGameAt === -1){
                console.log(e);
                return ""
            }
            if(e.type === 3){
                if(e.endGameAt === 0){
                    this.props.endClassicGame(e);
                } else {
                    this.props.updateClassicGameTimer(e.endGameAt);
                }
            } else {
                if(e.endGameAt === 0){
                    this.props.endSenoyrGame(e);
                } else {
                    this.props.updateSenoyrGameTimer(e.endGameAt);
                }
            }
            console.log(e);
        }).listen("AddParticipantKing", e => {
            if(e.type === 3){
                this.props.addParticipantClassic(e);
            } else {
                this.props.addParticipantSenoyr(e)
            }
            console.log(e);
        }).listen("EndBettingGameKing", e => {
            console.log(e);
        }).listen("EndGameKing", e => {
            if(e.winnerAccountId === this.props.id){
                this.props.updateBalance(e.balance)
            }
            console.log(e);
        })
    }
    componentWillUnmount() {
        echo.leaveChannel("king");
    }

    render() {
        const {user,kingOTH} = this.props;
        if(user.fetching && kingOTH.fetching) {
            return <Loading width="100%"/>
        }
        return (
            <div>
                <Header nowIs={"/king-of-the-hill"}/>
                <MainWrapper>
                    <SideBarLeft/>
                    <KingOfTheHillCenter/>
                    <OnlineChat/>
                </MainWrapper>
                <BalanceMobile/>
                <HelpBlockMobile/>
            </div>
        );
    }
}

export default KingOfTheHillPage