import React, {useState} from "react";
import vkImg from "../../../assets/img/icons-vk-96.png";
import fbImg from "../../../assets/img/icons-facebook-96.png";
import emailImg from "../../../assets/img/icons-email-96.png";
import {useDispatch} from "react-redux";
import {closeModal, openModal} from "../../../Actions/ModalAction";

const AuthModal = () => {
    const [isAccept, setIsAccept] = useState(true);
    const dispatch = useDispatch();


    return (
        <div className="iziModal-content" id="auth">
            <span>Авторизация</span>
            <ul>
                <li>
                    <a href="/oauth/vkontakte">
                        <img src={vkImg} alt="vk"/>
                    </a>
                </li>
                <li style={{display: "none"}}><a href="/oauth/facebook"><img src={fbImg} alt="facebook"/></a></li>
                <li>
                    <a onClick={()=>{
                        dispatch(closeModal);
                        setTimeout(() =>  dispatch(openModal('AuthEmail')), 150)
                    }} className="standart-auth">
                        <img src={emailImg} alt="email"/>
                    </a>
                </li>
            </ul>

            <label htmlFor="checkAuth" style={{textAlign: 'center', display: "block", marginTop:"-10px"}}>
                <input id="checkAuth" type="checkbox" checked={isAccept} onChange={()=>setIsAccept(!isAccept)}/> Я подтверждаю что мне есть 18 лет и я согласен с <a href="/terms-of-use.pdf" target="_blank">условиями сайта</a></label>
        </div>
    )
};


export {
    AuthModal
}