// import {NotificationManager} from "react-notifications";
// import 'react-notifications/lib/notifications.css';
import { store } from 'react-notifications-component';
import 'react-notifications-component/dist/theme.css';

const notification = (text = "",subtitle = "") => {
    store.addNotification({
        title: text,
        message: subtitle,
        type: "info",
        insert: "top",
        container: "top-right",
        animationIn: ["animated", "fadeIn"],
        animationOut: ["animated", "fadeIn"],
        dismiss: {
            duration: 1000,
            onScreen: true,
            pauseOnHover: true
        }
    });
};
const notificationSuccess = (text = "",subtitle = "") => {
    store.addNotification({
        title: text,
        message: subtitle,
        type: "success",
        insert: "top",
        container: "top-right",
        animationIn: ["animated", "fadeIn"],
        animationOut: ["animated", "fadeIn"],
        dismiss: {
            duration: 3000,
            onScreen: true,
            pauseOnHover: true
        }
    });
};

const errorNotification = (text = "",status = "") => {
    store.addNotification({
        title: text,
        message: status,
        type: "danger",
        insert: "top",
        container: "top-right",
        animationIn: ["animated", "fadeIn"],
        animationOut: ["animated", "fadeOut"],
        dismiss: {
            duration: 3000,
            onScreen: true,
            pauseOnHover: true
        }
    });
};

const warningNotification = (text = "", message = "") => {
    store.addNotification({
        title: text,
        message: message,
        type: "warning",
        insert: "top",
        container: "top-right",
        animationIn: ["animated", "fadeIn"],
        animationOut: ["animated", "fadeOut"],
        dismiss: {
            duration: 3000,
            onScreen: true,
            pauseOnHover: true
        }
    });
};

export {
    notification,
    notificationSuccess,
    errorNotification,
    warningNotification
}