import React from "react";
import defImg from "../../assets/img/fox.png";
import {useDispatch, useSelector} from "react-redux";
import {changeChatMode} from "../../Actions/OnlineChatAction";
import {openModal} from "../../Actions/ModalAction";


class OnlineChat extends React.Component {
    state = {
        messages: [{}, {}]
    };

    componentDidMount() {
        if (window.location.pathname.split("/")[1] !== "admin") {
            window.addEventListener("scroll", () => {
                if (window.location.pathname.split("/")[1] === "") {
                    const headerBottom = document.querySelector(".header").getBoundingClientRect().bottom;
                    console.log(headerBottom);
                    const chatBlock = document.querySelector(".chat");
                    if (chatBlock) {
                        if (headerBottom < 0) {
                            chatBlock.classList.add("chat--fixed");
                        } else {
                            chatBlock.classList.remove("chat--fixed");
                        }
                    }
                }

            })
        }
    }

    componentWillUnmount() {
        const chatBlock = document.querySelector(".chat");
        chatBlock.removeEventListener("scroll", () => {
            const headerBottom = document.querySelector(".header").getBoundingClientRect().bottom;
            const chatBlock = document.querySelector(".chat");
            if (headerBottom < 0) {
                chatBlock.classList.add("chat--fixed");
            } else {
                chatBlock.classList.remove("chat--fixed");
            }
        });
    }

    render() {
        const messagesHistory = this.state.messages;
        return (
            <>
                <MainChat messagesHistory={messagesHistory}/>
                <MainChatMobile messagesHistory={messagesHistory}/>
            </>
        );
    }
}

const MainChat = ({messagesHistory}) => {
    const dispatch = useDispatch();
    const isOpen = useSelector(state => state.Chat.isOpen);
    return (
        <div className={`main__right chat ${isOpen ? "" : "chat--close"} `}>
            <div className="chat__top">
                <p className="chat__title">ОНЛАЙН ЧАТ</p>
                <a href="#" data-izimodal-open="#chatRules" onClick={() => dispatch(openModal("ChatHelpModal"))}
                   data-izimodal-transitionin="fadeInDown"
                   className="chat__rules">Правила чата</a>
                <div className={`chat__close ${!isOpen ? "chat__open" : ""}`}
                     onClick={() => dispatch(changeChatMode)}></div>
            </div>
            <BodyChatBlock messagesHistory={messagesHistory}/>
            <BottomChatBlock/>
        </div>
    )
}
const BodyChatBlock = ({messagesHistory = []}) => {
    const isAuth = useSelector(state => state.User.isAuth);
    const role_id = useSelector(state => state.User.role_id);
    const balance = useSelector(state => state.User.balance);

    return (
        <div className="chat__body"
             style={{height: `${isAuth && role_id !== 4 && balance >= 3000 ? "calc(100% - 127px)" : ""}`}}>
            {/*@if(auth()->check() && auth()->user()->id != 4 && getBalance(auth()->user(), 'onlyChat') >= 3000) style="height:calc(100% - 127px)" @endif*/}
            <div className="chat__body-wrap nano">
                <div className="nano-content">
                    {messagesHistory.map((message, index) => {
                        return <Message key={index} message={message}/>
                    })}
                </div>
            </div>
        </div>
    )
};

const BottomChatBlock = () => {
    const isAuth = useSelector(state => state.User.isAuth);
    const role_id = useSelector(state => state.User.role_id);
    const balance = useSelector(state => state.User.balance);
    const dispatch = useDispatch();

    return (
        <div className="chat__bottom">
            {/*@if(auth()->check() && auth()->user()->id != 4 && getBalance(auth()->user(), 'onlyChat') >= 3000)*/}
            {
                isAuth && role_id !== 4 && balance >= 3000 ?
                    <div className="chat__bottom-wrap">
                        <div className="chat-form">
                            <input type="text" className="chat-form__inp"
                                   placeholder="Введите сообщение"
                                // onKeyUp="Message.sendMessage(event)"
                            />
                            <button className="chat-form__btn"
                                // onClick="Message.sendMessage()"
                            >
                                <i className="ic-chat"></i>
                            </button>
                        </div>
                    </div>
                    :
                    <>
                        <div className="chat__bottom-wrap">
                            <p className="chat__bottom-text">Для общения в чате внесите депозит не менее 3000 coins</p>
                            <i className="ic-deposit-warning"></i>
                        </div>
                        <div className="chat__bottom-wrap">
                            {isAuth && role_id !== 4 ?
                                <a className="button chat__bottom-button" data-izimodal-open="#inputModal"
                                   data-izimodal-transitionin="fadeInDown">ПОПОЛНИТЬ</a>
                                :
                                <a className="button chat__bottom-button" data-izimodal-open="#auth"
                                   data-izimodal-transitionin="fadeInDown"
                                   onClick={() => dispatch(openModal("Auth"))}>ПОПОЛНИТЬ</a>

                            }
                        </div>
                    </>

            }
        </div>
    )
};
const Message = ({message = {}}) => {
    const {img = defImg, messageValue = 9, name = "nameless", text = "test text", date = "1 день назад", role_id = 0} = message;
    return (
        <div className="chat__message message">
            <div className="message__top">
                <div className="message__ava">
                    <img src={defImg} alt="avatar"/>
                    <span className="message__value">
                            {messageValue}
                        </span>
                </div>
                <p className={`message__name ${role_id === 4 ? "message__name--icon" : ""}`}>
                    {/*@if($message->account->role_id == 4) message__name--icon @endif*/}
                    <span>{name}</span>
                    <i className={role_id === 4 ? "ic-youtube" : ""}>

                    </i></p>
                <span className="message__time">{date}</span>
            </div>
            <div className="message__body">
                <p className="message__text">{text}</p>
            </div>
        </div>
    )
};
const MainChatMobile = ({messagesHistory}) => {
    const dispatch = useDispatch();
    const isOpen = useSelector(state => state.Chat.isOpen);
    return (
        <div className={`main__right chat mobile ${!isOpen ? "" : "chat--close"}`}>
            <div className="chat__top">
                <p className="chat__title">ОНЛАЙН ЧАТ</p>
                <a href="#" data-izimodal-open="#chatRules" data-izimodal-transitionin="fadeInDown"
                   className="chat__rules">Правила чата</a>
                <div className={`chat__close ${!isOpen ? "" : "chat__open"}`} onClick={() => dispatch(changeChatMode)}>

                </div>
            </div>
            <BodyChatBlockMobile messagesHistory={messagesHistory}/>
            <BottomChatBlock/>
        </div>
    )
};
const BodyChatBlockMobile = ({messagesHistory = []}) => {
    const isAuth = useSelector(state => state.User.isAuth);
    const role_id = useSelector(state => state.User.role_id);
    const balance = useSelector(state => state.User.balance);

    return (
        <div className="chat__body"
             style={{height: `${isAuth && role_id !== 4 && balance >= 3000 ? "calc(100% - 150px)" : ""}`}}>
            <div className="chat__body-wrap nano">
                <div className="nano-content">
                    {messagesHistory.map((message, index) => {
                        return <Message key={index} message={message}/>
                    })}
                </div>
            </div>
        </div>
    )
};
