import React, {useState} from "react";
import {useSelector} from "react-redux";
import {HistoryBalance} from "./HistoryBalance";
import {HistoryGames} from "./HistoryGames";


const HistoryWrap = () => {
    let [nowIs, setNowIs] = useState(1);
    return(
        <div id="history" className="history">
            <div className="history__top">
                <ul className="history__links">
                    <li className={`history__link ${!nowIs?"history__link--active":""}`}
                        onClick={()=>setNowIs(0)}
                    >ИСТОРИЯ БАЛАНСА </li>
                     /
                    <li className={`history__link ${nowIs?"history__link--active":""}`}
                        onClick={()=>setNowIs(1)}> ИСТОРИЯ ИГР</li>
                </ul>
                <a className="history__help hide" data-izimodal-open="#outputRules"
                   data-izimodal-transitionin="fadeInDown"><i className="ic-rules"></i>Правила вывода</a>
            </div>
            {
                !nowIs?
                    <HistoryBalance/>
                    :
                    <HistoryGames/>
            }
        </div>
    )
};

export {
    HistoryWrap
}