import {AXIOS_PARAMS, headers} from "../../Help/AxiosHTTP";
import axios from "axios";
import {errorNotification} from "../../Notifications/Notification";




export const GET_DAILY_GAME = "GET_DAILY_GAME";
export const START_DAILY_GAME = "START_DAILY_GAME";




const getDailyGame = dispatch => {
    axios.get(`${AXIOS_PARAMS.path}api/account/daily-bonus`, {...headers()}).then(res => {
        console.log(res);
        dispatch({
            type: GET_DAILY_GAME,
            data: res.data
        })
    }).catch(res => {
        errorNotification("Произошла непредвиденная ошибка", " ");
    })
};

const startDailyGame = dispatch => {
    dispatch({
        type: START_DAILY_GAME,
        data: {}
    })
    // axios.get(`${AXIOS_PARAMS.path}api/account/daily-bonus-generate`, {...headers()}).then(res => {
    //     console.log(res);
    // })
};






export {
    startDailyGame,
    getDailyGame
}