import React from "react";
import Header from "../../Components/Header/Header";
import {SideBarLeft} from "./SideBarLeft";
import {JackpotCenter} from "./JackpotCenter";
import OnlineChat from "../../Components/Chat/Chat";
import {MainWrapper} from "../../Wrapper/MainWrapper";
import {echo} from "../../socket/Socket";
import {ParserData} from "../../Help/ParserData";
import {AXIOS_PARAMS} from "../../Help/AxiosHTTP";
import axios from "axios";
import {Loading} from "../../Components/Loading";


class JackpotPage extends React.Component {
    constructor(props) {
        super(props);
        this.initChannel = this.initChannel.bind(this);
    }

    componentDidMount() {
        const gameTypeId = this.props.gameTypeId;
        const getJackpotData = this.props.getJackpotData;
        getJackpotData(gameTypeId);
        this.initChannel(gameTypeId);
        //start timer
        // const timer = this.props.timer;
        // const gameRolling = this.props.gameRolling;
        // if (timer === 0) {
        //     gameRolling();
        // } else {
        //     startTimerJackPot(timer, gameRolling);
        // }

    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        echo.leaveChannel("jackpot-classic");
        echo.leaveChannel("jackpot");
        this.initChannel(this.props.gameTypeId);
        // console.log(this.props.Jackpot)
        //
        // const timer = this.props.timer;
        // const gameRolling = this.props.gameRolling;
        // const isGoing = this.props.isGoing;
        //
        // if (!isGoing) {
        //     if (timer === 0) {
        //         gameRolling();
        //     } else {
        //         startTimerJackPot(timer, gameRolling);
        //     }
        // }
    }

    componentWillUnmount() {
        echo.leaveChannel("jackpot");
        echo.leaveChannel("jackpot-classic");
    }

    initChannel(gameTypeId) {
        echo.channel(gameTypeId === 1 ? "jackpot-classic" : "jackpot").listen("AddParticipant", newParticipant => {
            this.props.addParticipants(newParticipant);
            console.log(newParticipant);
        }).listen("StartGame", e => {
            this.props.updateJackpotTimer(e.endGameAt);
        }).listen("EndGameTimer", timerData => {
            if(timerData.timer) {
                this.props.getWinner(timerData);
                if (!this.props.isGoing) {
                    this.props.gameRolling();
                }
                this.props.updateJackpotTimerNewGame(timerData.timer);
            } else {
                console.log(timerData);
                this.props.prepareNewGame(timerData, this.props.id)
            }
        }).listen("EndGame", e => {
        }).listen("CreateGame", e => {
        });
    }

    render() {
        const {user,jackpot} = this.props;
        if(user.fetching && jackpot.fetching) {
            return <Loading width="100%"/>
        }
        return (
            <div>
                <Header nowIs={"/"}/>
                <div className="change-game-wrap-jackpot">
                    <MainWrapper>
                        <SideBarLeft/>
                        <JackpotCenter/>
                        <OnlineChat/>
                    </MainWrapper>
                </div>
            </div>
        )
    }
}


export default JackpotPage