import React, {useEffect, useState} from "react";
import {useDispatch, useSelector} from "react-redux";
import {closeModal, openModal} from "../../../Actions/ModalAction";
import axios from "axios";
import {AXIOS_PARAMS, headers} from "../../../Help/AxiosHTTP";
import {errorNotification, notificationSuccess} from "../../../Notifications/Notification";
import {UPDATE_BALANCE} from "../../../Actions/UserAction";


const handleBet = (cash, gameTypeId, dispatch) => {
    dispatch(closeModal);
    axios.post(`${AXIOS_PARAMS.path}api/account/set-participant-game`, {cash, gameTypeId}, {...headers()}).then(res => {
        console.log(res);
        const {
            error,
            message
        } = res.data;
        if (!error) {
            notificationSuccess(message, `Вы поставили ${cash}`);
            dispatch({
                type: UPDATE_BALANCE,
                balance: res.data.balance
            });
        } else {
            errorNotification(message, " ");
        }
    }).catch(e => {
        errorNotification("Сервер не отвечает", " ")
    })
};


const BetModal = () => {
    const dispatch = useDispatch();
    const balance = useSelector(state => state.User.balance);
    const gameTypeId = useSelector(state => state.Jackpot.gameTypeId);
    const minBet = gameTypeId === 1 ? 300 : 10;
    const maxBet = gameTypeId === 2 ? 300 : 9999999;
    const [valueBet, setValueBet] = useState(minBet);
    const [isDown, setIsDown] = useState(false);
    const [isUp, setIsUp] = useState(false);
    useEffect(() => {
        let timer;
        clearTimeout(timer);
        if (isDown) {
            setIsUp(false);
            timer = setTimeout(() => {
                if(valueBet +1 <=0){
                    setValueBet(0)
                } else {
                    setValueBet(valueBet + 1)
                }
            }, 75);
        } else if (isUp) {
            setIsDown(false);
            timer = setTimeout(() => {
                if(valueBet - 1 <=0){
                    setValueBet(0)
                } else {
                    setValueBet(valueBet - 1)
                }
            }, 75);
        } else {
            clearTimeout(timer);
        }

    }, [valueBet, isDown, isUp]);
    return (
        <div className="modal modal--bet" id="betModal">
            <div className="modal__top">
                <h6 className="modal__title">СДЕЛАТЬ СТАВКУ</h6>
                <a className="modal__close" data-izimodal-close onClick={() => dispatch(closeModal)}>
                    <i className="ic-close">
                    </i>
                </a>
            </div>
            <div className="modal__body">
                <div className="conclusion__balance">
                    <p className="conclusion__balance-text">Текущий баланс:</p>
                    <p className="conclusion__balance-value">{balance} <span>coins</span></p>
                </div>
                <form className="bet__form">
                    <div className="bet__form-wrap">
                        <label className="bet__form-label">COINS</label>
                        <div id="coins" className="bet__form-box">
                            {
                                gameTypeId === 1 ?
                                    <input type="number"
                                        // onChange="MainFunction.calculate(this, event, 'jackpot');"
                                        // onKeyUp="MainFunction.calculate(this, event, 'jackpot');" step="2"
                                           className="bet__form-input cash-coins inputWithoutArrow" min="300"
                                           value={valueBet}
                                           onChange={e => {
                                               let nextValue = Number(e.target.value);
                                               // if (nextValue >= minBet) {
                                               setValueBet(nextValue)
                                               // } else {
                                               //     setValueBet(minBet)
                                               // }
                                           }}
                                    />
                                    :
                                    <input type="number"
                                           className="bet__form-input cash-coins" min="10" max="300" value={valueBet}
                                           onChange={e => {
                                               let nextValue = Number(e.target.value);
                                               if(nextValue <= 0){
                                                   setValueBet(0)
                                               } else {
                                                   setValueBet(nextValue)
                                               }
                                           }}
                                        />
                            }
                            <span className="bet__form-up"
                                  onMouseDown={() => {
                                      setIsDown(true);
                                  }}
                                  onMouseUp={() => {
                                      setIsDown(false)
                                  }}
                                  onMouseLeave={() => {
                                      setIsDown(false)
                                  }}
                            >
                            </span>
                            <span className="bet__form-down"
                                  onMouseDown={() => {
                                      setIsUp(true);
                                  }}
                                  onMouseUp={() => {
                                      setIsUp(false)
                                  }}
                                  onMouseLeave={() => {
                                      setIsUp(false)
                                  }}
                            >
                            </span>
                        </div>
                    </div>
                    <span className="bet__form-sum">=</span>
                    <div className="bet__form-wrap bet__form-wrap--gray">
                        <label className="bet__form-label">руб</label>
                        <div id="coins-result" className="bet__form-box count-money">
                            <input type="number" className="bet__form-input" readOnly value={valueBet / 10}/>
                            <span className="bet__form-up"></span>
                            <span className="bet__form-down"></span>
                        </div>
                    </div>
                </form>
                <div className={`warning__balance ${valueBet < balance ? "hide" : ""}`}>
                    <p className="warning__balance-text">* На вашем счете недостаточно средств для ставки</p>
                </div>
                <div className={`warning__balance ${valueBet >= minBet ? "hide" : ""}`}>
                    <p className="warning__balance-text">* Минимальная ставка {minBet}</p>
                </div>
            </div>
            <div className="modal__buttons">
                <a onClick={() => {
                    handleBet(valueBet, gameTypeId, dispatch);
                }}
                    // onClick="MainFunction.placeBet(this);"
                   className="button button--gradient">СДЕЛАТЬ СТАВКУ</a>
                <a href="#" className="button" data-izimodal-open="#inputModal"
                   onClick={async () => {
                       await dispatch(closeModal);
                       setTimeout(() => {
                           dispatch(openModal("InputPayment"))
                       }, 150)
                   }}
                   data-izimodal-transitionin="fadeInDown">Пополнить</a>
            </div>
        </div>
    )
};


export {
    BetModal
}