import axios from "axios";
import {AXIOS_PARAMS, headers} from "../../Help/AxiosHTTP";
import {errorNotification} from "../../Notifications/Notification";


export const ADMIN_GET_USERS_DATA = "ADMIN_GET_USERS_DATA";
export const CHANGE_SEARCH_USERS = "CHANGE_SEARCH_USERS";


const adminGetUsersData = (pageCount = 1,search = null) => dispatch => {
    axios.get(`${AXIOS_PARAMS.path}api/admin/users?${search?`search=${search}&`:""}page=${pageCount}`, {...headers()}).then(res => {
        dispatch({
            type: ADMIN_GET_USERS_DATA,
            data: res.data
        })
        console.log(res);
    }).catch(res => {
        errorNotification("Произошла непредвиденная ошибка", " ");
    })
};

