import {combineReducers} from "redux";
import {UserReducer} from "./UserReducer";
import {OnlineChatReducer} from "./OnlineChatReducer";
import {ModalReducer} from "./ModalReducer";
import {JackpotReducer} from "./Game/JackpotReducer";
import {CoinFlipReducer} from "./Game/CoinFlipReducer";
import {KingOfTheHillReducer} from "./Game/KingOfTheHillReduce";
import {ProfileReducer} from "./ProfileReducer";
import {BonusDailyGameReducer} from "./Game/BonusDailyGameReducer";
import {CrashReducer} from "./Game/CrashReducer";
import {AdminMainReducer} from "./Admin/AdminMainReducer";
import {AdminCrashReducer} from "./Admin/AdminCrashReducer";
import {AdminWalletReducer} from "./Admin/AdminWalletReducer";
import {AdminOutputReducer} from "./Admin/AdminOutputReducer";
import {AdminGamesReducer} from "./Admin/AdminGamesReducer";
import {AdminUsersReducer} from "./Admin/AdminUsersReducer";
import {AdminPromoCodesReducer} from "./Admin/AdminPromocodesReducer";



export default combineReducers({
    User: UserReducer,
    Chat: OnlineChatReducer,
    Modal: ModalReducer,
    Jackpot: JackpotReducer,
    CoinFlip: CoinFlipReducer,
    KingOfTheHill: KingOfTheHillReducer,
    Profile: ProfileReducer,
    Bonus: BonusDailyGameReducer,
    Crash: CrashReducer,
    AdminMain: AdminMainReducer,
    AdminCrash: AdminCrashReducer,
    AdminWallet: AdminWalletReducer,
    AdminOutput: AdminOutputReducer,
    AdminGames: AdminGamesReducer,
    AdminUsers: AdminUsersReducer,
    AdminPromo: AdminPromoCodesReducer,
})