import React from "react";
import imgVector from "../../../assets/img/Vector.png";
import {useDispatch, useSelector} from "react-redux";
import {closeModal, openModal} from "../../../Actions/ModalAction";
import {parseTime} from "../../../Help/ParserData";

const sha224 = require('js-sha256').sha224;

const CrashHistoryModal = () => {
    let dispatch = useDispatch();
    return (
        <div className="modal modal--help" id="historyCrash">
            <div className="modal__top">
                <h6 className="modal__title"><img src={imgVector}/> История</h6>
                <a href="javascript:void(0);" className="modal__close" onClick={() => dispatch(closeModal)}>
                    <i className="ic-close">

                    </i>
                </a>
            </div>
            <CrashHistoryModalBody/>
        </div>
    )
};


const CrashHistoryModalBody = () => {
    const {games = []} = useSelector(state => state.Crash);
    return (
        <div className="modal__body">
            <div className="modal-help nano">
                <div className="nano-content rules-of-the-game modal-table">
                    {
                        games.length ?
                            <table className="table">
                                <thead>
                                <tr>
                                    <th scope="col">Номер игры</th>
                                    <th>Коэффициент</th>
                                    <th>Дата</th>
                                    <th>Hash</th>
                                </tr>
                                </thead>
                                <tbody>
                                {
                                    games.map(item => {
                                        const {game = {},bets = []} = item;
                                        return <CrashHistoryModalItem game={game} bets={bets}/>
                                    })
                                }
                                </tbody>
                            </table>
                            :
                            <div style={{textAlign: "center"}}>
                                Никто не играл
                            </div>
                    }
                </div>
            </div>
        </div>
    )
};
const CrashHistoryModalItem = ({game = {},bets = []}) => {
    const dispatch = useDispatch();
    const {profit, created_at: date, id} = game;
    const color = profit < 3 ? 1 : profit < 4 ? 2 : profit < 50 ? 3 : profit < 100 ? 4 : 5;
    const round = profit;
    let hash = sha224.create();
    hash = hash.update(round.toString()).hex();
    return (
        <tr>
            <th scope="row" className="link-info-game-modal">
                <a href={`#${id}`} onClick={() => {
                    dispatch(openModal("CrashHistoryGame", {
                        game: {
                            hash,
                            profit,
                            date: parseTime(date),
                            id,
                        },
                        bets: [...bets]
                    }))
                }}>{id}</a>
            </th>
            <td className={`text-color-${color}`} scope="row">{profit}x</td>
            <td scope="row">{parseTime(date)}</td>
            <td scope="row">{hash}</td>
        </tr>
    )

}

export {
    CrashHistoryModal
}