import React from "react";
import {Header} from "../block/Header";
import {SideMenuLeft} from "../../Components/Admin/SideMenuLeft";
import {GamesMain} from "./GamesMain";



class GamesPage extends React.Component{
    componentDidMount() {
        this.props.getData();
    }

    render() {
        return (
            <>
                <Header pageName={'Игры'}/>
                <div className="main">
                    <SideMenuLeft nowIs={'games'}/>
                    <GamesMain/>
                </div>
            </>
        );
    }
}

export default GamesPage