import axios from "axios";
import {AXIOS_PARAMS, headers} from "../../Help/AxiosHTTP";
import {errorNotification} from "../../Notifications/Notification";


export const ADMIN_GET_PROMO_DATA = "ADMIN_GET_PROMO_DATA";
export const CHANGE_SEARCH_PROMO = "CHANGE_SEARCH_PROMO";



const adminGetPromoData = (page = 1,search = null) => dispatch => {
    axios.get(`${AXIOS_PARAMS.path}api/admin/promocodes?${search?`search=${search}&` : ""}page=${page}`, {...headers()}).then(res => {
        console.log(res);
        dispatch({
            type: ADMIN_GET_PROMO_DATA,
            data: res.data
        })
    }).catch(res => {
        errorNotification("Произошла непредвиденная ошибка", " ");
    })
};







export {
    adminGetPromoData
}