import React from "react";
import ReactPaginate from 'react-paginate';

const PaginationComponent = ({setState, state, maxPageCount}) => {
    console.log(state);
    return <ReactPaginate pageCount={maxPageCount}
                          containerClassName={"paginations__list"}
                          pageRangeDisplayed={5}
                          previousLabel={<li className="paginations__item">
                              <a href={`#`} className="paginations__link paginations__link--back"></a>
                          </li>}
                          nextLabel={<li className="paginations__item">
                              <a href={`#`} className="paginations__link paginations__link--next"></a>
                          </li>}
                          breakLinkClassName={"paginations__link"}
                          // breakLabel={<li className="paginations__item" style={{cursor: "default"}}>
                          //     <a href={`#`}
                          //        className="paginations__link" style={{cursor: "default"}}>
                          //         ...
                          //     </a>
                          // </li>}
                          pageClassName={"paginations__item"}
                          pageLinkClassName={"paginations__link"}
                          activeLinkClassName={"paginations__link--active"}
                          onPageChange={(newItem) => {
                              setState(newItem.selected + 1)
                              console.log(newItem);
                          }}
    />

};

const PaginationLeftItem = ({setState, state, maxCount}) => {
    return (
        <>
            {
                state - 1 <= 0 ?
                    <li className="paginations__item" style={{cursor: "default"}}>
                        <a href={`#${state}`} data-page={state}
                           className="paginations__link" style={{cursor: "default"}}>
                        </a>
                    </li>
                    :
                    <li className="paginations__item" onClick={() => setState(state - 1)}>
                        <a href={`#${state}`} data-page={state}
                           className="paginations__link paginations__link--back">
                        </a>
                    </li>
            }
            {
                [...new Array(maxCount > 3 ? 3 : maxCount)].map((item, index) => {
                    if (index === 2 && state >= 7) {
                        return (
                            <li className="paginations__item" style={{cursor: "default"}}>
                                <a href={`#`}
                                   className="paginations__link" style={{cursor: "default"}}>
                                    ...
                                </a>
                            </li>
                        )
                    }
                    return <PaginationComponentItem setState={setState} state={index + 1}
                                                    isActive={index + 1 === state}/>
                })
            }
        </>

    )

};
const PaginationRightItem = ({state, setState, maxCount}) => {
    return (
        <>
            {
                [...new Array(3)].map((item, index) => {
                    if (state < maxCount - 4 && index === 0) {
                        return (
                            <li className="paginations__item" style={{cursor: "default"}}>
                                <a href={`#`}
                                   className="paginations__link" style={{cursor: "default"}}>
                                    ...
                                </a>
                            </li>
                        )
                    }
                    return <PaginationComponentItem state={maxCount - 3 + index} setState={setState}
                                                    isActive={maxCount - 3 + index === state}/>
                })
            }
            {
                state + 2 > maxCount ?
                    <li className="paginations__item" style={{cursor: "default"}}>
                        <a href={`#${state}`} data-page={state}
                           className="paginations__link" style={{cursor: "default"}}>
                        </a>
                    </li>
                    :
                    <li className="paginations__item" onClick={() => setState(state + 1)}>
                        <a href={`#${state}`} data-page={state}
                           className="paginations__link paginations__link--next">
                        </a>
                    </li>
            }
        </>
    )
};

const PaginationCenterItem = ({state, setState, maxCount}) => {
    if (maxCount <= 3) {
        return ""
    }
    return (
        [...new Array(maxCount - 3 > 0 ? maxCount - 3 > 7 ? 7 : 3 : 0)].map((item, index) => {
            if (state < 7) {
                if (index < 5) {
                    return <PaginationComponentItem setState={setState} state={index + 4}
                                                    isActive={index + 4 === state}/>
                } else {
                    return ""
                }
            } else {
                if (maxCount + 3 - index <= state - 2 + index) {
                    return ""
                }

                // if(state - 3 + index > maxCount - 3){
                //     return <PaginationComponentItem state={maxCount - 3 + index} setState={setState} isActive={maxCount - 3 + index === state}/>
                // }
                return <PaginationComponentItem setState={setState} state={state - 3 + index}
                                                isActive={state - 3 + index === state}/>
            }
        })
    )
};


const PaginationComponentItem = ({setState, state, isActive = false}) => {
    return (
        <li className="paginations__item" onClick={() => setState(state)}>
            <a href={`#${state}`} data-page={state}
               className={`paginations__link ${isActive ? "paginations__link--active" : ""}`}>{state}</a>
        </li>
    )
};

export {
    PaginationComponent
}