import {connect} from "react-redux";
import CoinFlipPage from "../CoinflipPage";
import {
    addGameCoinFlip,
    gameEndSocketData,
    gameStartedSocketHandler,
    getCoinFlipPageData
} from "../../../Actions/Game/CoinFlipAction";




const mapStateToProps = state => {
    return {
        userId: state.User.id,
        user: {
            fetching: state.User.fetching,
            fetched: state.User.fetched,
        },
        coinflip: {
            fetching: state.CoinFlip.fetching,
            fetched: state.CoinFlip.fetched,
        }
    }
};

const mapDispatchToProps = dispatch => {
    return {
        getCoinFlipPageData: () => dispatch(getCoinFlipPageData),
        addGameCoinFlip: (data) => dispatch(addGameCoinFlip(data)),
        gameStartedSocketHandler: (data,id) => dispatch(gameStartedSocketHandler(data,id)),
        gameEndSocketData: (data, userId) => dispatch(gameEndSocketData(data,userId))
    }
};


export default connect(mapStateToProps, mapDispatchToProps)(CoinFlipPage);