import axios from "axios";
import {AXIOS_PARAMS, headers} from "../../../Help/AxiosHTTP";
import {errorNotification, notificationSuccess} from "../../../Notifications/Notification";
import {UPDATE_BALANCE} from "../../../Actions/UserAction";

const gameIn = (game_id,dispatch) => {
    axios.post(`${AXIOS_PARAMS.path}api/account/set-participant-coinflip`, {gameId: game_id}, {...headers()}).then(res => {
        const {error, message} = res.data;
        if(Number(error) === 1){
            errorNotification("Ошибка", message);
        } else {
            const {id} = res.data.data.game;
            notificationSuccess("Вы были добавлены к игре", `Игра #${id} скоро начнется`)
            dispatch({
                type: UPDATE_BALANCE,
                balance: res.data.balanceUser
            });
        }
    })

};

export {
    gameIn
}
