import {ADMIN_GET_USERS_DATA, CHANGE_SEARCH_USERS} from "../../Actions/AdminActions/AdminUsersAction";

const AdminUsersReducer = (state = {
    search: null,
    users: {
        current_page: 1,
        data: [],
        from: 0,
        last_page: 1000,
        per_page: 12,
        to: 12,
        total: 1000,
    },
}, action) => {
    switch (action.type) {
        case CHANGE_SEARCH_USERS:
            return {
                ...state,
                search: action.value
            };
        case ADMIN_GET_USERS_DATA:
            return {
                ...state,
                ...action.data
            };
        default:
            return state;
    }
};


export {
    AdminUsersReducer
}