import defImg from "../assets/img/fox.png";
import {
    CHANGE_SESSION_THEME, FETCHED,
    GET_LOGIN,
    LIKE_ADMIN,
    LIKE_GUEST,
    LIKE_USER, LOGOUT,
    SET_MAIN_STYLE, START_FETCHING, UPDATE_BALANCE
} from "../Actions/UserAction";

const UserReducer = (state = {
    isAuth: false,
    role_id: 0,
    id: 0,
    sessionTheme: true,
    balance: 0,
    fetching: true,
    fetched: false,
    status: ""

}, action) => {
    switch (action.type) {

        //test action handle for role
        case LIKE_GUEST: return {
            ...state,
            role_id: 0,
            isAuth: false,
        };
        case LIKE_USER: return {
            ...state,
            isAuth: true,
            role_id: 1
        };
        case LIKE_ADMIN: return {
            ...state,
            isAuth: true,
            role_id: 2
        };
        case SET_MAIN_STYLE: return {
            ...state,
            mainStyleHref: action.href
        };
        //end
        case GET_LOGIN: return  {
            ...state,
            ...action.data
        };

        case LOGOUT: return {
            isAuth: false,
            role_id: 0,
            id: 0,
            sessionTheme: state.sessionTheme
        };
        case CHANGE_SESSION_THEME: return {
            ...state,
            sessionTheme: action.theme
        };

        //fetch handler
        case START_FETCHING: return {
            ...state,
            status: "",
            fetched: false,
            fetching: true,
        };
        case FETCHED: return {
            ...state,
            status: action.status,
            fetched: true,
            fetching: false
        };
        case UPDATE_BALANCE: return {
            ...state,
            balance: action.balance
        };
        default: return state
    }
};


export {
    UserReducer
}

