import {ADMIN_GET_WALLET_DATA, CHANGE_SEARCH_WALLET} from "../../Actions/AdminActions/AdminWalletAction";

const AdminWalletReducer = (state = {
    search: null,
    payments: {
        current_page: 1,
        data: [],
        from: 0,
        last_page: 1000,
        per_page: 12,
        to: 12,
        total: 1000,
    },
}, action) => {
    switch (action.type) {
        case CHANGE_SEARCH_WALLET:
            return  {
                ...state,
                search: action.value
            }
        case ADMIN_GET_WALLET_DATA:
            return  {
                ...state,
                ...action.data
            };
        default:
            return state;
    }
};


export {
    AdminWalletReducer
}