import {AXIOS_PARAMS, headers} from "../../Help/AxiosHTTP";
import axios from "axios";
import {errorNotification} from "../../Notifications/Notification";

export const GET_KING_OF_THE_HILL_DATA = "GET_KING_OF_THE_HILL_DATA";
export const GAME_CLASSIC_START = "GAME_CLASSIC_START";
export const GAME_CLASSIC_END = "GAME_CLASSIC_END";
export const GAME_SENOYR_START = "GAME_SENOYR_START";
export const GAME_SENOYR_END = "GAME_SENOYR_END";

export const ADD_PARTICIPANT_CLASSIC = "ADD_PARTICIPANT_CLASSIC";
export const ADD_PARTICIPANT_SENOYR = "ADD_PARTICIPANT_SENOYR";
export const UPDATE_TIMER_CLASSIC = "UPDATE_TIMER_CLASSIC";
export const UPDATE_TIMER_SENOYR = "UPDATE_TIMER_SENOYR";

export const NEW_GAME_SENOYR = "NEW_GAME_SENOYR";
export const NEW_GAME_CLASSIC = "NEW_GAME_CLASSIC";


export const UPDATA_GAMES = "UPDATA_GAMES";
//fetch action
export const START_FETCHING = "START_FETCHING";
export const FETCHED = "FETCHED";


const getKingOfTheHillData = dispatch => {
    dispatch({
        type: START_FETCHING
    });
    axios.get(`${AXIOS_PARAMS.path}api/king-of-the-hill`, {...headers()}).then(res => {
        dispatch({
            type: GET_KING_OF_THE_HILL_DATA,
            data: res.data
        });
        dispatch({
            type: FETCHED,
            status: res.status
        })
    }).catch(e => {
        dispatch({
            type: FETCHED,
            status: e.status
        });
        errorNotification("Service no response", " ");
    })
};

const addParticipantClassic = (data) => dispatch =>  {
    const {bank, participant} = data;
    dispatch({
        type: ADD_PARTICIPANT_CLASSIC,
        bank,
        participant
    })
};
const updateClassicGameTimer = (newTime) => dispatch => {
    console.log(newTime);
    dispatch({
        type: UPDATE_TIMER_CLASSIC,
        newTime
    })
};

const addParticipantSenoyr= (data) => dispatch =>  {
    const {bank, participant} = data;
    dispatch({
        type: ADD_PARTICIPANT_SENOYR,
        bank,
        participant
    })
};
const updateSenoyrGameTimer = (newTime) => dispatch => {
    dispatch({
        type: UPDATE_TIMER_SENOYR,
        newTime
    })
};
