const startClassicGame = dispatch => {
    dispatch({
        type: GAME_CLASSIC_START
    })
};
const endClassicGame = data => dispatch => {
    const {game} = data;
    const winner = game.participants[0];
    const winnerData = {
        ...game,
        participants: "",
        winner,
    };
    dispatch({
        type: GAME_CLASSIC_END,
        winnerData
    });
    dispatch({
        type: UPDATA_GAMES,
        game: {
            ...winnerData,
            winner: {
                ...winnerData.winner,
                account: "",
                ...winnerData.winner.account
            }
        }
    });
    axios.get(`${AXIOS_PARAMS.path}api/account/create-classic`, {...headers()}).then(res => {
        dispatch({
            type: NEW_GAME_CLASSIC,
            game: res.data
        })
    })
};


const startSenoyrGame = dispatch => {
    dispatch({
        type: GAME_SENOYR_START
    })
};
const endSenoyrGame = data => dispatch => {
    const {game} = data;
    const winner = game.participants[0];
    const winnerData = {
        ...game,
        participants: "",
        winner,
    };
    dispatch({
        type: GAME_SENOYR_END,
        winnerData
    });
    dispatch({
        type: UPDATA_GAMES,
        game: {
            ...winnerData,
            winner: {
                ...winnerData.winner,
                account: "",
                ...winnerData.winner.account
            }
        }
    });
    axios.get(`${AXIOS_PARAMS.path}api/account/create-senior`, {...headers()}).then(res => {
        dispatch({
            type: NEW_GAME_SENOYR,
            game: res.data
        })
    })
};

export {
    getKingOfTheHillData,
    startClassicGame,
    endClassicGame,
    startSenoyrGame,
    endSenoyrGame,
    addParticipantClassic,
    updateClassicGameTimer,
    addParticipantSenoyr,
    updateSenoyrGameTimer
}