import React from "react";
import defImg from "../../../../assets/img/fox.png"
import {useSelector} from "react-redux";
import {GameTypeId} from "../../help/GameTypeId";

function ParticipantsWrapper() {
    return (
        <div className="wrap-first-step ">
            <div
                className="winners">
                <a href="#" className="winners__game">
                    {/*{{$gameType->name}} @if($game) #{{$game->id}} @endif*/}
                    <GameTypeId/>
                </a>
                <h5 className="winners__title">Игроки</h5>
                <div className="winners__slider">
                    <Participants/>
                </div>
            </div>
        </div>
    )
};

const Participants = () => {
    const {participants} = useSelector(state => state.Jackpot.game);

    if (participants.length >= 1) {
        return (
            <>
                {
                    participants.map((player, index)=> {
                        return <ParticipantsWinner key={index} player={player}/>
                    })
                }
            </>
        )
    } else {
        return (
            <>
                <div className="winners__slider-item winners__slider-item--">
                    <img src={defImg} alt="avatar" className="winners__slider-avatar"/>
                    <span className="winners__slider-percent"></span>
                </div>
                <div className="winners__slider-item winners__slider-item--">
                    <img src={defImg} alt="avatar" className="winners__slider-avatar"/>
                    <span className="winners__slider-percent"></span>
                </div>
            </>
        )
    }
}
const ParticipantsWinner = ({player = {}}) => {
    const {image = defImg, color = "#333", cash = 3000} = player;
    const bank = Number(useSelector(state => state.Jackpot.game.bank));
    return (
        <div className="winners__slider-item winners__slider-item--"
             style={{borderBottom: `3px solid ${color}`}}>
            <img src={image} alt="avatar" className="winners__slider-avatar"/>
            <span className="winners__slider-percent">{Math.round(cash * 100 / bank)} %</span>
        </div>
    )
}

export {
    ParticipantsWrapper
}