import React from "react";
import {MainWrapper} from "../../Wrapper/MainWrapper";
import {HelpBlockMobile} from "../../Components/HelpBlock";
import OnlineChat from "../../Components/Chat/Chat";
import Header from "../../Components/Header/Header";
import {SideBarLeft} from "./SideBarLeft";
import {CrashCenter} from "./CrashCenter";
import {CrashHistoryModal} from "./Modal/CrashHistoryModal";
import Modal from "react-awesome-modal";
import {connect} from "react-redux";
import {closeModal} from "../../Actions/ModalAction";
import {
    getGameDataCrash,
    setNewBet,
    setNewCoef,
    setNewGame,
    ticTimer,
    updateUbets
} from "../../Actions/Game/CrashAction";
import {echo} from "../../socket/Socket";
import {Loading} from "../../Components/Loading";
import {updateBalanceHardly} from "../../Actions/UserAction";

const mapStateToProps = state => {
    return {
        id: state.User.id,
        user: {
            fetching: state.User.fetching,
            fetched: state.User.fetched,
        },
        crash: {
            fetching: state.Crash.fetching,
            fetched: state.Crash.fetched,
        }
    }
};
const mapDispatchToProps = dispatch => {
    return {
        getGameDataCrash: () => dispatch(getGameDataCrash),
        setNewCoef: newCoef => dispatch(setNewCoef(newCoef)),
        ticTimer: timer => dispatch(ticTimer(timer)),
        setNewGame: newGame => dispatch(setNewGame(newGame)),
        setNewBet: res => dispatch(setNewBet(res)),
        updateUbets: (bet) => dispatch(updateUbets(bet)),
        updateBalanceHardly: () => dispatch(updateBalanceHardly)
    }
};


class CrashPage extends React.Component{
    componentDidMount() {
        this.props.getGameDataCrash();
        echo.channel("crash-coef").listen("CrashCoef", res => {
            console.log(res);
        });
        echo.channel("crash").listen("CreateGameCrash", res => {
            this.props.updateBalanceHardly();
            this.props.setNewGame(res)
            console.log(res);
        });
        echo.channel("bet").listen("JoinCrash", res => {
            console.log(res);
            this.props.setNewBet(res);
            console.log("BEEEEEEEEEEEEEEEEEEEEEEEEEEETS")
            // crash_game_id: 71329
            // created_at: "2020-06-30 22:54:23"
            // id: 1104
            // number: "1.22"
            // price: 2
            // updated_at: "2020-06-30 22:54:30"
            // user_id: 872
        });
        echo.channel("crash-stop").listen("StopCrash", res => {
            console.log(res);
        });
        echo.channel("flash-out").listen("FlashOutCrash", res => {
            this.props.updateUbets(res.bet);
            console.log(res);
        });
        echo.channel("bet-adm").listen("AdmCrash", res => {
            console.log(res);
        });
        echo.channel("crash-timer").listen("CrashTimer", res => {
            console.log(res);
            console.log("DEFAULT TIMER")
            this.props.setNewCoef(res, true);
        });
        echo.channel("end-crash-timer").listen("EndGameTimerCrash", res => {
            console.log(res);
            console.log("PREPARE TIMER")
            this.props.ticTimer(10 - res.timer);
        })
    }
    componentWillUnmount() {
        echo.leaveChannel("crash-coef");
        echo.leaveChannel("crash");
        echo.leaveChannel("bet");
        echo.leaveChannel("bet-adm");
        echo.leaveChannel("crash-timer");
        echo.leaveChannel("end-crash-timer")
    }

    render() {
        const {user,crash} = this.props;
        if(user.fetching && crash.fetching) {
            return <Loading width="100%"/>
        }
        return (
            <div>
                <Header nowIs={"/crash"}/>
                <MainWrapper>
                    <SideBarLeft/>
                    <CrashCenter/>
                    <OnlineChat/>
                </MainWrapper>
            </div>
        );
    }
}


export default connect(mapStateToProps,mapDispatchToProps)(CrashPage)