import {
    BETS_TIME, CHECK_AM_I_BET,
    CHECK_ONLINE, FETCHED,
    GET_DATA_ABOUT_GAME, MERGE_GAME,
    RELOAD, SET_NEW_BET,
    SET_NEW_COEF, SET_NEW_GAME, START_FETCHING,
    START_TIMER, UPDATE_UBETS
} from "../../Actions/Game/CrashAction";
import {START_GAME} from "../../Actions/Game/CoinFlipAction";
import {findUserBet, updateUbets} from "../Help/help";

const CrashReducer = (state = {
    isGoing: false,
    isIBet: false,
    isICanCashOut: false,
    isReload: false,
    prepareStart: true,
    timer: 10,
    bets:[],
    game: {
        create_game: 0,
        created_at: "",
        id: 0,
        number: 0,
        price: 0,
        profit: 0,
        rand_number: "",
        status: 0,
        stop_game: 0,
        updated_at: "",
        endGameAt: 0,
        coef: 0,
        isOnline: false,
    },
    games: [],
    ubets: [],
    newGame: {},
    fetching: true,
    fetched: false,
},action) => {
    switch (action.type) {

        case START_FETCHING: return  {
            ...state,
          fetching: true,
          fetched: false,
        };
        case FETCHED: return {
            ...state,
            fetching: false,
            fetched: true
        }

        case GET_DATA_ABOUT_GAME: return  {
            ...state,
            ...action.data,
            game: {
                ...state.game,
                ...action.data.game
            },
            // myBet:
        };
        case RELOAD: return {
            ...state,
            isReload: !state.isReload
        };
        case UPDATE_UBETS: return {
            ...state,
            ubets: [...updateUbets(state.ubets, action.bet)]
        };
        case BETS_TIME: return  {
            ...state,
            isGoing: false,
            prepareStart: true
        };
        case START_GAME: return {
            ...state,
            isGoing: true,
            prepareStart: false,
        };
        case START_TIMER: return {
            ...state,
            timer: action.time
        };
        case MERGE_GAME: return {
            ...state,
            game: {
                ...state.newGame,
            },
            newGame: {}
        };
        case SET_NEW_GAME: return {
            ...state,
            games: [{game:{...state.game}, bets: [...state.bets]}, ...state.games],
            newGame: {
                ...state.game,
                coef: 0,
                ...action.game
            },
            bets:[],
            ubets:[...action.ubets],
        };
        case SET_NEW_COEF: return {
            ...state,
            isGoing: true,
            prepareStart: false,
            game: {
                ...state.game,
                ...action.newCoef,
                profit: action.newCoef.endTimer ?Math.pow(1.06,action.newCoef.endTimer - 17): state.game.profit,
            }
        };
        case CHECK_AM_I_BET: return  {
            ...state,
            ...findUserBet(state.ubets,action.userID)
        };
        case CHECK_ONLINE: return  {
            ...state,
            isOnline: action.online
        };
        case SET_NEW_BET: return {
            ...state,
            ubets: [action.data,...state.ubets]
        };
        default: return state;
    }
};




export {
    CrashReducer
}