import React from "react";
import {SideMenuLeft} from "../../Components/Admin/SideMenuLeft";
import {Header} from "../block/Header";
import {HomeMain} from "./HomeMain";



class HomePage extends React.Component {
    componentDidMount() {
        this.props.getData();
    }

    render() {
        return (
            <>
                <Header pageName={'Статистика'}/>
                <div className="main">
                    <SideMenuLeft nowIs={'/admin'}/>
                    <HomeMain/>
                </div>
            </>
        );
    }
}

export default HomePage