import {
    FINISH_GAME,
    GET_GAME_DATA,
    START_GAME,
    FETCHED,
    START_FETCHING,
    GET_COIN_FLIP_PAGE_DATA,
    INIT_NEW_GAME,
    UPDATE_USER_GAMES,
    NEW_STARTED_GAME,
    CLEAR_GAME,
    GAME_END_SOCKET, WIN_CASH, UPDATE_USER_BANK
} from "../../Actions/Game/CoinFlipAction";
import {
    filterCoinFlipActiveGameWithoutCurrent,
    filterCoinFlipGameWithoutCurrentID,
    filterCoinFlipHistoryGame
} from "../Help/FilterCoinFlipGame";



const CoinFlipReducer = ( state = {
    activeGames: [],
    animationGames: [],
    bank: 0,
    bankUser: 0,
    games: [],
    gamesToday: 0,
    userGames: 0,
    userGamesNow: 0,
    gameReady: false,
    gameEnd: false

},action) => {
    switch (action.type) {

        case GET_COIN_FLIP_PAGE_DATA: return {
            ...state,
            ...action.data,
        };
        case GET_GAME_DATA: return {
            ...state,
            ...action.data
        };
        case START_GAME: return {
            ...state,
            gameReady: true,
        };
        case FINISH_GAME: return {
            ...state,
            gameEnd: true
        };
        case CLEAR_GAME: return {
            ...state,
            gameEnd: false,
            gameReady: false
        };

        //fetch handler
        case START_FETCHING: return  {
            ...state,
            status: "",
            fetching: true,
            fetched: false
        };
        case FETCHED: return  {
            ...state,
            status: action.status,
            fetching: false,
            fetched: true
        };
        //init new game
        case INIT_NEW_GAME: return  {
            ...state,
            bank: action.data.allCash,
            activeGames: [action.data.game, ...state.activeGames],
        };
        case UPDATE_USER_GAMES: return  {
            ...state,
            userGames: action.userGames
        };
        case UPDATE_USER_BANK: return {
            ...state,
            bankUser: action.bankUser
        };
        //start game action
        case NEW_STARTED_GAME: return {
            ...state,
            animationGames: filterCoinFlipActiveGameWithoutCurrent(state.animationGames, action.id)?state.animationGames : [action.game,... state.animationGames],
            activeGames: filterCoinFlipGameWithoutCurrentID(state.activeGames, action.id),
        };

        case GAME_END_SOCKET: return {
            ...state,
            animationGames: filterCoinFlipGameWithoutCurrentID(state.animationGames, action.id),
            games: filterCoinFlipHistoryGame(state.games, action.game)
        };
        case WIN_CASH: return {
            ...state,
            bankUser: state.bankUser + action.cash
        };
        default: return state
    }
};
export {
    CoinFlipReducer
}

