import React from "react";
import defImg from "../../../../assets/img/fox.png";
import {useSelector} from "react-redux";
import {ParserData} from "../../../../Help/ParserData";


const BiggestWinner = () => {
    const luckyImg = defImg;
    const {
        image = defImg,
        bank,
        color,
        name,
        percent,
        winnerTicket
    } = useSelector(state => state.Jackpot.biggestBank);
    return (
        <>
            <div className="jackpot-timer__wrap stats">
                <p className="history__link history__link--active">НАИБОЛЬШИЙ ВЫИГРЫШ</p>

            </div>
            <li className="jackpot-history__item history-item history-item--"
                style={{borderBottom: `2px solid ${color}`}}>
                <div className="history-item__left">
                    <img src={image} alt="avatar" className="history-item__avatar"/>
                    <div className="history-item__wrap">
                        <p className="history-item__name">
                            {name}
                            {/*{{$biggestBank['name']}}*/}
                        </p>
                    </div>
                </div>
                <div className="history-item__right">
                    <p className="history-item__value"><span
                        style={{color: "#a5a5a5"}}>Выигрыш: {ParserData(bank)}</span>
                        {/*{{$biggestBank['bank']}}*/}
                        <span> COINS</span>
                    </p>
                    <p className="history-item__range"><span
                        style={{color: "#a5a5a5"}}>Шанс: {percent}</span>
                        {/*{{$biggestBank['percent']}} */}
                        %</p>
                </div>
            </li>
        </>
    )
};


export {
    BiggestWinner
}