import React, {useState} from "react";
import {useDispatch, useSelector} from "react-redux";
import {closeModal} from "../../Actions/ModalAction";
import defImg from "../../assets/img/fox.png";
import {AXIOS_PARAMS, headers} from "../../Help/AxiosHTTP";
import {notificationSuccess} from "../../Notifications/Notification";
import axios from "axios";

const setDataForm = (setter, name, value) => {
    setter(prevState => {
        return {
            ...prevState,
            [name]: value
        }
    })
}
const OutputInfoModal = () => {
    const dispatch = useDispatch();
    const {
        user,
        application,
        total_price_input,
        total_price_output,
        total_price_referal,
        payments = [],
    } = useSelector(state => state.Modal.props);
    const {
        balance,
        image = defImg,
        vkontakte_id,
        name,
        last_name,
        role_id,
        email,
        is_blocked,
        is_referral_power,
        facebook_id
    } = user;
    const {
        status_id,
        id,
        comment,
        account_id,
        phone,
        price,
        payment_system_id,
    } = application;

    const [formData, setFormData] = useState({
        status_id,
        id: account_id,
        applicationId: id
    });
    return (
        <div className="modal" style={{
            visibility: "visible",
            opacity: 1,
            transition: "all 0.5s ease 0s",
            margin: 0,
            minWidth: "900px",
            width: "100%"
        }}>
            <div className="modal__top">
				<span className="modal__icon">
                    <img style={{width: "100%", borderRadius: "50%"}} src={image}
                         alt="avatar"/>
				</span>
                <p className="modal__name"><a target="_blank" href={`https://vk.com/id${vkontakte_id}`}>
                    {name} {last_name}</a></p>
                <div className="modal__close">
                    <a className="modal__close-link" onClick={() => dispatch(closeModal)}>
                    </a>
                </div>
            </div>
            <div className="output-info">
                <div className="output-info__top">
                    <div className="output-info__box">
                        <p className="output-info__title">Тип пользователя</p>
                        <span
                            className="output-info__value">{role_id === 4 ? "Youtuber" : role_id === 2 ? "Admin" : "Пользователь"}</span>
                    </div>
                    <div className="output-info__box">
                        <p className="output-info__title">Реф. Отчисления</p>
                        <span className="output-info__value">{is_referral_power ? "Да" : "Нет"}</span>
                    </div>
                    <div className="output-info__box">
                        <p className="output-info__title">Заблокирован</p>
                        <span className="output-info__value">{is_blocked ? "Да" : "Нет"}</span>
                    </div>
                    <div className="output-info__box">
                        <p className="output-info__title">Система</p>
                        <span className="output-info__value">{payment_system_id}</span>
                    </div>
                    <div className="output-info__box">
                        <p className="output-info__title">Реквизиты</p>
                        <span className="output-info__value">{phone}</span>
                    </div>
                </div>
                <div className="output-info__bottom">
                    <div className="output-info__box">
                        <p className="output-info__title">Баланс, coins:</p>
                        <span className="output-info__value">{balance / 10}</span>
                    </div>
                    <div className="output-info__box">
                        <p className="output-info__title">Баланс, руб:</p>
                        <span className="output-info__value">{balance}</span>
                    </div>
                    <div className="output-info__box">
                        <p className="output-info__title">Сумма к выводу</p>
                        <input type="text" name="" readOnly="" className="output-info__field" value={price}/>
                    </div>
                    <div className="output-info__box">
                        <p className="output-info__title">Статус вывода</p>
                        <div className="selectric-wrapper">
                            <StatusCell status={formData.status_id} setter={setFormData}/>
                        </div>
                    </div>
                </div>
            </div>


            <div className="modal__statistics">
                <p className="modal__statistics-title">Сводная статистика</p>
                <table className="modal-table">
                    <thead>
                    <tr>
                        <th>Общая сумма вывода</th>
                        <th>Общая сумма пополнений</th>
                        <th>Общая сумма реф. начислений</th>
                    </tr>
                    </thead>
                    <tbody>
                    <tr>
                        <td>{total_price_output}</td>
                        <td>{total_price_input}</td>
                        <td>{total_price_referal}</td>
                    </tr>
                    </tbody>
                </table>
            </div>
            <div className="wrap-paginate-user">
                <div className="modal__history">
                    <p className="modal__history-title">История пополнений/вывода</p>
                    <table className="modal-table">
                        <thead>
                        <tr>
                            <th>Сумма</th>
                            <th>Назначение</th>
                            <th>Дата</th>
                        </tr>
                        </thead>
                    </table>
                    <div className="nano modal-table__wrap">
                        <div className="nano-content">
                            <table className="modal-table modal-table--spacing">
                                <tbody>
                                {payments.map(item => {
                                    return (
                                        <tr className={item.payment_type_id===1?"modal-table--green":"modal-table--red"}>
                                            <td>{item.price}</td>
                                            <td>{
                                                item.payment_type_id === 1 ?
                                                    "Пополнение"
                                                    :
                                                    "Вывод"
                                            }</td>
                                            <td>{item.created_at.split(" ")[0]}</td>
                                        </tr>
                                    )
                                })}

                                </tbody>
                            </table>
                        </div>
                    </div>

                </div>
                <div className="modal__bottom">
                    <div></div>
                    {/*// <!-- Start paginations -->*/}
                    <a className="modal__save" onClick={() => {
                        axios.post(`${AXIOS_PARAMS.path}api/admin/save-application-info`, {
                            ...formData,
                            status_id: Number(formData.status_id)
                        }, {...headers()}).then(res => {
                            dispatch(closeModal);
                            notificationSuccess("Данные вывода успешно измененны", " ");
                        })
                    }}>Сохранить</a>
                </div>
            </div>


            <input type="hidden" name="id" value="71"/>
            <input type="hidden" name="applicationId" value="1"/>
        </div>
    )
};
const StatusCell = ({status, setter}) => {
    if (status === 4) {
        return <div className="selectric">Готово</div>
    }
    return (
        <div className="selectric">

            <select className="label" name="status_id" style={{width: '100%', borderRadius: "50px"}} onChange={(e) => {
                setDataForm(setter, "status_id", e.target.value)
            }}>
                <option value="2" selected={status === 2} className="bgn-text bgn-text--orange">Ожидает</option>
                <option value="1" selected={status === 1} className="bgn-text bgn-text--blue">К выводу</option>
                <option value="5" selected={status === 5} className="bgn-text bgn-text--green">Согласовано</option>
                <option value="6" selected={status === 6} className="bgn-text bgn-text--gray">Перевод</option>
                <option value="7" selected={status === 7} className="bgn-text bgn-text--darkblue">Выплачено</option>
                <option value="3" selected={status === 3} className="bgn-text bgn-text--red">Отказано</option>
                <option value="8" selected={status === 8} className="bgn-text bgn-text--purple">Заморожено</option>
                <option value="9" selected={status === 9} className="bgn-text bgn-text--yellow">Сбой</option>
            </select>

        </div>
    )
};


export {
    OutputInfoModal
}