import {useSelector} from "react-redux";
import React from "react";
import {parseTime} from "../../../../Help/ParserData";

const HistoryBalance = () => {
    return(
        <div id="historyBodyOne" className="history__body history__body--active nano">
            <HistoryBalanceList/>
        </div>
    )
};
const HistoryBalanceList = ({offers = [{payment_id: 1},{payment_id: 2},{payment_id: 3}]}) => {
    const historyBalance = useSelector(state => state.Profile.getHistoryBalance);
    return(
        <ul className="history__list nano-content">
            {historyBalance.map((offer,index) => {
                return <HistoryBalanceListItem key={index} offer={offer}/>
            })}
        </ul>
    )
};
const HistoryBalanceListItem = ({offer}) => {
    let {payment_type_id = 1} = offer;
    switch (payment_type_id) {
        case 7:{
            const {price = 999, game_id, created_at } = offer;
            return (
                <li className="history__item hist-balance hist-balance--green">
                    <div className="hist-balance__left">
                        <p className="hist-balance__sum"><i className="ic-money-green"></i>{price} Руб.</p>
                    </div>
                    <div className="hist-balance__right">
                        <p className="hist-balance__source">{gameId(game_id)}</p>

                        <p className="hist-balance__data">{parseTime(created_at)}</p>
                    </div>
                </li>
            );
        }
        case 6:{
            let {price = 999, game_id, created_at} = offer;
            return (
                <li className="history__item hist-balance hist-balance--red">
                    <div className="hist-balance__left">
                        <p className="hist-balance__sum"><i className="ic-money-red"></i>{price} Руб.</p>
                    </div>
                    <div className="hist-balance__right">
                        <p className="hist-balance__source">{gameId(game_id)}</p>
                        <p className="hist-balance__data">{parseTime(created_at)}</p>
                    </div>
                </li>
            );
        }
        default:{
            let {price = 999, name, created_at} = offer;
            return (
                <li className="history__item hist-balance hist-balance--yellow">
                    <div className="hist-balance__left">
                        <p className="hist-balance__sum">
                            <i className="ic-money-yellow">
                            </i>{price} Руб.
                        </p>
                    </div>
                    <div className="hist-balance__right">
                        <p className="hist-balance__source--user">
                            <i className="ic-user-black"></i> Игрок <span>{name}</span> пополнил
                            баланс
                        </p>
                        <p className="hist-balance__data">{parseTime(created_at)}</p>
                    </div>
                </li>
            )
        }
    }
};

// 1	Daily bonus
// 2	KING OF THE HILL
// 3	Jackpot
// 4	Coinflip

function gameId(id) {
    switch (id) {
        case 1: return "Daily bonus";
        case 2: return "KING OF THE HILL";
        case 3: return "Jackpot";
        default: return "Coinflip";
    }
}



export {
    HistoryBalance
}