import React, {useState} from "react";
import {useDispatch, useSelector} from "react-redux";
import {closeModal, openModal} from "../../../Actions/ModalAction";
import axios from 'axios';
import {AXIOS_PARAMS, headers} from "../../../Help/AxiosHTTP";
import {errorNotification, notificationSuccess} from "../../../Notifications/Notification";
import {UPDATE_BALANCE} from "../../../Actions/UserAction";
import {BetForm} from "../../JackpotPage/Modal/UI/BetForm";

const makeBet = (gameType, cash, dispatch) => {
    axios.post(`${AXIOS_PARAMS.path}api/account/set-participants-king`, {
        type: gameType,
        cash
    }, {...headers()}).then(res => {
        if (res.data.error) {
            errorNotification("Ошибка", res.data.message)
        } else {
            notificationSuccess(res.data.message, `Потенциальный банк ${res.data.bank}`)
            dispatch({
                type: UPDATE_BALANCE,
                balance: res.data.balance
            })
        }
    })
};


const BetKingOfTheHill = () => {
    const balance = useSelector(state => state.User.balance);
    const {lastBet, typeGame = "", readOnly = false} = useSelector(state => state.Modal.props);
    const [valueBet, setValueBet] = useState(lastBet);
    const dispatch = useDispatch();

    return (
        <div className="modal modal--bet" id="betModal">
            <div className="modal__top">
                <h6 className="modal__title">СДЕЛАТЬ СТАВКУ</h6>
                <a className="modal__close"
                   onClick={() => dispatch(closeModal)}
                   data-izimodal-close><i className="ic-close"></i></a>
            </div>
            <div className="modal__body">
                <div className="conclusion__balance">
                    <p className="conclusion__balance-text">Текущий баланс:</p>
                    <p className="conclusion__balance-value">{balance} <span>coins</span></p>
                </div>
                <BetForm setValueBet={setValueBet} valueBet={valueBet} readOnly={readOnly}/>
                <div className={`warning__balance ${valueBet < balance? "hide" : ""}`}>
                    <p className="warning__balance-text">* На вашем счете недостаточно средств для ставки</p>
                </div>
                <div className="modal__buttons">
                    <a onClick={() => {
                           makeBet(typeGame, valueBet, dispatch);
                           dispatch(closeModal)
                       }}
                        // onClick="MainFunction.createGameKing('senyor');"
                       className="button button--gradient">СДЕЛАТЬ
                        СТАВКУ</a>
                    <a href="#"
                       className="button"
                       data-izimodal-open="#inputModal"
                       data-izimodal-transitionin="fadeInDown"
                       onClick={() => {
                           dispatch(closeModal);
                           setTimeout(() => dispatch(openModal("InputPayment")), 150)
                       }}
                    >Пополнить</a>
                </div>
            </div>
        </div>
    )
};

export {
    BetKingOfTheHill
}