import React from "react";
import {useSelector} from "react-redux";

const TableStatistic = () => {
    const {
        coinflipStatistics3Months,
        coinflipStatistics7days,
        coinflipStatisticsMonth,
        coinflipStatisticsToday,
        coinflipStatisticsYesterday,
        income3Months,
        income7days,
        incomeMonth,
        incomeToday,
        incomeYesterday,
        jackpotStatistics3Months,
        jackpotStatistics7days,
        jackpotStatisticsMonth,
        jackpotStatisticsToday,
        jackpotStatisticsYesterday,
        kingStatistics3Months,
        kingStatistics7days,
        kingStatisticsMonth,
        kingStatisticsToday,
        kingStatisticsYesterday,
        newUsers3Months,
        newUsers7days,
        newUsersMonth,
        newUsersToday,
        newUsersYesterday,
        page,
        paymentsInput3Months,
        paymentsInput7days,
        paymentsInputMonth,
        paymentsInputToday,
        paymentsInputYesterday,
        paymentsOutput3Months = 0,
        paymentsOutput7days = 0,
        paymentsOutputMonth = 0,
        paymentsOutputToday = 0,
        paymentsOutputYesterday = 0,
        paymentsReferral3Months,
        paymentsReferral7days,
        paymentsReferralMonth,
        paymentsReferralToday,
        paymentsReferralYesterday,
    } = useSelector(state => state.AdminMain);

    const STATISTICS = [{
        title: "Статистика пополнения",
        inputToday: paymentsInputToday ,
        inputYesterday: paymentsInputYesterday,
        input7days: paymentsInput7days,
        inputMonth: paymentsInputMonth,
        input3month: paymentsInput3Months
    }, {
        title: "Статистика вывода",
        inputToday: 0 - paymentsOutputToday ,
        inputYesterday: 0 - paymentsOutputYesterday,
        input7days: 0 - paymentsOutput7days,
        inputMonth: 0 - paymentsOutputMonth,
        input3month: 0 - paymentsOutput3Months
    }, {
        title: "Статистика дохода",
        inputToday: incomeToday ,
        inputYesterday: incomeYesterday,
        input7days: income7days,
        inputMonth: incomeMonth,
        input3month: income3Months
    }, {
        title: "Количество новых пользователей",
        inputToday: newUsersToday ,
        inputYesterday: newUsersYesterday,
        input7days: newUsers7days,
        inputMonth: newUsersMonth,
        input3month: newUsers3Months
    }, {
        title: "Статистика реф. начислений",
        inputToday: paymentsReferralToday ,
        inputYesterday: paymentsReferralYesterday,
        input7days: paymentsReferral7days,
        inputMonth: paymentsReferralMonth,
        input3month: paymentsReferral3Months
    }, {
        title: "Статистика по JACKPOT",
        inputToday: jackpotStatisticsToday ,
        inputYesterday: jackpotStatisticsYesterday,
        input7days: jackpotStatistics7days,
        inputMonth: jackpotStatisticsMonth,
        input3month: jackpotStatistics3Months
    }, {
        title: "Статистика по COINFLIP",
        inputToday: coinflipStatisticsToday ,
        inputYesterday: coinflipStatisticsYesterday,
        input7days: coinflipStatistics7days,
        inputMonth: coinflipStatisticsMonth,
        input3month: coinflipStatistics3Months
    }, {
        title: "Статистика по KING OF THE HILL",
        inputToday: kingStatisticsToday ,
        inputYesterday: kingStatisticsYesterday,
        input7days: kingStatistics7days,
        inputMonth: kingStatisticsMonth,
        input3month: kingStatistics3Months
    }];

    return (
        <table className="statistics">
            <tbody>
            {STATISTICS.map(item => <TableStatisticItem item={item}/>)}
            </tbody>
        </table>
    )
};

const TableStatisticItem = ({item = {}}) => {
    const {title = "", inputToday = 0, inputYesterday = 0, input7days = 0, inputMonth = 0, input3month = 0} = item;
    return (
        <tr>
            <td className="statistics__title">{title}</td>
            <td>
                <span className="statistics__number">{inputToday}</span>
                <p className="statistics__period">За сегодня</p>
            </td>
            <td>
                <span className="statistics__number">{inputYesterday}</span>
                <p className="statistics__period">За вчера</p>
            </td>
            <td>
                <span className="statistics__number">{input7days}</span>
                <p className="statistics__period">За 7 дней</p>
            </td>
            <td>
                <span className="statistics__number">{inputMonth}</span>
                <p className="statistics__period">За месяц</p>
            </td>
            <td>
                <span className="statistics__number">{input3month}</span>
                <p className="statistics__period">За 3 месяца</p>
            </td>
        </tr>
    )
};

export {
    TableStatistic
}