import React from "react";
import {useSelector} from "react-redux";
import {MainCenterWrapper} from "../../../Wrapper/MainCenterWrapper";


const ProfileBlock = () => {
    const {image, name} = useSelector(state => state.User);
    return(
        <div className="profile">
            <div className="profile__avatar">
                <img src={image} alt="avatar"/>
            </div>
            <p className="profile__name">{name}</p>
        </div>
    )
};
const ProfileBlockMobile = () => {
    const {image, name} = useSelector(state => state.User);

    return(
        <div className="profile mobile-show">
            <div className="profile__avatar">
                <img src={image} alt="avatar"/>
            </div>
            <p className="profile__name">{name}</p>
        </div>
    )
};


export {
    ProfileBlock,
    ProfileBlockMobile
}