import React, {useState} from "react";
import {useDispatch, useSelector} from "react-redux";
import {adminGetOutputData, CHANGE_SEARCH_OUTPUT} from "../../../Actions/AdminActions/AdminOutputAction";
import DateRangePicker from "@wojtekmaj/react-daterange-picker";
import moment from "moment";

const OutPutSearch = () => {
    const [dates, setDates] = useState([null, null]);
    const {search} = useSelector(state => state.AdminPromo);
    const dispatch = useDispatch();
    const setSearch = (value) => {
        dispatch({type: CHANGE_SEARCH_OUTPUT, value})
    };
    const [searchValue, setSearchValue] = useState("");
    // console.log(dates);
    return (
        <div className="header__right">

            <form className="search" onSubmit={e => {
                e.preventDefault();
                setSearch(searchValue);
                dispatch(adminGetOutputData(1, searchValue));
            }}>
                <input type="text" name="search" value={searchValue} onChange={e => setSearchValue(e.target.value)}
                       className="search__inp" placeholder="Введите запрос"/>
                <button className="search__btn" onClick={() => {
                    setSearch(searchValue);
                    dispatch(adminGetOutputData(1, searchValue));
                }}>
                    <svg width="16" height="16" viewBox="0 0 16 16" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <path
                            d="M15.8616 15.184L11.9725 11.2958C13.0157 10.0967 13.6474 8.53215 13.6474 6.82217C13.6474 3.05809 10.5851 0 6.82369 0C3.05877 0 0 3.06164 0 6.82217C0 10.5827 3.06232 13.6443 6.82369 13.6443C8.53404 13.6443 10.0989 13.0129 11.2983 11.9698L15.1874 15.8581C15.2797 15.9503 15.4039 16 15.5245 16C15.6452 16 15.7693 15.9539 15.8616 15.8581C16.0461 15.6736 16.0461 15.3685 15.8616 15.184ZM0.954535 6.82217C0.954535 3.5867 3.58749 0.957871 6.82014 0.957871C10.0563 0.957871 12.6857 3.59024 12.6857 6.82217C12.6857 10.0541 10.0563 12.69 6.82014 12.69C3.58749 12.69 0.954535 10.0576 0.954535 6.82217Z"
                            fill="white"></path>
                    </svg>
                </button>
            </form>

            <div className="calendar">
                <form className="calendar__form" onSubmit={e => {
                    e.preventDefault();
                    dispatch(adminGetOutputData(1, searchValue,[moment(dates[0]).format('DD.MM.YYYY'), moment(dates[1]).format('DD.MM.YYYY')]));
                }}>
                    {/*<input type="text" name="calendar_range" id="" autoComplete="off" className="calendar__inp"*/}
                    {/*       placeholder="Выбор периода"/>*/}
                    <DateRangePicker className={"search__inp"} format={"dd.MM.yy"} value={dates} selectRange={true}
                                     onChange={newDates => {
                                         if (newDates) {
                                             setDates([...newDates.map(item => new Date(item))]);
                                         } else {
                                             setDates([null, null])
                                         }
                                     }} calendarIcon={<button className="calendar__btn">
                        <svg width="16" height="16" viewBox="0 0 16 16" fill="none"
                             xmlns="http://www.w3.org/2000/svg">
                            <path
                                d="M14.125 1.25H13.375V0H12.125V1.25H3.875V0H2.625V1.25H1.875C0.841125 1.25 0 2.09113 0 3.125V14.125C0 15.1589 0.841125 16 1.875 16H14.125C15.1589 16 16 15.1589 16 14.125V3.125C16 2.09113 15.1589 1.25 14.125 1.25ZM14.75 14.125C14.75 14.4696 14.4696 14.75 14.125 14.75H1.875C1.53038 14.75 1.25 14.4696 1.25 14.125V5.875H14.75V14.125ZM14.75 4.625H1.25V3.125C1.25 2.78038 1.53038 2.5 1.875 2.5H2.625V3.75H3.875V2.5H12.125V3.75H13.375V2.5H14.125C14.4696 2.5 14.75 2.78038 14.75 3.125V4.625Z"
                                fill="white"></path>
                            <path d="M3.625 7.1875H2.375V8.4375H3.625V7.1875Z" fill="white"></path>
                            <path d="M6.125 7.1875H4.875V8.4375H6.125V7.1875Z" fill="white"></path>
                            <path d="M8.625 7.1875H7.375V8.4375H8.625V7.1875Z" fill="white"></path>
                            <path d="M11.125 7.1875H9.875V8.4375H11.125V7.1875Z" fill="white"></path>
                            <path d="M13.625 7.1875H12.375V8.4375H13.625V7.1875Z" fill="white"></path>
                            <path d="M3.625 9.6875H2.375V10.9375H3.625V9.6875Z" fill="white"></path>
                            <path d="M6.125 9.6875H4.875V10.9375H6.125V9.6875Z" fill="white"></path>
                            <path d="M8.625 9.6875H7.375V10.9375H8.625V9.6875Z" fill="white"></path>
                            <path d="M11.125 9.6875H9.875V10.9375H11.125V9.6875Z" fill="white"></path>
                            <path d="M3.625 12.1875H2.375V13.4375H3.625V12.1875Z" fill="white"></path>
                            <path d="M6.125 12.1875H4.875V13.4375H6.125V12.1875Z" fill="white"></path>
                            <path d="M8.625 12.1875H7.375V13.4375H8.625V12.1875Z" fill="white"></path>
                            <path d="M11.125 12.1875H9.875V13.4375H11.125V12.1875Z" fill="white"></path>
                            <path d="M13.625 9.6875H12.375V10.9375H13.625V9.6875Z" fill="white"></path>
                        </svg>
                    </button>}/>

                </form>

            </div>
        </div>
    )
};



export {
    OutPutSearch
}