import {useSelector} from "react-redux";
import React from "react";
import {ParserData} from "../../../Help/ParserData";

const leftSelector = state => {
    return {
        bank: state.CoinFlip.bank,
        countOfGameToday: state.CoinFlip.gamesToday + state.CoinFlip.activeGames.length,
        countOfAwaitingGame: state.CoinFlip.activeGames.length,
    }
};

const rightSelector = state => {
    return {
        bank: state.CoinFlip.bankUser,
        countOfGame: state.CoinFlip.userGames,
        countOfAwaitingGame: state.CoinFlip.userGamesNow
    }
};



class HistoryStatistics extends React.Component {
    render() {
        return (
            <div className="coin-history">
                <HistoryStatisticsLeft/>
                <HistoryStatisticsRight/>
            </div>
        );
    }
}

const HistoryStatisticsLeft = () => {
    const {
        bank,
        countOfGameToday,
        countOfAwaitingGame
    } = useSelector(leftSelector);
    return (
        <div className="coin-history__left">
            <h4 className="coin-history__title">Ежедневная статистика</h4>
            <div className="coin-history-box">
                <div className="coin-history-box__col">
                    <p className="coin-history-box__value coin-history-box__value--orange">{ParserData(bank)}
                        <span> COINS</span></p>
                    <p className="coin-history-box__text">Общая сумма</p>
                </div>
                <div className="coin-history-box__col coin-history-box__col--center">
                    <p className="coin-history-box__value">{countOfGameToday}</p>
                    <p className="coin-history-box__text">Всего игр</p>
                </div>
                <div className="coin-history-box__col coin-history-box__col--last">
                    <p className="coin-history-box__value">{countOfAwaitingGame}</p>
                    <p className="coin-history-box__text">Игр в ожидании</p>
                </div>
            </div>
        </div>
    )
};


const HistoryStatisticsRight = () => {
    const isAuth = useSelector(state => state.User.isAuth);
    const {
        bank,
        countOfGame,
        countOfAwaitingGame
    } = useSelector(rightSelector);

    return (
        <div className="coin-history__right">
            {
                isAuth ?
                    <>
                        <h4 className="coin-history__title">ВАШИ ИГРЫ</h4>
                        <div className="coin-history-box">
                            <div
                                className={`coin-history-box__col bet-indicator ${countOfGame == 0 ? "disabled" : ""}`}>
                                {
                                    countOfGame > 0 ?
                                        <>
                                            <i className="ic-user"></i>
                                            <p className="coin-history-box__text">Есть ставки</p>
                                        </>
                                        :
                                        <>
                                            <i className="ic-user"></i>
                                            <p className="coin-history-box__text">Нет ставок</p>
                                        </>
                                }
                            </div>
                            <div className="coin-history-box__col coin-history-box__col--center">
                                <p className="coin-history-box__value">{countOfGame} </p>
                                <p className="coin-history-box__text">Создано игр</p>
                            </div>
                            <div className="coin-history-box__col coin-history-box__col--last">
                                <p className="coin-history-box__value coin-history-box__value--orange">{ParserData(bank)} <span>COINS</span>
                                </p>
                                <p className="coin-history-box__text">Сумма выигрышей</p>
                            </div>
                        </div>
                    </>
                    :
                    ""
            }
        </div>
    )
};


export {
    HistoryStatistics
}