import axios from "axios";
import {AXIOS_PARAMS, headers} from "../../Help/AxiosHTTP";
import {errorNotification, notificationSuccess} from "../../Notifications/Notification";
import {UPDATE_BALANCE} from "../UserAction";


export const GET_JACKPOT_DATA = "GET_JACKPOT_DATA";



export const CHANGE_GAME_TYPE = "CHANGE_GAME_TYPE";
export const GAME_START = "GAME_START";
export const GAME_ROLLING = "GAME_ROLLING";
export const GAME_END = "GAME_END";


export const ADD_BET_JACKPOT = "ADD_BET_JACKPOT";
export const UPDATE_BANK = "UPDATE_BANK";
export const UPDATE_TIMER = "UPDATE_TIMER";
export const UPDATE_TIMER_NEW_GAME = "UPDATE_TIMER_NEW_GAME";
export const SET_WINNER = "SET_WINNER";
export const NEW_GAME = "NEW_GAME";
//action to fetch

export const START_FETCHING = "START_FETCHING";
export const FETCHED = "FETCHED";




const changeGameType = newTypeId => dispatch => {
    dispatch({
        type: START_FETCHING
    });
    axios.post(`${AXIOS_PARAMS.path}api`, {typeGameId: newTypeId}).then(res => {
        const data = res.data;
        dispatch({
            type: GET_JACKPOT_DATA,
            data
        });
        dispatch({
            type: FETCHED,
            status: res.status
        })
    });
    dispatch({
        type: CHANGE_GAME_TYPE,
        gameTypeId: newTypeId
    })
};



const gameStart = dispatch => {
    dispatch({
        type: GAME_START
    })
};

const gameRolling = dispatch => {
    dispatch({
        type: GAME_ROLLING
    })
}

const gameEnd = dispatch => {
    dispatch({
        type: GAME_END
    })
};



const getJackpotData = (GameTypeId = 1) => dispatch => {
    const gameTypeId = GameTypeId;
    dispatch({
        type: START_FETCHING
    });
    axios.post(`${AXIOS_PARAMS.path}api`, {typeGameId: gameTypeId},{...headers()}).then(res => {
        let data = res.data;
        dispatch({
            type: GET_JACKPOT_DATA,
            data
        });
        dispatch({
            type: FETCHED,
            status: res.status
        })
    }).catch(e => {
        dispatch({
            type: FETCHED,
            status: e.status
        });
        errorNotification("Service no response", " ");
    })
};
const addParticipants = newParticipant => dispatch => {
    const {bet, bank} = newParticipant;
    dispatch({
        type: UPDATE_BANK,
        bank
    });
    dispatch({
        type: ADD_BET_JACKPOT,
        bet
    })
};

const updateJackpotTimer = (newTime) => dispatch => {
    dispatch({
        type: UPDATE_TIMER,
        newTime
    })
};
const updateJackpotTimerNewGame = (newTime) => dispatch => {
    dispatch({
        type: UPDATE_TIMER_NEW_GAME,
        newTime
    })
};


const getWinner = (winnerData) => dispatch => {
  dispatch({
      type: SET_WINNER,
      winner: {
          winner_account_id: winnerData.winnerId
      },
      winnerData
  })
};
const prepareNewGame = (endGameData,id) =>  dispatch => {
    const {
        viewHistoryWinner,
        range,
        balanceUser,
        winnerId
    } = endGameData;
    if(id === winnerId){
        dispatch({
            type: UPDATE_BALANCE,
            balance: balanceUser
        });
        notificationSuccess("Вы выйграли в JackPot", " ");
    }
    dispatch({
        type: NEW_GAME,
        game:{
            ...viewHistoryWinner,
            winner: {
                ...viewHistoryWinner.winner,
                range
            }
        }
    })
};

export {
    changeGameType,
    gameStart,
    gameRolling,
    gameEnd,
    getJackpotData,
    addParticipants,
    updateJackpotTimer,
    getWinner,
    updateJackpotTimerNewGame,
    prepareNewGame
}