import React, {useState} from "react";
import axios from "axios"
import {AXIOS_PARAMS, headers} from "../../../Help/AxiosHTTP";
import {errorNotification, notificationSuccess} from "../../../Notifications/Notification";
import {useDispatch, useSelector} from "react-redux";
import {adminGetOutputData, CHANGE_SEARCH_OUTPUT} from "../../../Actions/AdminActions/AdminOutputAction";
import {adminGetPromoData, CHANGE_SEARCH_PROMO} from "../../../Actions/AdminActions/AdminPromocodesAction";
const CreatePromo = (promoValue, promoSum,funcAfter) => {
    axios.post(`${AXIOS_PARAMS.path}api/admin/promocodes`,{"promocode-value": promoValue,"promocode-sum": promoSum },{...headers()}).then(res => {
        console.log(res);
        notificationSuccess("Промокод успешно сгенерирован", " ")
        funcAfter();
    }).catch(res => {
        errorNotification("Произошла непредвиденная ошибка", " ")
    })
}


const PromoCodesSearchGen = () => {
    const dispatch = useDispatch();
    const [promoValue, setPromoValue] = useState(0);
    const [promoSum,setPromoSum] = useState(0);
    const {current_page} = useSelector(state => state.AdminPromo.promocodes);
    const {search} = useSelector(state => state.AdminOutput);
    const setSearch = (value) => {
        dispatch({type: CHANGE_SEARCH_PROMO, value})
    };
    const [searchValue, setSearchValue] = useState("");
    return (
        <div className="header__right">
            <div className="promocode">
                <p className="promocode__title">Генерация <br/> промокодов</p>
                <form className="promocode__form" onSubmit={(e) => {
                    e.preventDefault()
                    CreatePromo(promoValue,promoSum,()=>dispatch(adminGetPromoData(current_page)));
                    setPromoSum(0);
                    setPromoValue(0);
                }}>
                    <input type="hidden" name="_token" value="m0WrihzR9gOwbQv3qeQMQWDO4AduTBCNSE4QvMKM"/>
                    <input type="number" name="promocode-sum" id=""
                           value={promoSum}
                           onChange={(e) => setPromoSum(e.target.value)}
                           className="promocode__inp"
                           placeholder="Введите сумму"/>
                    <input type="number" name="promocode-value"
                           value={promoValue}
                           onChange={(e) => setPromoValue(e.target.value)}
                           id="" className="promocode__inp"
                           placeholder="Введите количество промокодов"/>
                    <button className="promocode__btn" onClick={(e)=> {
                        e.preventDefault();
                        CreatePromo(promoValue,promoSum, ()=>dispatch(adminGetPromoData(current_page)));
                        setPromoSum(0);
                        setPromoValue(0);
                    }}>
                        <svg width="16" height="16" viewBox="0 0 16 16" fill="none"
                             xmlns="http://www.w3.org/2000/svg">
                            <path
                                d="M15.7657 0.319082C15.4533 -0.106361 14.9468 -0.106361 14.6343 0.319082L5.04983 13.3703L1.3657 8.35369C1.0533 7.92824 0.546797 7.92829 0.234328 8.35369C-0.0781094 8.77909 -0.0781094 9.46878 0.234328 9.89422L4.48414 15.6811C4.79645 16.1065 5.30333 16.1062 5.61552 15.6811L15.7657 1.85966C16.0781 1.43426 16.0781 0.744525 15.7657 0.319082Z"
                                fill="white"></path>
                        </svg>
                    </button>
                </form>
            </div>
            <form className="search" onSubmit={e => {
                e.preventDefault();
                dispatch(adminGetPromoData(1, searchValue))
                setSearch(searchValue)
            }}>
                <input type="text" name="search" value={searchValue} onChange={e => setSearchValue(e.target.value)} className="search__inp" placeholder="Поиск промокодов"/>
                <button className="search__btn" onClick={e => {
                    e.preventDefault();
                    dispatch(adminGetPromoData(1, searchValue))
                    setSearch(searchValue)
                }}>
                    <svg width="16" height="16" viewBox="0 0 16 16" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <path
                            d="M15.8616 15.184L11.9725 11.2958C13.0157 10.0967 13.6474 8.53215 13.6474 6.82217C13.6474 3.05809 10.5851 0 6.82369 0C3.05877 0 0 3.06164 0 6.82217C0 10.5827 3.06232 13.6443 6.82369 13.6443C8.53404 13.6443 10.0989 13.0129 11.2983 11.9698L15.1874 15.8581C15.2797 15.9503 15.4039 16 15.5245 16C15.6452 16 15.7693 15.9539 15.8616 15.8581C16.0461 15.6736 16.0461 15.3685 15.8616 15.184ZM0.954535 6.82217C0.954535 3.5867 3.58749 0.957871 6.82014 0.957871C10.0563 0.957871 12.6857 3.59024 12.6857 6.82217C12.6857 10.0541 10.0563 12.69 6.82014 12.69C3.58749 12.69 0.954535 10.0576 0.954535 6.82217Z"
                            fill="white"></path>
                    </svg>
                </button>
            </form>
        </div>
    )
}

export {
    PromoCodesSearchGen
}