import {AXIOS_PARAMS, headers} from "../../Help/AxiosHTTP";
import axios from "axios";
import {UPDATE_BALANCE} from "../UserAction";
import {errorNotification, notification, notificationSuccess} from "../../Notifications/Notification";

export const GET_COIN_FLIP_PAGE_DATA = "GET_COIN_FLIP_PAGE_DATA";
export const GET_GAME_DATA = "GET_GAME_DATA";
export const START_GAME = "START_GAME";
export const FINISH_GAME = "FINISH_GAME";
export const CLEAR_GAME = "CLEAR_GAME";

//fetch action
export const START_FETCHING = "START_FETCHING";
export const FETCHED = "FETCHED";
//action game
export const INIT_NEW_GAME = "INIT_NEW_GAME";
export const UPDATE_USER_GAMES = "UPDATE_USER_GAMES";
export const UPDATE_USER_BANK = "UPDATE_USER_BANK_GAME";
export const NEW_STARTED_GAME = "NEW_STARTED_GAME";
export const GAME_END_SOCKET = "GAME_END_SOCKET";
export const WIN_CASH = "WIN_CASH";


const getCoinFlipPageData = dispatch => {
    dispatch({
        type: START_FETCHING
    });
    axios.get(`${AXIOS_PARAMS.path}api/coinflip`, {...headers()}).then(res => {
        dispatch({
            type: GET_COIN_FLIP_PAGE_DATA,
            data: res.data
        });
        dispatch({
            type: FETCHED,
            status: res.status
        });
    }).catch(e => {
        dispatch({
            type: FETCHED,
            status: e.status
        });
        errorNotification("Service no response", " ");
    })
};
const updateBalance = data => dispatch => {
    const {
        balance,
        bankUser,
        userGames,
    } = data;

    dispatch({
        type: UPDATE_BALANCE,
        balance
    });
    dispatch({
        type: UPDATE_USER_GAMES,
        bankUser: bankUser,
        userGames: userGames
    })
};

const getGameDataCoinFlip = (game_id) => dispatch => {
    let data = {
        game_id: game_id,
        gameReady: false,
        gameEnd: false,
        duration: 10,
        participants: [{
            name: "nameless1"
        }, {
            name: "nameless2"
        }],
        winner: 2,
        hashWinner: "9d0asi90u231jo9ra",
        winnerTicket: "999"
    };

    dispatch({
        type: GET_GAME_DATA,
        data: data
    })
};

const addGameCoinFlip = (data) => dispatch => {
    dispatch({
        type: INIT_NEW_GAME,
        data
    });
};

const updateUserGames = userGames => dispatch => {
    dispatch({
        type: UPDATE_USER_GAMES,
        userGames
    })
};

const gameStartedSocketHandler = (data, id) => dispatch => {
    const game = {
            ...data,
        },
        id = data.id;
    dispatch({
        type: NEW_STARTED_GAME,
        game,
        id
    })
};


const gameEndSocketData = (data, userId) => dispatch => {
    let {game} = data;
    const {
        winner_account_id = 0,
        bank = "0",
        id
    } = game;
    let winner = game.participants.filter(item => item.account_id === winner_account_id)[0];
    let looser = game.participants.filter(item => item.account_id !== winner_account_id)[0];
    game = {
        ...game,
        winner: {
            ...winner.account,
            ...winner
        },

    };
    if (userId === winner_account_id) {
        axios.get(`${AXIOS_PARAMS.path}api/account/bank-user`, {...headers()}).then(res => {
            console.log(res);
            dispatch({
                type: UPDATE_BALANCE,
                balance: res.data.balance
            });
            dispatch({
                type: UPDATE_USER_BANK,
                bankUser: res.data.bankUser
            })
        });
        notificationSuccess("Победа", `Вы выйграли ${bank}`)
    } else {
        if (userId === looser.account_id) {
            errorNotification("Поражение", `Вы проиграли ${Number(bank) / 2}`)
        }
    }
    dispatch({
        type: GAME_END_SOCKET,
        game,
        id
    })
};


