const startGame = dispatch => {
    dispatch({
        type: START_GAME
    })
};
const finishGame = dispatch => {
    dispatch({
        type: FINISH_GAME
    })
};

export {
    getCoinFlipPageData,
    getGameDataCoinFlip,
    addGameCoinFlip,
    startGame,
    finishGame,
    updateUserGames,
    updateBalance,
    gameStartedSocketHandler,
    gameEndSocketData
}