import React from "react";
import {AdminApp} from "../AdminApp";
import {UserApp} from "../UserApp";
import {connect} from "react-redux";
import {checkAuth, setDefMean, setMainStyle} from "../Actions/UserAction";






const mapStateToProps = state => {
    return {
        user: state.User,
        role_id: state.User.role_id,
        mainStyleHref: state.User.mainStyleHref
    }
};
const mapDispatchToProps = dispatch => {
    return {
        setMainStyle: (href) => dispatch(setMainStyle(href)),
        checkAuth: () => dispatch(checkAuth),
        checkSessionTheme: ()=>dispatch(setDefMean)
    }
};


