import React from "react";
import {KingParticipant} from "../KingParticipants";
import defImg from "../../../../assets/img/fox.png";
import {useDispatch, useSelector} from "react-redux";
import {openModal} from "../../../../Actions/ModalAction";
import {GameBank, GameBankMobile, GameBankMobileSenyor, GameBankSenoyr} from "../GameBank";
import {SenyorGameTimer} from "../GameTime";


const SenoyrGame = () => {
    const {
        status,
        participants,
    } = useSelector(state => state.KingOfTheHill.gameSenyor);
    return (
        <div className="game game-senyor">
            <SenoyrGameLeft/>
            <SenoyrGameCenter status={status} participants={participants}/>
            <GameBankMobileSenyor/>
            <SenoyrGameRight/>
        </div>
    )
};


const SenoyrGameLeft = () => {
    const {
        id,
        participants,
        status_id,
    } = useSelector(state => state.KingOfTheHill.gameSenyor);
    return (
        <div className="game__left">
            <p className="game__title">Senyor #{id}</p>
            <ul className="game__participants game__participants_senyor game__participants-wrapper">
                {
                    status_id ?
                        participants.reverse().map((participant, index) => {
                            return <KingParticipant key={index} participant={participant}/>
                        })
                        :
                        ""
                }
                {/*@if($gameSenyor)*/}
                {/*@foreach($gameSenyor->participants->reverse() as $key => $participant)*/}
                {/*@if($loop->index == $gameSenyor->participants->count()-1)*/}

                {/*@continue*/}

                {/*@endif*/}
                {/*@include('blocks.king-participants')*/}
                {/*@endforeach*/}
                {/*@endif*/}
            </ul>
        </div>
    )
};
const SenoyrGameCenter = () => {
    const {
        participants,
        status_id,
    } = useSelector(state => state.KingOfTheHill.gameSenyor);
    let image;
    if(participants.length){
        image = participants[0].account.image;
    } else {
        image = defImg;
    }
    return (
        <div className="game__center">
            <div className="game__participants--adaptive">
                {
                    status_id ?
                        <>
                            {participants.reverse().map((participant, index) => {
                                return <KingParticipant key={index} participant={participant}/>
                            })}
                        </>
                        :
                        ""
                }
            </div>
            <div className="game__winner">
                {
                    status_id && participants.length > 0 ?
                        <img src={image} alt="winner" className="game__winner-img"/>
                        :
                        <img src={defImg} alt="winner" className="game__winner-img"/>
                }
            </div>
        </div>
    )
};
const SenoyrGameRight = () => {
    return (
        <div className="game__right">
            <GameBankSenoyr/>
            <SenyorGameTimer/>
        </div>
    )
};

export {
    SenoyrGame
}