import React from "react";
import {useDispatch, useSelector} from "react-redux";
import {closeModal} from "../../../Actions/ModalAction";


const CrashHistoryGameModal = () => {
    const dispatch = useDispatch();
    const {game = {}, bets = []} = useSelector(state => state.Modal.props);
    const {
        id = 0,
        hash = 0,
        profit = 0,
        date = "",
    } = game;
    return (
        <div className="modal modal--help info-game-crash" style={{maxWidth: "100% !important"}}>
            <div className="modal__top">
                <h6 className="modal__title">CRASH: {id}</h6>
                <a onClick={() => dispatch(closeModal)}
                   className="modal__close">
                    <i className="ic-close">
                    </i>
                </a>
            </div>
            <div className="modal__body">
                <div className="modal-help nano">
                    <div className="nano-content rules-of-the-game modal-table mb prm">
                        <div className="info-bl-game">
                            <div className="list-info">Хэш: {hash} <a
                                href={`http://sha224.net/?val=${profit}`} target="_blank">Проверить</a></div>
                            <div className="list-info">Номер игры: {id}</div>
                            <div className="list-info">Дата: {date}</div>
                            <div className="list-info">Коэффициент: {profit}x</div>
                        </div>
                    </div>
                    <div className="nano-content rules-of-the-game modal-table prm">
                        {
                            bets.length ?
                                <table className="table">
                                    <thead>
                                    <tr>
                                        <th scope="col">Игрок</th>
                                        <th>Ставка</th>
                                        <th>Коэффициент</th>
                                        <th>Выигрыш</th>
                                    </tr>
                                    </thead>
                                    <tbody>
                                    {
                                        bets.map(bet => {
                                            const win = {
                                                isWin: bet.number <= profit,
                                                winCount: bet.number * bet.price - bet.price
                                            }
                                            return <CrashHistoryGameModalItem bet={bet} win={win}/>
                                        })
                                    }
                                    </tbody>
                                </table>
                                :
                                <div style={{textAlign: "center"}}>
                                    Никто не играл
                                </div>
                        }
                    </div>
                </div>
            </div>
        </div>
    )
};


const CrashHistoryGameModalItem = ({bet, win}) => {
    const {name, price, number} = bet;
    const {winCount = 10, isWin = false} = win;
    return (
        <tr style={{color: isWin ? "#7DD358" : "#E95E3F"}}>
            <th scope="row" className="link-info-game-modal">
                {name}
            </th>
            <td scope="row">{price}</td>
            <td scope="row">{number}</td>
            <td scope="row">{isWin ? "+" + winCount : "-"}</td>
        </tr>
    )
};


export {
    CrashHistoryGameModal
}