import {CLOSE_MODAL, FETCHED, OPEN_MODAL, START_FETCHING, UPDATE_PROPS} from "../Actions/ModalAction";

const ModalReducer = (state = {
    nowIs: "",
    isVisible: false,
    props: {},
    fetching: true,
    fetched: false,
    status: ""
},action) => {
    switch (action.type) {
        case OPEN_MODAL: return {
            ...state,
            nowIs: action.modalName,
            isVisible: true,
            props: action.props
        };
        case CLOSE_MODAL: return {
            ...state,
            nowIs: "",
            isVisible: false,
            fetching: false,
            fetched: false,
            props: {}
        };
        case START_FETCHING: return {
            ...state,
            fetching: true,
            fetched: false,
            status: "",
        };
        case FETCHED: return {
            ...state,
            fetching: false,
            fetched: true,
            status: action.status,
        };
        case UPDATE_PROPS: return {
            ...state,
            props:{
                ...state.props,
                ...action.data
            }
        };
        default: return state
    }
};

export {
    ModalReducer
}