import React from "react";
import {Header} from "../block/Header";
import {SideMenuLeft} from "../../Components/Admin/SideMenuLeft";
import {WalletMain} from "./WalletMain";


class WalletPage extends React.Component {
    componentDidMount() {
        this.props.getData();
    }
    render() {
        return (
            <>
                <Header pageName={'Пополнение'}/>
                <div className="main">
                    <SideMenuLeft nowIs={'wallet'}/>
                    <WalletMain/>
                </div>
            </>
        );
    }
}

export default WalletPage