import {
    ADD_PARTICIPANT_CLASSIC,
    ADD_PARTICIPANT_SENOYR,
    GAME_CLASSIC_END,
    GAME_CLASSIC_START,
    GAME_SENOYR_END,
    GAME_SENOYR_START,
    GET_KING_OF_THE_HILL_DATA,
    NEW_GAME_CLASSIC,
    NEW_GAME_SENOYR,
    UPDATA_GAMES,
    UPDATE_TIMER_CLASSIC,
    UPDATE_TIMER_SENOYR
} from "../../Actions/Game/KingOfTheHillAction";
import {FETCHED, START_FETCHING} from "../../Actions/Game/JackpotAction";
import {spliceGame} from "../Help/help";

const KingOfTheHillReducer = (state = {
    games: [],
    lastGame: {
        status_id: 0,
        bank: 0
    },
    gameClassic: {
        bank: 0,
        participants: [],
        looser: {},
        status_id: 1,

    },
    gameSenyor: {
        bank: 0,
        participants: [],
        looser: {},
        status_id:1,
    },
    classic: {
        timer: 20,
        gameStarted: false,
        gameFinished: false
    },
    classicWinner: {

    },
    senoyr: {
        timer: 20,
        gameStarted: false,
        gameFinished: false
    },
    senoyrWinner: {

    },
    fetching: true,
    fetched: false,
    status: "",
}, action) => {
    switch (action.type) {
        case GET_KING_OF_THE_HILL_DATA: return {
            ...state,
            ...action.data
        };
        //this classic game handlers
        case GAME_CLASSIC_START: return {
            ...state,
            classic: {
                ...state.classic,
                gameStarted: true,
                gameFinished: false

            }
        };
        case ADD_PARTICIPANT_CLASSIC: return {
            ...state,
            gameClassic: {
                ...state.gameClassic,
                participants: [action.participant, ...state.gameClassic.participants],
                bank: action.bank
            }
        };
        case UPDATE_TIMER_CLASSIC: return {
            ...state,
            classic: {
                ...state.classic,
                timer: action.newTime
            }
        };
        case GAME_CLASSIC_END: return {
            ...state,
            classic: {
                ...state.classic,
                gameStarted: false,
                gameFinished: true,
            },
            classicWinner: {
                ...action.winnerData,
            }
        };
        case NEW_GAME_CLASSIC: return {
            ...state,
            gameClassic: {
                ...action.game,
                status_id: 1,
                participants: [],
            },
            classic: {
                timer: 20,
                gameFinished: true
            },
        };
        case UPDATA_GAMES: return {
            ...state,
            games: [action.game, ...spliceGame(state.games)]
        };
        //  this end of classic game handlers

        //this senoyr game handlers

        case GAME_SENOYR_START: return {
            ...state,
            senoyr: {
                ...state.senoyr,
                gameStarted: true,
                gameFinished: false
            }
        };

        case ADD_PARTICIPANT_SENOYR: return {
            ...state,
            gameSenyor: {
                ...state.gameSenyor,
                participants: [action.participant, ...state.gameSenyor.participants],
                bank: action.bank
            }
        };
        case UPDATE_TIMER_SENOYR: return {
            ...state,
            senoyr: {
                ...state.senoyr,
                timer: action.newTime
            }
        };
        case GAME_SENOYR_END: return {
            ...state,
            senoyr: {
                ...state.classic,
                gameStarted: false,
                gameFinished: true,
            },
            senoyrWinner: {
                ...action.winnerData,
            }
        };
        case NEW_GAME_SENOYR: return {
            ...state,
            gameSenyor: {
                ...action.game,
                status_id: 1,
                participants: [],
            },
            senoyr: {
                timer: 20,
                gameFinished: true
            },
        };
        //  this end of senoyr game handlers

        //case for fetching
        case START_FETCHING: return  {
            ...state,
            status: "",
            fetching: true,
            fetched: false
        };
        case FETCHED: return  {
            ...state,
            status: action.status,
            fetching: false,
            fetched: true
        };
        default: return state
    }
};



export {
    KingOfTheHillReducer
}