import React, {useState} from "react";
import {useDispatch, useSelector} from "react-redux";
import {BetForm} from "../../JackpotPage/Modal/UI/BetForm";
import {closeModal, openModal} from "../../../Actions/ModalAction";
import axios from "axios";
import {AXIOS_PARAMS} from "../../../Help/AxiosHTTP";
import {updateBalance} from "../../../Actions/Game/CoinFlipAction";
import {errorNotification, notificationSuccess} from "../../../Notifications/Notification";

const handleCreateGame = (cash, color,dispatch) => {
    const {token, token_type} = JSON.parse(localStorage.getItem("tokenData"));
    const headers = {
        "Authorization": `${token_type} ${token}`
    };
    dispatch(closeModal);
    axios.post(`${AXIOS_PARAMS.path}api/account/create-game-coinflip`, {cash, color}, {headers}).then(res => {
        if(res.data.error){
            errorNotification(res.data.message, " ")
        } else {
            notificationSuccess("Игра создана", `Хэш вашей игры ${res.data.data.game.id}`);
            dispatch(updateBalance(res.data));
        }
    });
};
const BetCoinModalOrange = () => {
    const dispatch = useDispatch();
    const {image,balance} = useSelector(state => state.User);
    const [valueBet, setValueBet] = useState(100);
    return(
        <div className="modal modal--bet " id="betModalCoin">
            <div className="modal__top">
                <h6 className="modal__title">СДЕЛАТЬ СТАВКУ</h6>
                <a className="modal__close" data-izimodal-close onClick={()=>dispatch(closeModal)} ><i className="ic-close"></i></a>
            </div>
            <div className="modal__body">
                <div className="conclusion__balance">
                    <p className="conclusion__balance-text">Текущий баланс:</p>
                    <p className="conclusion__balance-value">{balance} <span>coins</span></p>
                </div>
                <div className="bet-avatar">
                    <div className="bet-avatar__wrap">
                        <i className="ic-coin-orange"></i>
                        <img src={image} alt="avatar" className="bet-avatar__img"/>
                    </div>
                </div>
                <BetForm valueBet={valueBet} setValueBet={setValueBet}/>
                <div className={`warning__balance balance-warning ${valueBet < balance ? "hide" : ""}`} >
                    <p className="warning__balance-text">* На вашем счете недостаточно средств для ставки</p>
                </div>
                <div className={`warning__balance min-price-warning ${valueBet >= 100? "hide": ""}`}>
                    <p className="warning__balance-text">* Минимальная ставка от 100 coins</p>
                </div>
                <div className="modal__buttons">
                    <a href="#"
                       onClick={()=>{
                           handleCreateGame(valueBet,"orange",dispatch)
                       }}
                        // onClick="MainFunction.createGameCoinflip(this);"
                       className="button button--gradient">СДЕЛАТЬ
                        СТАВКУ</a>
                    <a href="#" className="button button--border"
                       onClick={()=>{
                           dispatch(closeModal);
                           setTimeout(()=>dispatch(openModal("InputPayment")),150);
                       }}
                       data-izimodal-open="#inputModal"
                       data-izimodal-transitionin="fadeInDown">Пополнить</a>
                </div>
            </div>
        </div>
    )
};
