import $ from "jquery";

function startGame(winner, gameEndFunction = () => {
}) {
    let innerBlock = document.querySelector(".jackpotWinnerInner");
    let moveStep = 76;
    let nowIs = 3;
    let duration = 0.5;
    let timer;
    let innerWidth = innerBlock.parentNode.clientWidth;
    let width = window.innerWidth;
    console.log(innerWidth);
    let startPosition = width <= 500 ?
        Math.floor(((500 - width) * 111)/220) + 168
        :
        width <= 1280 ?
            168
            :
            width <= 1420 ?
                Math.floor(((1420 - width) * 38) / 140) + 37
                :
                37;
    // let startPosition = width <= 350 ?
    //     230
    //     :
    //     width <= 450 ?
    //         205
    //         :
    //         width <= 1280 ?
    //             167.5 :
    //
    //             width <= 1420 ?
    //                 (((1420 - innerWidth) * 40)/140 + 36.5)
    //                 :
    //                 36.5;
    innerBlock.style.marginLeft = `-${startPosition}px`;

    setLoop(35);
    function setLoop(duration) {
        if (nowIs < winner) {
            timer = setTimeout(() => {
                // duration+= Number(`1.10${playerLength - nowIs}`);
                if (winner - nowIs < 35) {
                    if (winner - nowIs > 24) {
                        duration *= 1.11;
                        step(duration);
                        setLoop(duration)

                    } else if (winner - nowIs > 9) {
                        duration *= 1.068;
                        step(duration);
                        setLoop(duration)
                    } else if (winner - nowIs > 7) {
                        duration *= 1.090;
                        step(duration * 1.2);
                        setLoop(duration)
                    } else {
                        console.log(1);
                        duration *= 1.15;
                        step(duration * 1.5);
                        setLoop(duration)
                    }
                } else {
                    step(duration);
                    setLoop(duration)
                }
            }, duration)
        } else {
            setTimeout(() => {
                gameEndFunction().gameEnd();
            }, duration * 4);
            // setTimeout(() => {
            //     gameEndFunction().gameStart();
            //     clearTimeout(timer);
            // }, duration * 10)
        }
    }

    function step(duration) {
        innerBlock.style.marginLeft = `-${nowIs * moveStep + startPosition}px`;
        innerBlock.style.transition = `${duration / 1000}s linear`;
        nowIs++;
    }

    function clearTimer() {
        clearTimeout(timer);
    }

    return {
        clearTimer,
        innerWidth
    }
}

function startTimerJackPot(duration, startGameFunction) {
    let timer = duration,
        minutes, seconds;
    let JackpotIntervalId = setInterval(function () {
        minutes = parseInt(timer / 60, 10);
        seconds = parseInt(timer % 60, 10);
        minutes = minutes < 10 ? "0" + minutes : minutes;
        seconds = seconds < 10 ? "0" + seconds : seconds;

        $('.jackpot-timer__time').text(seconds);
        if (--timer < 0) {
            timer = duration;
        }
        if (timer == 0) {

            setTimeout(function () {
                $('.jackpot-timer__time').text('00');
                startGameFunction()
            }, 1000);
            clearInterval(JackpotIntervalId);
        }
    }, 1000);

}


export {
    startGame,
    startTimerJackPot
}