import React from "react";
import defImg from "../../../assets/img/fox.png";
import {useSelector} from "react-redux";

const CrashPlayerTable = () => {
    const {ubets = []} = useSelector(state => state.Crash);
    if(ubets.length === 0){
        return (
            <div className="crash-player-table">
                <NoActivePlayers/>
            </div>
        )
    } else {
        return(
            <div className="crash-player-table">
                <CrashPlayerList/>
            </div>
        )
    }
};

const CrashPlayerList = () => {
    const {ubets = []} = useSelector(state => state.Crash);

    return (
        <div>
            <div className="crash-table-body-name name">
                <div className="crash-body-title">Пользователь</div>
                <div className="crash-body-title">Ставка</div>
                <div className="crash-body-title">Коэффициент</div>
                <div className="crash-body-title">Выигрыш</div>
            </div>
            <div className="block-state-crash">
                {ubets.map((bet,index) => {
                    return <CrashPlayerListItem key={index} ubet={bet}/>
                })}
            </div>
        </div>
    )
};

const CrashPlayerListItem = ({ubet = {}}) => {
    const {coef = 0} = useSelector(state => state.Crash.game);
    const isWin = Math.pow(1.06,coef);

    const {
        bet = {},
        user = {}
    } = ubet;
    const {
        number,
        price
    } = bet;
    const {
        name,
        image,
    } = user;
    return(
        <div className="result-table crash-table-body user-{{$bet['user']->id}}" data-cashout="{{$bet['bet']->number}}"
             data-bet="{{$bet['bet']->price}}" data-user="{{$bet['user']->id}}">
            <div className="crash-body-title img">
                <img src={image} alt="avatar" width="20" height="20"/> <div className="name-user">{name}</div>
            </div>
            <div className="crash-body-title bet">{price}</div>
            <div className="crash-body-title cash">{isWin > number ? number : "-"}</div>
            {
                Number(number) === 0 ?
                    <div className={`crash-body-title profit`} style={{color: isWin > number ?"#7DD358":""}}>
                        -
                    </div>
                    :
                    <div className={`crash-body-title profit`} style={{color: isWin > number ?"#7DD358":""}}>
                        {isWin > number?"+":"-"}{isWin > number ?(price * number - price).toFixed(2):""}
                    </div>
            }
        </div>
    )
}

const NoActivePlayers = () => {
    return(
        <div style={{textAlign: "center"}}>
            Нет активных игроков
        </div>
    )
};


export {
    CrashPlayerTable
}