import React from "react";
import {SenyorWinner} from "./SenyorWinner";
import {SenoyrGame} from "./SenoyrGame";


// this is class for senyor game

class SenoyrGameWrapper extends React.Component {

    componentDidMount() {
        // const timer = this.props.timer;
        // const gameEnd = this.props.gameEnd;
        // startTimerKingOfTheHill("game__time--senoyr", timer, gameEnd);

    }
    componentWillUnmount() {

    }

    render() {
        const gameFinished = this.props.gameFinished;
        const status_id = this.props.status_id;

        return (
            <>
                {
                    status_id ?
                        <SenoyrGame/>
                        :
                        ""
                }
                {
                    gameFinished ?
                        <SenyorWinner/>
                        :
                        ""
                }
            </>
        );
    }
}


export default SenoyrGameWrapper