import React, {useEffect, useState} from "react";
import {useDispatch, useSelector} from "react-redux";
import {openModal} from "../../../Actions/ModalAction";
import {AXIOS_PARAMS, headers} from "../../../Help/AxiosHTTP";
import axios from "axios";
import {errorNotification, notificationSuccess, warningNotification} from "../../../Notifications/Notification";
import {UPDATE_BALANCE} from "../../../Actions/UserAction";
import {checkAmIBet} from "../../../Actions/Game/CrashAction";
import {findUserBet} from "../../../Reducers/Help/help";

const betSetHandler = (value, currentValue, setState, balance) => {
    if (value + currentValue < balance && value + currentValue > 0) {
        setState(value + currentValue);
    } else {
        if (value + currentValue <= 0) {
            setState(0)
        } else {
            setState(balance)
        }
    }
};

const cashOutBet = () => {
    axios.post(`${AXIOS_PARAMS.path}api/crash/flash-cashout`,{}, {...headers()}).then(res => {
        notificationSuccess(res.data.msg, " ")
    }).catch(res => {
        errorNotification("Игра уже закончилась или еще не началась", " ")
    });
}
const buttonViewHandler = (isGoing = false, isIBet = false, isICanCashOut = false) => {
    if (!isGoing && isIBet) {
        return <button className="button" id="dp_bet">Ожидание</button>
    }
    if(isGoing && isIBet && !isICanCashOut){
        return <button className="button" id="dp_bet" disabled>Автовывод</button>
    }
    if(isGoing && isIBet && isICanCashOut){
        return <button className="button button--gradient" id="dp_bet" onClick={() => {
            cashOutBet();
        }}>Забрать</button>
    }
    return <button className="button button--gradient" id="dp_bet" type="submit">Играть</button>
    // !isGoing && isIBet ?
    {/*<button className="button"*/
    }
    {/*        id="dp_bet">*/
    }
    {/*    Ожидание*/
    }
    {/*</button>*/
    }
    //     :
    //
        {/*<button className="button button--gradient"*/}
        {/*        id="dp_bet"*/}
        {/*        type="submit">*/}
        {/*    Играть*/}
        {/*</button>*/}
}


const takeBet = (bet, cashOut, dispatch, balance) => {
    axios.post(`${AXIOS_PARAMS.path}api/crash/bet`, {bet, cashout: cashOut.toFixed(2)}, {...headers()}).then(res => {
        console.log(res);
        const {
            bet = 0,
            msg,
            status
        } = res.data;
        switch (status) {
            case 0: {
                warningNotification(msg, " ");
                return;
            }
            case 1: {
                notificationSuccess(msg, " ");
                dispatch({
                    type: UPDATE_BALANCE,
                    balance: balance - Number(bet)
                });
                return;
            }
            case 2: {
                notificationSuccess(msg, " ");
                dispatch({
                    type: UPDATE_BALANCE,
                    balance: balance - Number(bet)
                });
                return;
            }
            default: {
                errorNotification("Произошла непредвиденная ошибка", " ")
            }
        }
    }).catch(res => {
        errorNotification("Произошла непредвиденная ошибка", " ")
    })
};

const ControlPanel = () => {
    const {isAuth, balance, id = 0,role_id} = useSelector(state => state.User);
    const {
        ubets = [],
        timer,
        isGoing = false,
        isIBet = false,
        isICanCashOut = false,
    } = useSelector(state => state.Crash);
    const {
        coef = 0
    } = useSelector(state => state.Crash.game);
    const dispatch = useDispatch();
    const [currentBet, setCurrentBet] = useState(0);
    const [currentCashOut, setCurrentCashOut] = useState(0);
    useEffect(() => {
        dispatch(checkAmIBet(id))
    },[isGoing,ubets]);
    return (
        <div className="controls-panel-crash">
                <BetBlock currentBet={currentBet} setCurrentBet={setCurrentBet}/>
                <CashOutBlock currentBet={currentCashOut} setCurrentBet={setCurrentCashOut}/>
            <form method="POST" onSubmit={e => {
                e.preventDefault();
                if (isAuth && role_id !== 4&& !isICanCashOut) {
                    takeBet(currentBet, currentCashOut, dispatch, balance);
                    setCurrentCashOut(0);
                    setCurrentBet(0);
                }
            }} id="bet_form">
                {isAuth && role_id !== 4 ?
                    buttonViewHandler(isGoing,isIBet,isICanCashOut)
                    :
                    <a className="button button--gradient bank__button" data-izimodal-open="#auth"
                       onClick={() => dispatch(openModal("Auth"))}
                       data-izimodal-transitionin="fadeInDown">
                        Играть
                    </a>
                }
            </form>
        </div>
    )
};
const ControlPanelMobile = () => {
    const {isAuth, balance, id = 0,role_id} = useSelector(state => state.User);
    const {
        isGoing = false,
        isIBet = false,
        isICanCashOut = false,
    } = useSelector(state => state.Crash);

    const dispatch = useDispatch();
    const [currentBet, setCurrentBet] = useState(0);
    const [currentCashOut, setCurrentCashOut] = useState(0);

    return (
        <div className="controls-panel-crash mobile-show">
            <form method="POST" id="bet_form" onSubmit={e => {
                e.preventDefault();
                if (isAuth && role_id !== 4&& !isICanCashOut) {
                    takeBet(currentBet, currentCashOut, dispatch, balance);
                    setCurrentCashOut(0);
                    setCurrentBet(0);
                }
            }}>
                <BetBlock setCurrentBet={setCurrentBet} currentBet={currentBet}/>
                <CashOutBlock currentBet={currentCashOut} setCurrentBet={setCurrentCashOut}/>
                {isAuth && role_id !== 4 ?
                    buttonViewHandler(isGoing,isIBet,isICanCashOut)
                    :
                    <a className="button button--gradient bank__button" data-izimodal-open="#auth"
                       onClick={() => dispatch(openModal("Auth"))}
                       data-izimodal-transitionin="fadeInDown">
                        Играть
                    </a>
                }
            </form>
        </div>
    )
};

const BetBlock = ({currentBet = 0, setCurrentBet}) => {
    const balance = useSelector(state => state.User.balance);
    // let [currentBet, setCurrentBet] = useState(0);
    return (
        <div className="block-bet">
            <div className="crash-bet-title">Введите Вашу ставку:</div>

            <div className="block-bet-val noHighlight">
                <div className="clear-bet-name clear-bet noHighlight" onClick={() => setCurrentBet(0)}>Сброс</div>
                <div className="clear-bet-val {{ $btn_bets }}">
                    <ul>
                        <li data-action="+10"
                            onClick={() => {
                                betSetHandler(10, currentBet, setCurrentBet, balance);
                            }}
                        >+10
                        </li>
                        <li data-action="+50"
                            onClick={() => {
                                betSetHandler(50, currentBet, setCurrentBet, balance);
                            }}
                        >+50
                        </li>
                        <li data-action="+100"
                            onClick={() => {
                                betSetHandler(100, currentBet, setCurrentBet, balance);
                            }}
                        >+100
                        </li>
                        <li data-action="+500"
                            onClick={() => {
                                betSetHandler(500, currentBet, setCurrentBet, balance);
                            }}
                        >+500
                        </li>
                        <li data-action="+1000"
                            onClick={() => {
                                betSetHandler(1000, currentBet, setCurrentBet, balance);
                            }}
                        >+1000
                        </li>
                        <li data-action="half"
                            onClick={() => {
                                betSetHandler(Math.floor(currentBet / 2), currentBet, setCurrentBet, balance);
                            }}
                        >1/2
                        </li>
                        <li data-action="X2"
                            onClick={() => {
                                betSetHandler(currentBet, currentBet, setCurrentBet, balance);
                            }}
                        >2X
                        </li>
                        <li data-action="%10"
                            onClick={() => {
                                betSetHandler(Math.floor((currentBet * 10) / 100), currentBet, setCurrentBet, balance);
                            }}
                        >10%
                        </li>
                        <li data-action="%25"
                            onClick={() => {
                                betSetHandler(Math.floor((currentBet * 25) / 100), currentBet, setCurrentBet, balance);
                            }}
                        >25%
                        </li>
                        <li data-action="%50"
                            onClick={() => {
                                betSetHandler(Math.floor((currentBet * 50) / 100), currentBet, setCurrentBet, balance);
                            }}
                        >50%
                        </li>
                        <li data-action="max"
                            onClick={() => {
                                betSetHandler(balance, currentBet, setCurrentBet, balance);
                            }}
                        >MAX
                        </li>
                    </ul>
                </div>
            </div>
            {/*{!!$bl !!}*/}
            <input type="number"
                   name="bet"
                   id="bet"
                   value={currentBet}
                   autoComplete="off"
                   placeholder="Ваша ставка"
                   step="any"
                   min="0"
                   onChange={(e) => {
                       let value = e.target.value;
                       if (value < balance) {
                           setCurrentBet(value);
                       } else {
                           setCurrentBet(balance);
                       }
                   }}
            />
        </div>
    )
};

const CashOutBlock = ({currentBet = 0, setCurrentBet}) => {
    // const [currentBet, setCurrentBet] = useState(1);

    return (
        <div className="block-bet top">
            <div className="crash-bet-title">Автовывод:</div>

            <div className="block-bet-val noHighlight">
                <div className="clear-bet-name" id="clear_cashout" onClick={() => {
                    setCurrentBet(1);
                }}>Сброс
                </div>
                <div className="clear-bet-val cashout noHighlight">
                    <ul>
                        <li data-action="1" onClick={() => {
                            setCurrentBet(1);
                        }}>MIN
                        </li>
                        <li data-action="+.1" onClick={() => {
                            setCurrentBet(currentBet + 0.1)
                        }}>+0.1
                        </li>
                        <li data-action="+.5" onClick={() => {
                            setCurrentBet(currentBet + 0.5)
                        }}>+0.5
                        </li>
                        <li data-action="+1.00" onClick={() => {
                            setCurrentBet(currentBet + 1)
                        }}>+1
                        </li>
                        <li data-action="-.1" onClick={() => {
                            if (currentBet - 0.1 >= 1) {
                                setCurrentBet(currentBet - 0.1)
                            } else {
                                setCurrentBet(1);
                            }

                        }}>-0.1
                        </li>
                        <li data-action="-.5" onClick={() => {
                            if (currentBet - 0.5 >= 1) {
                                setCurrentBet(currentBet - 0.5)
                            } else {
                                setCurrentBet(1);
                            }
                        }}>-0.5
                        </li>
                        <li data-action="-1.00" onClick={() => {
                            if (currentBet - 1 >= 1) {
                                setCurrentBet(currentBet - 1)
                            } else {
                                setCurrentBet(1);
                            }
                        }}>-1
                        </li>
                        <li data-action="X2" onClick={() => {
                            setCurrentBet(currentBet * 2)
                        }}>2X
                        </li>
                        <li data-action="X5" onClick={() => {
                            setCurrentBet(currentBet * 5)
                        }}>5X
                        </li>
                        <li data-action="X10" onClick={() => {
                            setCurrentBet(currentBet * 10)
                        }}>10X
                        </li>
                    </ul>
                </div>
            </div>

            <input type="number"
                   className="inputWithoutArrow"
                   name="cashout"
                   id="cashout"
                   placeholder="Автовывод"
                   value={currentBet.toFixed(2)}
                   onChange={event => {
                       const nextValue = Number(event.target.value);
                       if (nextValue >= 0) {
                           setCurrentBet(nextValue)
                       } else {
                           setCurrentBet(1);
                       }
                   }}
                   step="0.01"
            />
        </div>
    )
};


export {
    ControlPanel,
    ControlPanelMobile
}