import React from "react";
import {JackpotLucky} from "./JackpotLucky";
import {BiggestWinner} from "./BiggestWinner";

const BannerJackpot = () => {
    return (
        <div className="jackpot-history__wrapper nano" style={{marginTop:"20px",height: "300px"}}>
            <div className="nano-content noScroll">

                <ul className="jackpot-history__list">
                    <JackpotLucky/>
                    <BiggestWinner/>
                </ul>
            </div>
        </div>
    )
};

export {
    BannerJackpot
}