import React, {useEffect, useState} from "react";
import {useDispatch, useSelector} from "react-redux";
import {openModal} from "../../../Actions/ModalAction";
import {ClassicGameBetButton, SenyorGameBetButton} from "./GameBetButton";
import {updateClassicGameTimer, updateSenoyrGameTimer} from "../../../Actions/Game/KingOfTheHillAction";


const GameTimer = () => {
    const dispatch = useDispatch();
    const timer = useSelector(state => state.KingOfTheHill.classic.timer);
    const status_id = useSelector(state => state.KingOfTheHill.gameClassic.status_id);
    const participants = useSelector(state => state.KingOfTheHill.gameClassic.participants);
    const isActive = participants.length ?
        participants.filter(item => item.account_id !== participants[0].account_id).length ?
            1
            :
            0
        :
        0;
    useEffect(() => {
        if(!timer || !isActive) return;

        const timerSync = setTimeout(()=>{
            dispatch(updateClassicGameTimer(timer - 1))
        },1000);
        return () => clearTimeout(timerSync);
    },[timer]);
    return (
        <div className="game__time">
            <p className="game__time-text classic-timer
                                    @if(!$gameClassic || !$gameClassic->end_game_at || $gameClassic->end_game_at < now() || $gameClassic->participants->groupBy('account_id')->count() < 2) @endif">
                Осталось:
                <span className="game__time-value">00:<span className="game__time-counter game__time--classic">
                    {
                        status_id >= 1 ? timer < 10 ? `0${timer}` : timer : "20"
                    }
                    {/*{{($gameClassic->status_id > 1) ? strtotime($gameClassic->end_game_at) - strtotime($timer) : '20'}}*/}
                </span></span>
            </p>
            <ClassicGameBetButton/>
        </div>
    )
};


const SenyorGameTimer = () => {
    const dispatch = useDispatch();
    const timer = useSelector(state => state.KingOfTheHill.senoyr.timer);
    const status_id = useSelector(state => state.KingOfTheHill.gameSenyor.status_id);
    const participants = useSelector(state => state.KingOfTheHill.gameSenyor.participants);
    const isActive = participants.length ?
        participants.filter(item => item.account_id !== participants[0].account_id).length ?
            1
            :
            0
        :
        0;
    useEffect(() => {
        if(!timer || !isActive) return;

        const timerSync = setTimeout(()=>{
            dispatch(updateSenoyrGameTimer(timer - 1))
        },1000);
        return () => clearTimeout(timerSync);
    },[timer]);


    return (
        <div className="game__time">
            <p className="game__time-text senyor-timer
                                    @if(!$gameClassic || !$gameClassic->end_game_at || $gameClassic->end_game_at < now() || $gameClassic->participants->groupBy('account_id')->count() < 2) @endif">
                Осталось:
                <span className="game__time-value">00:<span
                    className="game__time-counter game__time--senoyr">
                    {status_id === 1 ? timer < 10 ? `0${timer}` : timer : "20"}
                    {/*{{($gameClassic->status_id > 1) ? strtotime($gameClassic->end_game_at) - strtotime($timer) : '20'}}*/}
                </span></span>
            </p>
            <SenyorGameBetButton/>
        </div>
    )
};


export {
    GameTimer,
    SenyorGameTimer
}